.pickem-web-container{
    background: $app_bg_color;
    padding: 15px;
    padding-top: 56px;
    min-height: calc(100vh - 150px);
    .is-card{
        .dfs-card{
            background: #3C9FE5;
            height: 166px;
            .dfs-c {
                p{
                    color: $white;
                    font-size: $font-size-medium;
                    .button{
                        background: $primary_text_color;
                        color: $white;
                        font-size: $font-base;
                        border-radius: 6px;
                        width: 120px;
                        height: 30px;
                        display: block;
                        padding: 6px;
                        text-align: center;
                        margin-top: 15px;
                        text-transform: uppercase;
                    }
                }
                .img-dfs{
                    max-width: 130px;
                    position: relative;
                    bottom: -8px;
                }
            }
            @media (max-width: 550px){
                .img-dfs-shape{
                    width: 50px;
                }
                .dfs-c{
                    height: 100%;
                    display: block;
                    padding: 34px 10px;
                    p{
                        top: auto;
                        max-width: 64%;
                        line-height: 20px;
                        text-transform: none;
                    }
                    .img-dfs{
                        position: absolute;
                        right: -5px;
                        bottom: 0px;
                        max-width: 130px;
                        width: 40%;
                    }
                }
            }
            @media (max-width: 413px){
                .dfs-c p{
                    max-width: 72%;
                }
            }
            @media (max-width: 320px){
                height: 146px;
                .img-dfs-shape{
                    width: 40px;
                }
                .dfs-c{
                    padding: 30px 10px;
                    p{
                        line-height: 18px;
                        font-size: $font-base;
                        max-width: 76%;
                        .button{
                            font-size: $font-size-small;
                            height: 26px;
                            padding: 5px;
                            margin-top: 12px;
                        }
                    }
                    .img-dfs{
                        position: absolute;
                        right: -5px;
                        bottom: 0px;
                        max-width: 130px;
                        width: 38%;
                    }
                }
            }
        }
    }
    .pickem-prediction-outer-card{
        perspective: 100px;
        list-style: none;
        animation: flip-anim 0.4s linear;
        animation-fill-mode: both;
        transform: rotateY(-90deg);
        transition: all 0.2s cubic-bezier(.36, -0.64, .34, 1.76);
        &.is-card{
            padding: 0;
            margin-top: 20px;
        }
    }
    // Set delay per List Item
    @for $i from 1 through 20 {
        .pickem-prediction-outer-card:nth-child(#{$i}) {
            animation-delay: .15s * $i !important;
        }
    }
}
.my-contest-pre-card{
    perspective: 100px;
    list-style: none;
    animation: flip-anim 0.4s linear;
    animation-fill-mode: both;
    transform: rotateY(-90deg);
    transition: all 0.2s cubic-bezier(.36, -0.64, .34, 1.76);
}
@for $i from 1 through 20 {
    .my-contest-pre-card:nth-child(#{$i}) {
        animation-delay: .15s * $i !important;
    }
}
.pickem-prediction-outer-card{
    position: relative;
    padding-top: 17px;
    margin-bottom: 15px;
    &.my-contest-pre-card{
        margin-bottom: 45px;
    }
    .timer-section{
        margin: 0 auto;
        max-width: 132px;
        font-size: $font-size-xsmall;
        font-family: $font-bold;
        text-align: center;
        padding: 5px 4px 3px;
        line-height: 1;
        background: $disabled_text_color;
        color: $help_text_color;
        border-radius: 22px;
        height: 34px;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        span{
            display: inline-block;
            margin: 0 3px;
        }
        &.cric-score{
            max-width: 210px;
        }
    }
    .countdown-timer-section{
        background: $alert_danger_color;
        color: $white;
        margin: 0 auto;
        max-width: 92px;
        font-size: $font-size-xsmall;
        font-family: $font-bold;
        text-align: center;
        padding: 5px 4px 3px;
        line-height: 1;
        border-radius: 22px;
        height: 34px;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
    }
}
.pickem-prediction-card{
    border-radius: 14px;
    background: $white;
    overflow: hidden;
    position: relative;
    z-index: 2;
    &.pick-pred-two{
        .option-section{
            .option-info{
                width: 50%;
            }
        }
    }
    .option-section{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        .option-info{
            width: 33.33%;
            padding: 20px 15px 12px;
            border-bottom: 1px solid $app_bg_color;
            text-align: center;
            position: relative;
            .icon-share{
                cursor: pointer;
                position: absolute;
                left: 5px;
                top: 8px;
                z-index: 2;
            }
            .circular-progress{
                @include circular-progress-style(2px, $border_color, $primary, 26px);
            }
            .option-info-inner{
                position: relative;
                z-index: 1;
            }
            &:first-child{
                border-radius: 14px 0 0 0;
            }
            &:last-child{
                border-radius: 0 14px 0 0;
            }
            .correct-text{
                display: none;
            }
            .shimmer-v{
                position: absolute;
                left: 0;
                top: -3px;
                height: 95%;
                width: 100%;
                display: none;
                span{
                    height: 100%;
                    margin: 0;
                }
            }
            &:not(:first-child){
                border-left: 1px solid $app_bg_color !important;
            }

            &:after {
                content: '';
                position: absolute;
                left: 0px;
                top: 0px;
                width: 110%;
                height: 110%;
                background-color: $app_bg_color;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -o-border-radius: 50%;
                -ms-border-radius: 50%;
                z-index: 1;
                opacity: 0;
                transform: scale(1, 1);
                -webkit-transform: scale(1, 1);
                -moz-transform: scale(1, 1);
                -o-transform: scale(1, 1);
                -ms-transform: scale(1, 1);
                transition: transform 1s, opacity 1s;
                -webkit-transition: transform 1s, opacity 1s;
                -moz-transition: transform 1s, opacity 1s;
                -o-transition: transform 1s, opacity 1s;
                -ms-transition: transform 1s, opacity 1s;
            }
            
            &:active:after {
                opacity: 0.7;
                transform: scale(0, 0);
                -webkit-transform: scale(0, 0);
                -moz-transform: scale(0, 0);
                -o-transform: scale(0, 0);
                -ms-transform: scale(0, 0);
                transition: 0s;
                -webkit-transition: 0s;
                -moz-transition: 0s;
                -o-transition: 0s;
                -ms-transition: 0s;
            }
            .icon-tick-circular,
            .icon-cross-circular{
                display: none;
            }
            &.selected-option{
                background-color: #E7F4F8;
                border: 1px solid $white;
                border-bottom: 1px solid $primary;
                position: relative;
                &:before{
                    content: '';
                    position: absolute;
                    background: $primary;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 1px;
                }
                .shimmer-v{
                    display: block;
                    span{
                        background-color: #E7F4F8;
                        background-image: linear-gradient(90deg, #E7F4F8, rgba(255, 255, 255, 0.7), #E7F4F8);
                    }
                }
            }
            &.correct-selected{
                background-color: #E4FDE8;
                border: 1px solid $white;
                border-bottom: 1px solid $alert_success_color;
                position: relative;
                .icon-tick-circular{
                    display: block;
                    color: $alert_success_color;
                    position: absolute;
                    left: 9px;
                    top: 9px;
                    font-size: $font-size-medium;
                }
                &:before{
                    content: '';
                    position: absolute;
                    background: $alert_success_color;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 1px;
                }
                .shimmer-v{
                    display: block;
                    span{
                        background-color: #E4FDE8;
                        background-image: linear-gradient(90deg, #E4FDE8, rgba(255, 255, 255, 0.7), #E4FDE8);
                    }
                }
            }
            &.wrong-selected{
                background-color: #FDE4E4;
                border: 1px solid $white;
                border-bottom: 1px solid $alert_danger_color;
                position: relative;
                .icon-cross-circular{
                    display: block;
                    color: $alert_danger_color;
                    position: absolute;
                    left: 9px;
                    top: 9px;
                    font-size: $font-size-medium;
                }
                &:before{
                    content: '';
                    position: absolute;
                    background: $alert_danger_color;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 1px;
                }
                .shimmer-v{
                    display: block;
                    span{
                        background-color: #FDE4E4;
                        background-image: linear-gradient(90deg, #FDE4E4, rgba(255, 255, 255, 0.7), #FDE4E4);
                    }
                }
            }
            &.correct-option{
                border: 1px solid $white;
                border-bottom: 1px solid $alert_success_color;
                position: relative;
                .correct-text{
                    display: block;
                    position: absolute;
                    top: 2px;
                    left: 50%;
                    transform: translateX(-50%);
                    font-size: $font-size-xsmall;
                    font-family: $font-book;
                    color: $help_text_color;
                    text-transform: capitalize;
                }
                &:before{
                    content: '';
                    position: absolute;
                    background: $alert_success_color;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 1px;
                }
                .shimmer-v{
                    display: none;
                }
            }
  
        }
        .option-img{
            width: 38px;
            height: 38px;
            overflow: hidden;
            margin: 0 auto;
            img{
                max-width: 100%;
                max-height: 100%;
            }
        }
        .option-name{
            font-size: $font-size-medium;
            font-family: $font-bold;
            color: $primary_text_color;
            line-height: 1;
            margin-top: 8px;
            text-transform: uppercase;
        }
    }
    .pickem-pred-info{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 6px 18px;
        .league-name,.first-to-predict{
            font-size: $font-size-small;
            color: $help_text_color;
            font-family: $font-regular;
            .coin-img{
                max-width: 13px;
            }
        }
    }
    .status-section{
        padding: 6px 18px;
        font-size: $font-size-small;
        color: $help_text_color;
        font-family: $font-regular;
    }
    .pickem-pred-coin-info{
        background: $white;
        display: table;
        padding: 9px 15px 8px;
        width: 100%;
        .display-table-cell{
            width: 25%;
            color: $help_text_color;
            padding: 0 5px;
            .icon-info{
                position: relative;
                top: -1px;
                margin-left: 3px;
            }
            &:nth-child(2){
                width: 45%;
            }
            &:last-child{
                width: 30%;
            }
            @media (max-width: 360px){
                &:nth-child(2){
                    width: 38%;
                }
                &:last-child{
                    width: 37%;
                }
            }
            p{
                margin: 0;
                font-size: $font-size-xsmall;
                font-family: $font-book;
            }
            .amt{
                font-size: $font-size-small;
                font-family: $font-bold;
            }
            img{
                width: 13px;
                height: 13px;
            }
        }
        &.pickem-pred-completed-info{
            .display-table-cell{
                &:last-child{
                    width: 20%;
                }
            }
        }
    }
}
.pickem-pred-footer{
    width: calc(100% - 30px);
    margin: 0 auto;
    text-align: center;
    height: 79px;
    background: rgba(255, 255, 255, 0.49);
    border-radius: 0 0 39px 39px;
    color: $help_text_color;
    font-size: $font-size-small - 1;
    font-family: $font-regular; 
    position: absolute;
    left: 0;
    right: 0;
    bottom: -30px;
    padding: 58px 30px 0;
    display: flex;
    justify-content: space-between;
    &.center{
        justify-content: space-around;
    }
    a{
        color: $primary;
        font-family: $font-book;
        font-size: $font-size-small - 1;
    }
}
.modal-pred-confirm .container .your-pre{
    .option{
        span{
            font-size: $font-base;
            font-family: $font-bold;
            color: $primary;
            max-width: 130px;
            margin: 4px auto;
            @media (max-width: 360px){
                font-size: $font-base - 1;
            }
        }
    }
}
.your-pre{
    .option-img{
        max-width: 37px;
        max-height: 37px;
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
        @media (max-width: 360px){
            left: 15px;
        }
    }
}
.pickem-htp-modal{
    .modal-body{
        padding: 1rem 0;
    }
    .how-to_play_label{
        right: 0;
        animation: slide-t-r 1.5s ease;
        position: relative;
        padding: 0 35px;
        @keyframes slide-t-r {
            0% {
                right: -100%;
                opacity: 0;
            }

            100% {
                right: 0;
                opacity: 1;
            }
        }
    }
    .h2-htp-text{
        font-size: 38px;
        font-family: $font-bold;
        color: $app_bg_color;
        text-transform: uppercase;
        position: relative;
        padding: 0 35px;
        @media (max-width: 320px){
            font-size: 34px;
        }
        left: 0;
        animation: slide-t-l 1.5s ease;

        @keyframes slide-t-l {
            0% {
                left: -100%;
                opacity: 0;
            }

            100% {
                left: 0;
                opacity: 1;
            }
        }
    }
    .htp-step-section{
        padding: 35px;
        max-height: calc(100vh - 260px);
        overflow-x: hidden;
        overflow-y: auto;
    }
    .parallelogram-outer-white{
        background: linear-gradient(90deg, $white 15%, $help_text_color 150%);
        @media (max-width: 500px){
            background: linear-gradient(90deg, $white 5%, $help_text_color 150%);
        }
    }
    .parallelogram-outer-r-white{
        background: linear-gradient(-90deg, rgba(255, 255, 255, 0) 80%, $help_text_color 150%);
        .create-your-team-and{
            text-align: right;
        }
    }
    .plan-bg{
        &:nth-child(2){
            position: relative;
            left: -2px;
        }
        &.shift-left{
            position: relative;
            left: -4px;
        }
    }
    .plan-bg-r{
        width: 100%;
        transform: skew(-10deg);
        &:first-child{
            position: relative;
            right: -8px;
        }
    }
    &.hall-of_fame-modal.modal-lg.modal-dialog .modal-content .modal-body{
        overflow-x: hidden;
    }
    .ic1,.ic3{
        height: 85px;
        animation: fadeing 1.5s ease;

        @keyframes fadeing {
            0% {
                opacity: 0.5;
                transform: scale(0.9) skew(0deg);
            }

            100% {
                opacity: 1;
                transform: scale(1) skew(15deg);
            }
        }
    }
    .ic2{
        height: 85px;
        transform: scale(1) skew(-8deg);
        animation: fadeing-ic2 1.5s ease;

        @keyframes fadeing-ic2 {
            0% {
                opacity: 0.5;
                transform: scale(0.9) skew(0deg);
            }

            100% {
                opacity: 1;
                transform: scale(1) skew(-8deg);
            }
        }
    }
    .text-blue{
        position: relative;
        &.right-s {
            right: 0;
            animation: slide-t-r 1.5s ease;
    
            @keyframes slide-t-r {
                0% {
                    right: -100%;
                    opacity: 0;
                }
    
                100% {
                    right: 0;
                    opacity: 1;
                }
            }
        }
    
        &.left-s {
            left: 0;
            animation: slide-t-l 1.5s ease;
    
            @keyframes slide-t-l {
                0% {
                    left: -100%;
                    opacity: 0;
                }
    
                100% {
                    left: 0;
                    opacity: 1;
                }
            }
        }

    }
    .create-your-team-and{
        position: relative;
        top: 0;
        animation: slide-t 1.5s ease;

        @keyframes slide-t {
            0% {
                top: 20px;
                opacity: 0;
            }

            100% {
                top: 0;
                opacity: 1;
            }
        }
    }
    @media (max-width: 414px){
        .button-footer-modal{
            bottom: 40px;
        }
    }
}

.pickem-lobby-shimmer{
    &.contest-list .shimmer-container{
        padding: 0;
    }
    .shimmer-top-view{
        display: table;
        width: 100%;
        .shimmer-top-inner-view{
            display: table-cell;
            width: 50%;
            position: relative;
            padding: 12px 14px;
            border-bottom: 1px solid $border-color;
            &:first-child{
                border-right: 1px solid $border-color;
            }
        }
        .shimmer-line{
            text-align: center;
        }
    }
    .shimmer-bottom-view{
        padding: 8px 5px 2px;
    }
    .progress-bar-default{
        width: 30%;
    }
}
.pickem-share-wrap{
    padding-top: 56px;
    .icon-share{
        position: absolute;
        top: 10px;
        right: 10px;
    }
    .pickem-info-header{
        padding: 33px;
        .pickem-info-top{
            margin-bottom: 10px;
            .name{
                font-size: $font-size-h4;
                font-family: $font-bold;
                color: $primary_text_color;
                text-transform: capitalize;
            }
            a{
                float: right;
                color: $primary;
                font-size: $font-size-small;
                font-family: $font-book;
                position: relative;
                top: 6px;
                i{
                    margin-right: 7px;
                    position: relative;
                    top: -1px;
                }
            }
        }
        .pickem-info-bottom{
            .pickem-timer{
                font-size: $font-base;
                font-family: $font-bold;
                .countdown-timer-section{
                    display: inline-block;
                    vertical-align: middle;
                    color: $alert_danger_color;
                }
                .timer-section{
                    display: inline-block;
                    vertical-align: middle;
                }
            }
            .league-name{
                font-size: $font-size-small;
                font-family: $font-regular;
                color: $help_text_color;
                float: right;
            }
        }
    }
    .pickem-prize-section{
        background: $app_bg_color;
        padding: 21px 20px 19px;
        font-size: $font-size-h4;
        color: $primary_text_color;
        text-align: center;
        &.be-first{
            font-size: $font-size-medium;
        }
        span{
            font-family: $font-bold;
            img{
                max-width: 17px;
                max-height: 17px;
                margin-left: 10px;
                position: relative;
                top: -1px;
            }
        }
    }
    .pickem-que{
        margin: 45px auto 45px;
        font-size: $font-base;
        font-family: $font-book;
        color: $body_text_color;
        max-width: 245px;
        text-align: center;
    }
    .option-info{
        list-style-type: none;
        margin: 0;
        padding: 0 35px 35px;
        li{
            border: 1px solid $border-color;
            padding: 19px 22px 19px 76px;
            position: relative;
            border-radius: 8px;
            margin-bottom: 20px;
            &:after {
                content: '';
                position: absolute;
                left: 0px;
                top: 0px;
                width: 100%;
                height: 100%;
                background-color: $app_bg_color;
                border-radius: 30%;
                -webkit-border-radius: 30%;
                -moz-border-radius: 30%;
                -o-border-radius: 30%;
                -ms-border-radius: 30%;
                z-index: 1;
                opacity: 0;
                transform: scale(1, 1);
                -webkit-transform: scale(1, 1);
                -moz-transform: scale(1, 1);
                -o-transform: scale(1, 1);
                -ms-transform: scale(1, 1);
                transition: transform 1s, opacity 1s;
                -webkit-transition: transform 1s, opacity 1s;
                -moz-transition: transform 1s, opacity 1s;
                -o-transition: transform 1s, opacity 1s;
                -ms-transition: transform 1s, opacity 1s;
            }            
            &:active:after {
                opacity: 0.7;
                transform: scale(0, 0);
                -webkit-transform: scale(0, 0);
                -moz-transform: scale(0, 0);
                -o-transform: scale(0, 0);
                -ms-transform: scale(0, 0);
                transition: 0s;
                -webkit-transition: 0s;
                -moz-transition: 0s;
                -o-transition: 0s;
                -ms-transition: 0s;
            }
        }
        img{
            max-width: 38px;
            max-height: 38px;
            position: absolute;
            left: 18px;
            top: 50%;
            transform: translateY(-50%);
        }
        .opt-name{
            font-size: $font-size-h4;
            font-family: $font-bold;
            color: $primary_text_color;
            text-transform: uppercase;
        }
    }
}
.participants-list{
    background: $white;
    padding-top: 56px;
    .header-table{
        display: table;
        width: 100%;
        position: fixed;
        top: 56px;
        width: 100vw;
        max-width: 550px;
        .dtr{
            display: table-row;
            width: 100%;
            &.dth{
                .dtc{
                    border-bottom: 1px solid $white;
                    display: table-cell;
                    vertical-align: middle;
                    background: $app_bg_color;
                    padding: 4px 6px;
                    color: $help_text_color;
                    font-size: $font-size-small;
                    font-family: $font-book;
                    text-transform: capitalize;
                    vertical-align: middle;
                    font-weight: normal;
                    line-height: 1.1;
                    &.part-name{
                        width: 43%;
                        @media (max-width: 550px){
                            width: 39%;
                        }
                        padding-left: 20px;
                    }
                    &.part-pick,&.part-bet{
                        width: 18%;
                    }
                    &.part-est-win{
                        width: 21%;
                        @media (max-width: 550px){
                            width: 25%;
                        }
                        padding-right: 20px;
                    }
                }
            }
        }
    }
    .all-part-table{
        &.all-part-table-cp{
            margin-top: 22px;
        }
        .display-table{
            display: table;
            width: 100%;
        }
        margin-top: 34px;
        .dtr.dtb{
            display: table-row;
            width: 100%;
            &.own-data{
                background-color: #EDF6FA;
            }
            .dtc{
                border-bottom: 1px solid $border-color;
                display: table-cell;
                vertical-align: middle;
                padding: 14px 5px 12px;
                color: $primary_text_color;
                font-size: $font-size-small;
                font-family: $font-regular;
                &.part-name{
                    padding-left: 20px;
                    font-size: $font-base;
                    width: 43%;
                    span{
                        vertical-align: middle;
                        display: inline-block;
                        &.nm{
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            max-width: 160px;
                        }
                    }
                    @media (max-width: 550px){
                        width: 39%;
                        span.nm{
                            max-width: 80px;
                        }
                    }
                    @media (max-width: 320px){
                        span.nm{
                            max-width: 50px;
                        }
                    }
                }
                &.part-pick,&.part-bet{
                    width: 18%;
                }
                &.part-bet,
                &.part-est-win{
                    font-family: $font-bold;
                    img{
                        max-width: 17px;
                        max-height: 17px;
                        margin-right: 5px;
                    }
                }
                &.part-est-win{
                    padding-right: 20px;
                    width: 21%;
                    @media (max-width: 550px){
                        width: 25%;
                    }
                }
            }
        }
    }
}
.pick-highlight{
    font-size: $font-size-small;
    font-family: $font-book;
    color: $help_text_color;
    background: $disabled_text_color;
    padding: 8px 15px;
    text-align: center;
    margin-top: 15px;
}
.my-contest-style{
    .mycontest-shimmer-wrap{
        padding: 15px;
    }
}
.pickem-prediction-card .option-section .option-info .circular-progress:after{
    border-width: 1px;
}
.pickem-participants-wrap{
    .app-header-style{
        .home-team-flag, .away-team-flag{
            @media (max-width: 400px){
                max-width: 30px;
                max-height: 30px;
            }
            @media (max-width: 320px){    
                max-width: 26px;
                max-height: 26px;
            }
        }
        .row-container .section-middle{
            @media (max-width: 400px){
               padding: 0 3px;
               .team-home, 
               .team-away,
               .vs-text{
                   font-size: $font-base - 1;
               }
            }
            .team-home, 
            .team-away,
            .vs-text{
                @media (max-width: 360px){
                    font-size: $font-size-small;
                }
                @media (max-width: 320px){
                    font-size: $font-size-xsmall + 1;
                }
            }
        }
    }
}
.prediction-detail-wrap{
    .circular-progress{
        @include circular-progress-style(2px, $border_color, $primary, 26px);
        top: 50%;
        transform: translateY(-50%);
        right: 25px;
        &:after{
            border-width: 1px;
        }
    }
}
.open-predict-leaderboard.prediction-part-v.is-leaderboard.fpp-leaderboard.pickem-leaderboard{
    .header-v,.list-item{
        span{
            &.usernm{
                width: 68% !important;
            }
            &.u-rank{
                width: 10% !important;
            }
            &.amount{
                width: 22% !important;
            }
        }
    }
    .top-three-users{
        .rank-section{
            .circle-wrap{
                .win{
                    span{
                        font-size: $font-size-medium;
                        position: static;
                        img{
                            position: relative;
                            top: -1px;
                        }
                    }
                    .coin-txt{
                        display: block;
                        color: $help_text_color;
                        font-size: $font-size-xsmall;
                        text-align: center;
                        width: 100%;
                        top: 0;
                        line-height: 1;
                        font-family: $font-book;
                    }
                }
            }
            &.first-rank{
                .circle-wrap{
                    .win{
                        span{
                            font-size: $font-size-h4;
                            &.coin-txt{
                                font-size: $font-size-xsmall;
                            }
                        }
                    }
                }
            }
        }
    }
}
.lobby-go {
    position: absolute;
    width: 100%;
    padding: 0 10%;
    left: 0;
    bottom: 80px;
    text-align: center;
    @media (max-height: 750px){
        position: relative;
        bottom: auto;
        margin-bottom: 80px;
    }
    @media (max-width: 767px) {   
        position: relative;     
        margin-bottom: 80px;
        text-align: center;
        bottom: auto;
        z-index: 3;
    }

    a {
        color: $primary;
        font-family: $font-bold;
    }
}
.hall-of_fame-modal{
    .button-footer-modal{
        position: static;
        margin-top: 80px;
        @media (max-width: 414px){
            margin-top: 60px;
        }
    }
}
.center-modal.opacityZero{
    opacity: 0;
}
.modal-pred-confirm{
    .pickem-share{
        position: absolute;
        right: 20px;
        top: 20px;
        cursor: pointer;
    }
}