.tabs-primary {
    .top-fixed{
        position: fixed;
        max-width: $window_width;
        z-index: 1;
        margin-top: -5px;
    }
    .top-tab-margin{
        margin-top: 39px;
    }
    .nav {
        border: 0;
        box-shadow: 0px 14px 16px 3px rgba(0,0,0,0.08);
        li {
            width: calc(100% / 3);
            a {
                width: 100%;
                background: $white;
                color: $help_text_color;
                text-align: center;
                text-transform: uppercase;
                font-size:  $font-size-small;
                font-family: $font-bold;
                display: inline-block;
                vertical-align: top;
                padding: 13px;
            }
            &.active {
                a {
                    color: $primary;
                    position: relative;
                    .live-indicator{
                        background-color: $primary;
                    }
                    &:after {
                        content: '';
                        position: absolute;
                        bottom: 0px;
                        height: 2px;
                        width: 100%;
                        background: $primary;
                        left: 0;
                    }
                }
            }
        }
    }
}
.live-indicator {
    height: 8px;
    width: 8px;
    background-color:$alert_danger_color;
    border-radius: 50%;
    display: inline-block;
    margin-right: 5px;
}
.contest-card {
    margin-bottom: 10px;
    margin-top: 10px;
    .contest-card-header {
        text-align: center;
        margin: 0 0px -5px 0px;
        padding-top: 12px;
        &.pb12{
            padding-bottom: 12px;
        }
        ul {
            display: inline-block;
            margin: 0;
            padding: 0;
            li {
                display: inline-block;
                vertical-align: middle;
                &:nth-child(2){
                    max-width: 80%;
                }
                .team-content-img {
                    width: 34px;
                    height: 34px;
                    @media screen and(max-width:435px){
                        width: 25px;
                        height: 25px;
                    }
                    overflow: hidden;
                    vertical-align: middle;
                    img {
                        max-width: 100%;
                    }
                }
                .team-content {
                    color: $primary_text_color;
                    font-family: $font-bold;
                    margin: 5px 10px 0 10px;
                    &.public-contest{
                        margin: 25px 10px 0 10px;
                    }
                    @media (max-width: 400px){
                        margin: 0 6px;
                    }
                    @media (max-width: 320px){
                        margin: 0 2px;
                    }
                    .share-contest-time-date{
                        color: $primary;
                        font-size: $font-size-h6 !important;
                        font-family: $font-bold;
                    }
                    .share-contest-countdown{
                        color: $alert_danger_color !important;
                        font-size: $font-size-h6 !important;
                        font-family: $font-bold;
                        line-height: 1.1;
                        display: block;
                        strong{
                            display: inline-block;
                            color: $alert_danger_color !important;
                            font-size: $font-size-h6 !important;
                        }
                        span{
                            display: inline-block;
                            font-size: $font-size-h6 !important;
                            color: $alert_danger_color !important;
                        }
                    }
                    span{
                        color: $primary_text_color;
                        font-family: $font-bold;
                        span{
                            color: $alert_danger_color;
                            font-family: $font-bold;
                            font-size: $font-size-h6;
                            text-align: center;
                        }
                    }
                    .time-line-date{
                        color: $primary;
                        font-size: $font-size-xsmall;
                        font-family: $font-bold;
                    }
                    .countdown{
                        span{
                            color: $alert_danger_color;
                            font-size: $font-size-xsmall;
                            font-family: $font-bold;
                            display: block;
                            line-height: 1.1;
                        }
                    }
                    small {
                        color: $primary_text_color;
                        font-size: $font-size-medium;
                        font-family: $font-bold;
                    }
                }
                p {
                    margin: 0;
                    font-size: $font-base;
                    font-family: $font-bold;
                    color: $primary_text_color;
                    line-height: 13px;
                    max-width: 100%;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    @media screen and (max-width: 140px) {
                        font-size: 8px;
                    }
                    @media (max-width: 414px){
                        max-width: 150px;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }
                }
            }
        }
    }
    .contest-card-body {
        background-color: $white;
        border-radius: 10px;
        border: 1px solid $border_color;
        &:first-child{
            margin-top: 0px;
        }
        .contest-card-body-header {
            padding: 10px 12px 5px 18px;
            position: relative;
            .contest-name-heading {
                color: $primary_text_color;
                font-size:  $font-size-small;
                font-family: $font-italic;
                margin-bottom: 0;
                margin-left: 0;
                position: relative;
                text-transform: capitalize;
                .icon-m,.icon-g,.icon-share{
                    margin-left: 10px;
                    display: inline-block;
                    position: absolute;
                    top: 0;
                    font-size: $font-size-medium;
                }
                .icon-m,.icon-g{
                    right: 20px;
                }
                .icon-m{
                    & ~ .icon-g{
                        margin-right: 20px;
                    }
                }
                .icon-share{        
                    right: -4px;
                    color: $icon_color;
                }
            }
            .contest-details {
                display: inline-block;
                img {
                    display: inline-block;
                    vertical-align: top;
                }
                .contest-details-action {
                    display: inline-block;
                    vertical-align: middle;
                    position: relative;
                    ul{
                        line-height: 1;
                        &.list-inner{
                            li.f-red{
                                font-size:  $font-size-xsmall;
                                font-family: $font-book;
                            }
                        }
                    }                    
                    h4 {
                        font-size: $font-size-medium;
                        color: $primary;
                        font-family: $font-bold;
                        line-height: 18px;
                        margin: 0;
                        display: inline-block;
                        i.icon-bonus{
                            position: relative;
                            top: -2px;
                            right: 0;
                            margin-left: 0px;
                        }
                    }
                    .text-small {
                        position: relative;
                        top: -4px;
                        display: block;
                        color: $help_text_color;
                        font-size: $font-size-xsmall;
                        font-family: $font-bold-italic;
                        line-height: 13px;
                    }
                    h4 .icon-ruppee{
                        font-size:  $font-size-small;
                        position: relative;
                        top: -1px;
                    }
                }
            }
        }
    }
}
