        ul{
            .progress-middle{
                width: 42%;
            }
            .team-left-side,.team-right-side{
                width: 28%;
            }
        }
    }
    &.contest-card{
        .contest-card-body{
            .contest-card-body-header{
                padding: 10px 12px 6px 14px;   
            }
        }
    }
    .contest-listing li{
        padding: 10px 12px 5px 14px;
    }    
    .contest-progress-block{
        .btn-primary{
            position: relative;
            top: -5px;
            img.img-coin{
                max-width: 12px;
                margin-right: 2px;
                margin-left: 2px;
                position: relative;
                top: -1px;
            }
        }
    }
    &.contest-card-with-collection{
        .contest-card-header{
            ul{
                .progress-middle{
                    width: 100%;
                }
                li{
                    p{
                        max-width: 100%;
                        font-size: $font-base;
                        font-family: $font-bold;
                        color: $primary_text_color;
                        text-transform: capitalize;
                    }
                }
            }
        }
    }
    .user-link.completed-user-link{
        font-size: $font-size-small;
        font-family: $font-regular;
        padding-top: 6px;
    }
    .padding-strip{
        padding: 0px 15px 0 15px;
    }
}
.no-record-found{
    @include color-opacity($black,0.8);
    font-family: $font-regular;
    font-size: $font-size-medium;
    text-align: center;
    padding-top: 100px;
    padding-bottom: 100px;
    vertical-align: middle;
}
.contest-details-right {
    text-align: center;
    float: right;
    color:$primary_text_color;
    margin: 4px 0;
    &.absolute{
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translate(0px, -50%);
    }
    a {
        display: block;
        color: $primary_text_color;
        i {
            font-size: $font-size-h5;
            &.f-sm{
                font-size: $font-size-medium;
            }
        }
        .fs8{
            font-size: 8px;
        }
    }
    span {
        font-family: $font-bold;
        color: $help_text_color;
        font-size: 9px;
        display: block;
    }
}
.list-inner {
    margin: 0;
    padding: 0;
    display: block;
    li {
        display: inline-block;
        margin-right: 10px;
        &:last-child {
            margin-right: 0;
        }
    }
}
.contest-listing {
    display: block;
    margin: 0;
    padding: 0;
    li {
        display: table;
        border-top: 1px solid $border-light;
        padding: 10px 12px 10px 18px;
        width: 100%;
        .cell-block {
            display: table-cell;
            vertical-align: middle;
            .multi-txt{
                font-size: 13px;
            }
            &.middle{
                vertical-align: middle;
            }
        }
    }
    &.upcoming {
        li {
            .cell-block {
                vertical-align: middle;
            }
        }
        .contest-details-right {
            a {
                display: inline-block;
                margin: 0 10px;
                @media (max-width: 320px){
                    margin: 0 6px;
                }
                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }
}
.primary-select {
    min-width: 90px;
    font-size: $font-size-small;
    .select__control {
        border: 0;
        box-shadow: none;
        min-height: auto;
    }
    .select__indicator-separator {
        display: none;
    }
    .select__dropdown-indicator {
        padding: 0 4px;
    }
    .select__value-container {
        padding: 0;
    }
}
.f-red {
    color: $help_text_color;
}
.user-link {
    color: $primary_text_color;
    font-size: $font-base;
    font-family: $font-bold;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;    
    cursor: default;
    line-height: 1.3;
    display: block;
    @media (max-width: 767px){
        max-width: 200px;
    }
    &.no-hover{
        &:hover{
            color: inherit;
        }
    }    
    &:hover{
        color: $primary;
    }
    &:hover{
        color: $primary;
    }
    &.completed-user-link{
        display: inline-block;
        @media (max-width: 767px){
            max-width: 120px;
        }
        @media (max-width: 400px){
            max-width: 100px;
        }
        @media (max-width: 320px){
            max-width: 72px;
        }
    }
}
.multi-txt {
    color: $help_text_color;
    font-family: $font-bold;
    margin: 0;
    display: inline-block;
    b {
        font-family: $font-bold;
        color: $primary_text_color;
    }
}
.time-line {
    color: $alert_danger_color;
    font-size: $font-size-h6;
    font-family: $font-bold;
    display: block;
    line-height: 1.5;
}
.contest-progress-block {
    .progress-bar-default {
        display: inline-block;
        width: 68%;
        .progress{
            width: 100%;
        }
        .total-output {
            color: $primary_text_color;
            font-size: $font-size-small;
            font-family: $font-book;
        }
        .min-entries {
            right: 0%;
            left: auto;
        }
        &.full-width-progress-bar{
            width: 98%;
        }
    }
    .btn-primary {
        font-size: $font-size-small;
        padding: 7px 15px 6px;
        border-radius: 25px;
        vertical-align: top;
        font-family: $font-bold;
        &.width100{
            width: 100px;
        }
    }
    .btn-default {
        font-size:  $font-size-small;
        padding: 6px 9px;
        border-radius: 25px;
        vertical-align: top;
        margin-left: 11px;
        font-family: $font-bold;
        background-color: $gray-170;
        color: $gray-200;
    }
}
.share-contest {
    position: absolute;
    top: 10px;
    right: 12px;
    color: $help_text_color;
}
