.collection-status{
    position: absolute;
    border-radius: 0 0 7px 7px;
    padding: 2px 8px;
    text-transform: uppercase;
    top:-1px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 8px;
    font-family: $font-bold;
    > span{
        color: $white !important;
        position: relative;
        div{
            position: relative;
            z-index: 2;
            display: inline-block;
        }
        &:after{
            content: '';
            height: 18px;
            width: 16px;
            position: absolute;
            -webkit-transform: rotate(150deg);
            transform: rotate(-160deg);
            top: -8px;
            right: -10.1px;
        }
        &:before{
            content: '';
            height: 30px;
            width: 24px;
            position: absolute;
            -webkit-transform: rotate(152deg);
            transform: rotate(152deg);
            top: -8px;
            left: -11px;
            height: 18px;
            width: 16px;
        }
    }
    .circle-divider{
        display: inline-block !important;
        background: $white;
        margin: 0 4px 1px 0;
        position: relative;
        top: -1px;
    }
    &.live-status{
        background: $danger;
        span{
            &:after,&:before{
                background: $danger;
            }            
        }
    }
    &.completed-status{
        background: $success;
        span{
            &:after,&:before{
                background: $success;
            }
        }
    }
}
