
.transaction-list{
    list-style-type: none;
    margin: 0;
    padding: 0;
    &.transaction-class-scroll{
        
        height: 100vh;
        overflow-y: auto;

        &.no-height{
            height: auto;
        }
    }
    li{
        background: $white;
        border-bottom: 1px solid $border_color;
        padding: 12px 15px;
        font-size: 11px;
        font-family: $font-regular;
        color: $help_text_color;
        .flex{
            display: flex;
        }
        .transaction-icon{
            display: flex;
            flex-direction: column;
            flex: 1;
            color: $help_text_color;
        }
        .transaction-detail{
            display: flex;
            flex-direction: column;
            flex: 6;
            justify-content: center;

            .id-date-time{
                color: $help_text_color;
                font-weight: bold;
                font-size: 12px;
            }
        }
        .transaction-type {
            display: flex;
            flex-direction: column;
            flex: 2;
            text-align: right;
            justify-content: center;
            .amount-text{
                font-family: $font-regular;
                font-size: 14px;
                font-weight: bold;
                line-height: 18px;
                text-align: right;
            }
            .transaction-status {
                font-size: 12px;
                font-family: $font-bold;
                &.success{
                    color: $success;
                }
            }
        }
    }

    
}

.promo-code-section{
    text-align: center;
    margin: 15px 0;
    font-size: 14px;
    cursor: pointer;
    font-family: $font-regular;
    font-weight: bold;
    color: $primary;
}

.promocode-congrats {
    padding: 30px;
    font-size: 11px;
    color: $help_text_color;
    font-family: $font-regular;
    line-height: 1.2;
}
.btn-rounded.btn-success{
    position: absolute;
    color: $primary;
    background: transparent;
    right: 0;
    top: 13px;

    &.position-initial{
        position: initial;
    }
    &:active:focus{
        box-shadow: none !important;
    }
    &:disabled{
        display: none !important;
    }
}

.input-with-cancel{
    .remove-promo-code{
        top: 30px;
        bottom: auto;
        position: absolute;
        right: 10px;
        width: 20px !important;
        i{
            font-size: 10px;
            position: static;
        }
    }
}

.promo-btn{
    &.btn-rounded.btn{
        position: static;
        background: $primary;
        min-width: 100px;
        color: $white;
        margin-top: 30px;
        text-transform: capitalize;
        font-size: 12px;
        font-family: $font-bold;
        border-radius: 20px;
        background-position: center;
        transition: 0.8s;

        &:active {
            @include background-opacity($primary,0.8);
            background-size: 100%;
            transition: 0s;
        }
    }
}
.verify-wrapper{
    padding: 30px;
    padding-bottom: 0;
}
.upload-section{
    border: 1px dashed $help_text_color;
    border-radius: 10px;
    padding: 20px 15px;
    text-align: center;
    i{
        font-size: 90px;
        color: #E5E5E5;
        // margin-bottom: 30px;
        display: inline-block;
        line-height: 60px;
        margin-bottom: 15px;
        &.icon-delete{
            line-height: 30px;
        }
    }
    .upload-text{
        font-size: $font-base;
        color: $primary_text_color;
        font-family: $font-regular;
        cursor: pointer;
    }
    .upload-details{    
        font-size: $font-size-h6;
        color: $help_text_color;
        margin-top: 5px;
        margin-bottom: 5px;
    }
    .upload-description{    
        font-size: $font-size-h6;
        color: $help_text_color;
        margin: auto;
        max-width: 80%;
        font-family: $font-book;
    }
    img.pan-img{
        height: 65px;
        color: #E5E5E5;
        display: inline-block;
        /* line-height: 60px; */
        margin-bottom: 15px;
    }
}
.uploaded-info-section{
    padding: 20px 0;
    &.noneditable-section{
        padding: 20px;
        border-radius: 10px;
        @include background-opacity($app_bg_color, 0.6);
        margin-top: 10px;
        .input-label-center:not(.label-left) span{
            color: $help_text_color !important;
            font-size: $font-size-small !important;
            font-family: $font-regular !important;
        }
        .input-label-spacing .form-group input{
            font-size: $font-base !important;
            color: $primary_text_color !important;
            font-family: $font-book !important;
            border-bottom: none !important;
        }
        .datepicker_display{
            label{
                color: $help_text_color !important;
                font-size: $font-size-small !important;
                font-family: $font-regular !important;
                &.dob-text{
                    font-size: $font-base !important;
                    font-family: $font-book !important;
                    color: $primary_text_color !important;
                }
            }
        }
        .datepicker_display.float-label input{
            border: none !important;
            font-size: $font-base !important;
            color: $primary_text_color !important;
            font-family: $font-book !important;
        }
        .dob-border{
            border: none;
        }
    }
}
.button.button-primary-rounded.btn-verify{
    max-width: 280px;
    display: block;
    margin: 0 auto;
    @media (min-width: 768px){
        max-width: 80%;
    }
}

.profile-section{
    .select-gender .secondary__single-value{
        opacity: 0;
        height: 100%;
    }
    .secondary__value-container{
        height: 100%;
    }
}
.genderStyle.float-label{
    position: relative;
    .input-label-center:not(.label-left) input{
        pointer-events: none !important;
    }
    .secondary__control{
        height: 100%;
    }
    .select-secondary{
        position: absolute;
        top: 0px;
        width: 100%;
        height: 100%;
        background: transparent !important;
        opacity: 1;
    }
    input{
       pointer-events: none !important;
        // background: red !important;
        margin-top: 0 !important;
    }
}
.datepicker_display.float-label{
    .dobField-overlay{
        position: absolute;
        height: 60px;
        z-index: 999;
        background: transparent;
        opacity: 0.1;
        width: 100%;
    }
    .dobField{
        height: 50px;
    }
    position: relative;
    height: 56px;
    .dob-text{
        width: 100%;
        position: static;
        height: 100%;
    }
    .date-picker-custom{
        padding: 0;
        position: absolute;
        top: 0;
        height: 55px;
        width: 100%;
        left: 0;
        z-index: 999;
    }
    .react-date-picker__inputGroup__input{
        pointer-events: none !important;
        width: 100% !important;
    }
    input{
        margin-top: 0 !important;
    }
    .react-date-picker__inputGroup__divider{
        width: 0 !important;
    }
}
.input-label-center{
    input{
        border-radius: 0 !important;
    }
    &.disabled{
        pointer-events: none;
    }
}
.coin-size{
    height: 20px;
    width: 20px;
    margin-right: 5px;
    &.small{
        width: 12px;
        height: 12px;
        margin-right: 3px;
    }
}
