.what-is-new {
    background: $white !important;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 0 !important;
    overflow-x: hidden;

    .header-action.skip-step {
        color: $help_text_color;
        font-size: $font-base;
        font-weight: 600;
        text-transform: uppercase;
        font-family: $font-regular;
        height: auto;
        width: auto;

        &.active {
            color: $primary;
        }
    }

    .swipe-view {
        width: 100%;
    }

    .slider-container {
        width: 100%;
        padding: 0 8%;
        margin-top: 75px;
        overflow: hidden;
        text-align: center;

        @media (max-height:800px) {
            margin-top: 56px;
        }

        @media (max-width:320px) {
            padding: 0 6%;
        }

        .slides {
            position: relative;

            .c-slide {
                transform: translateX(100%);
                opacity: 0;
                position: absolute;
                top: 6px;
                left: 0;

                &.active {
                    opacity: 1;
                    position: inherit;
                }

                .slider-title {
                    font-size: $font-size-h2;
                    font-family: $font-bold;
                    color: $primary_text_color;
                    margin-bottom: 12px;
                    text-align: left;
                    line-height: 1.25;
                    max-width: 230px;
                    width: 230px;
                    margin-left: 8.5%;

                    @media (max-height:667px) {
                        line-height: 1.15;
                        margin-bottom: 8px;
                    }
                }

                .slider-desc {
                    font-size: $font-size-small;
                    font-family: $font-regular;
                    color: $help_text_color;
                    text-align: left;
                    max-width: 274px;
                    width: 274px;
                    margin-left: 8.5%;
                    height: 40px;
                    max-height: 40px;
                }

                img {
                    width: 100%;
                    object-fit: cover;
                    padding-top: 28px;
                    max-width: 320px;

                    @media (max-width:414px) {
                        max-width: 100%;
                    }

                    @media (max-height:667px) {
                        width: 95%;
                        padding-top: 16px;
                    }

                    @media (min-height:800px) {
                        max-width: 400px;
                        width: 100%;
                        padding-top: 36px;
                    }
                }

                &.slide-in-l {
                    animation: slide-in-l 0.6s forwards;
                }

                &.slide-in-r {
                    animation: slide-in-r 0.6s forwards;
                }

                @keyframes slide-in-l {
                    0% {
                        transform: translateX(-100%) scale(0.8);
                    }

                    100% {
                        transform: translateX(0%) scale(1);
                    }
                }

                @keyframes slide-in-r {
                    0% {
                        transform: translateX(100%) scale(0.8);
                    }

                    100% {
                        transform: translateX(0%) scale(1);
                    }
                }

                &.slide-out-l {
                    animation: slide-out-l 0.6s forwards;

                    &.no-anim {
                        animation: none;
                    }
                }

                &.slide-out-r {
                    animation: slide-out-r 0.6s forwards;
                }

                @keyframes slide-out-l {
                    0% {
                        opacity: 1;
                        transform: translateX(0%) scale(1);
                    }

                    100% {
                        opacity: 0;
                        transform: translateX(100%) scale(0.8);
                    }
                }

                @keyframes slide-out-r {
                    0% {
                        opacity: 1;
                        transform: translateX(0%) scale(1);
                    }

                    100% {
                        opacity: 0;
                        transform: translateX(-100%) scale(0.8);
                    }
                }

            }
        }


        .slider-dots-ul {
            align-self: center;
            list-style: none;
            margin: 0;
            padding: 0;
            display: flex;
            justify-content: center;
            padding-top: 55px;
            margin-bottom: 30px;

            @media (max-height:667px) {
                padding-top: 20px;
                margin-bottom: 20px;
            }

            @media (min-height:812px) {
                padding-top: 66px;
                margin-bottom: 56px;
            }

            li {
                margin-right: 8px;

                &:before {
                    content: '';
                    height: 7px;
                    width: 15px;
                    display: inline-block;
                    background: $secondary_border_color;
                    border-radius: 3.5px;
                    cursor: pointer;
                    transition: all 300ms ease;
                }

                &.active::before {
                    width: 30px;
                    background: $primary;
                }

                &:last-child {
                    margin-right: 0px;
                }
            }
        }

        .footer-btns {
            display: flex;
            justify-content: space-between;

            a {
                padding: 5px 0;
                transition: all 300ms ease;
            }

            .btn-hide {
                visibility: hidden;
                pointer-events: none;
            }
        }
    }
}

.daily-chekin-modal {
    @media (max-width:480px) {
        padding: 40px 30px 20px;
    }

    @media screen and (min-width: 384px) and (max-width:384px) {
        padding: 40px 44px 20px;
    }

    .modal-content {
        @media (max-height:700px) {
            transform: scale(0.93);
        }

        background: transparent !important;
        background-color: transparent !important;
        box-shadow: none !important;
        border-radius: 0;

        .curve-img {
            width: 100%;
            min-height: 400px;
            min-width: 250px;

            @media (max-width:360px) {
                width: 98%;
            }
        }

        .modal-body {
            padding: 0px;

            .container {
                position: relative;
                width: 85%;

                @media (max-width:1000px) {
                    width: 90%;
                }

                @media (max-width:1366px) and (max-height: 768px) {
                    width: 80%;
                }

                @media (max-width:414px) {
                    width: 95%;
                }

                @media (max-width:400px) {
                    width: 100%;
                }

                .daily-view {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;


                    .top-view {
                        display: flex;
                        position: relative;
                        top: -40px;
                        padding: 0 35px 0 20px;

                        .c1 {
                            position: absolute;
                            width: 7%;
                            height: 12%;
                            top: -12px;
                            left: 105px;
                            opacity: 0;

                            @media (max-width:320px) {
                                width: 6%;
                                height: 11%;
                                top: -5px;
                                left: 90px;
                            }
                        }

                        .c2 {
                            position: absolute;
                            width: 12%;
                            height: 20%;
                            top: 14px;
                            left: 124px;
                            opacity: 0;

                            @media (max-width:320px) {
                                width: 11%;
                                height: 19%;
                                top: 15px;
                                left: 100px;
                            }
                        }

                        img {
                            width: 45%;
                            height: 100%;
                            object-fit: contain;
                            opacity: 0;
                        }

                        &.animate-coins {

                            img {
                                opacity: 1;
                                animation: animate-coin 0.8s;

                                @keyframes animate-coin {
                                    from {
                                        opacity: 0;
                                    }

                                    to {
                                        opacity: 1;
                                    }
                                }
                            }

                            .c1 {
                                animation: animate-c1 1.2s;
                                transform: rotateY(0deg);

                                @keyframes animate-c1 {
                                    from {
                                        top: -100%;
                                        transform: rotateY(-180deg);
                                    }

                                    to {
                                        top: -12px;
                                        transform: rotateY(0deg);

                                        @media (max-width:320px) {
                                            top: -5px;
                                        }
                                    }
                                }
                            }

                            .c2 {
                                animation: animate-c2 0.6s;
                                transform: rotateY(0deg);

                                @keyframes animate-c2 {
                                    from {
                                        top: -100%;
                                        transform: rotateY(-180deg);
                                    }

                                    to {
                                        top: 14px;
                                        transform: rotateY(0deg);

                                        @media (max-width:320px) {
                                            top: 15px;
                                        }
                                    }
                                }
                            }
                        }



                        .top-text-view {
                            display: flex;
                            flex-direction: column;
                            justify-content: space-between;
                            font-size: $font-size-medium;
                            color: $primary_text_color;
                            line-height: 22px;
                            width: 53%;
                            position: relative;

                            .earn-daily {
                                text-align: right;
                                position: absolute;
                                bottom: 0;
                                right: -8px;
                                width: 185px;
                            }

                            p {
                                text-align: right;
                                font-family: $font-bold;

                                img {
                                    height: 18px;
                                    width: 18px;
                                    margin-right: 4px;
                                    animation: spinHorizontal 0.7s infinite linear;
                                    margin-bottom: 1.5px;

                                    @keyframes spinHorizontal {
                                        12% {
                                            transform: rotateY(0deg);
                                        }

                                        100% {
                                            transform: rotateY(180deg);
                                        }
                                    }
                                }
                            }

                            .daily-text {
                                width: 117px;
                                color: $white;
                                font-family: $font-bold;
                                font-size: $font-size-h3;
                                line-height: 27px;
                                padding-top: 10px;
                                text-transform: uppercase;
                                margin-left: auto;
                                margin-right: -18px;
                            }
                        }


                        @media (max-width:400px) {
                            top: -50px;
                            padding: 0 12px 0 18px;

                            img {
                                width: 50%;
                                max-height: 205px;
                            }

                            .top-text-view {
                                font-size: $font-base;
                                line-height: 16px;
                                min-width: 160px;

                                @media (max-width:320px) {
                                    min-width: 140px;
                                }

                                .earn-daily {
                                    right: 36px;
                                    text-align: right;

                                    p {
                                        img {
                                            height: 18px;
                                            width: 16px;
                                            margin-right: 3px;
                                        }
                                    }

                                    @media (max-width:320px) {
                                        font-size: $font-size-small;
                                    }
                                }

                                .daily-text {
                                    font-size: $font-size-h4;
                                    line-height: 24px;
                                    right: 14px;
                                    position: relative;
                                    margin-right: 0px;
                                    padding-top: 18px;

                                    @media (max-width:320px) {
                                        width: 90px;
                                        font-size: $font-size-medium;
                                        line-height: 20px;
                                        right: 20px;
                                    }
                                }
                            }
                        }
                    }

                    .daily-grid {

                        display: grid;
                        grid-template-columns: repeat(3, 1fr);
                        grid-auto-rows: auto;
                        grid-gap: 14px;
                        padding: 0 30px;
                        margin-top: -20px;

                        @for $i from 1 through 9 {
                            li:nth-child(#{$i}) {
                                animation-delay: .15s * $i;
                            }
                        }

                        @keyframes fade-anim {
                            0% {
                                opacity: 0;
                                transform: scale(0.5);

                            }

                            100% {
                                opacity: 1;
                                transform: scale(1);
                            }
                        }

                        .daily-card {
                            position: relative;
                            display: flex;
                            flex-direction: column;
                            justify-content: space-between;
                            border: 1px solid $alert_success_color;
                            border-radius: 6px;
                            padding: 12px;
                            animation: fade-anim 0.3s linear;
                            animation-fill-mode: both;
                            max-height: 118px;
                            max-width: 90px;

                            .crad-tick {
                                position: absolute;
                                top: -1px;
                                right: -1px;
                                width: 20px;
                            }

                            p {
                                text-align: center;
                                margin-bottom: 0;
                                font-family: $font-bold;
                                font-size: $font-size-xsmall;
                                color: $help_text_color;

                                @media (min-height:815px) {
                                    font-size: $font-size-small;
                                }
                            }

                            .coin-count {
                                font-size: $font-size-h4;
                                color: $body_text_color;
                                margin: 6px 0 4px;

                                @media (min-height:815px) {
                                    font-size: $font-size-h3;
                                    margin: 15px 0 12px;
                                }
                            }

                            .coin-img {
                                width: 16px;
                                height: 16px;
                                margin-right: 3px;
                            }

                            &.disabled {
                                background-color: $app_bg_color;
                                border: none;

                                .coin-count {
                                    color: $help_text_color;
                                }

                                p {
                                    color: $help_text_color !important;

                                    .coin-count {
                                        color: $help_text_color;
                                    }
                                }

                                .crad-tick {
                                    display: none;
                                }
                            }

                            &.active {
                                background-color: $primary;
                                border: none;

                                .crad-tick {
                                    display: none;
                                }

                                p {
                                    color: $white;

                                    .coin-count {
                                        color: $white;
                                    }
                                }
                            }

                        }

                        @media screen and (max-width: 350px),
                        screen and (max-height: 700px) {
                            margin-top: -30px;
                        }

                        @media (max-height:600px) {
                            margin-top: -30px;
                            padding: 0 20px;
                            grid-gap: 10px;

                            .daily-card {
                                padding: 8px;

                                .coin-count {
                                    font-size: $font-size-h5;
                                    margin: 5px 0 3px;
                                }
                            }
                        }
                    }

                    .bottom-msg {
                        color: $help_text_color;
                        font-size: $font-size-xsmall;
                        text-align: center;
                        width: 196px;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        bottom: 3%;

                        &.botm-0 {
                            bottom: 4px;
                        }

                        @media (max-height:640px) {
                            bottom: 2%;
                        }
                    }

                    .btn-claim {
                        max-width: 64%;
                        padding: 12px 30px 11px;
                        text-align: center;
                        border: 5px solid rgba(0, 0, 0, 0.9);
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        bottom: -27px;
                        border-radius: 27px;
                        cursor: pointer;

                        &:active {
                            background-color: $secondary;
                        }

                        @media (max-height:640px) {
                            bottom: -24px;
                            border-radius: 24px;
                            padding: 10px 30px 9px;
                            border: 4px solid rgba(0, 0, 0, 0.9);
                        }
                    }
                }
            }
        }
    }
}

