            width: 100%;
            max-width: 96px;
        }

        .coins-label {
            position: absolute;
            bottom: 2.5px;
            font-size: $font-size-small;
            font-family: $font-bold;
            color: $white;
            transform: rotate(-4deg);
            z-index: 2;
            width: 100%;
            text-align: center;

            &.sports-h {
                font-size: 11.52px;
                text-transform: uppercase;
