    .ring-container {
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 125px;
        height: 120px;
        transform: translateX(-50%);
    }

    .pulse-view {
        border-radius: 50%;
        background-color: white;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 35px;
        opacity: 0;
        animation: pulse-round 2.8s infinite cubic-bezier(.36, .11, .89, .32);
    }
    .line-img {
        &.min-line{
            min-width: 60px;
            min-height: 330px;
        }
    }
    @media (max-height:650px) {
        .line-img {
            max-height: 330px;
        }
    }

    &.animate-v {

        opacity: 1;
        animation: animate-v 0.2s;

        @keyframes animate-v {
            from {
                opacity: 0;
            }

            to {
                opacity: 1;
            }
        }
    }
}

.coin-shine {
    .fcoin {
        position: relative;
        overflow: hidden;
        border-radius: 25px 25px 0 0;

        .spark1 {
            position: absolute;
            top: 3px;
            color: $white;
            left: 33%;
            font-size: $font-size-h6;
            animation: blink-shine1 2s infinite ease-in-out;
            transform: scale(0);

            @keyframes blink-shine1 {
                0% {
                    transform: scale(0);
                }

                14% {
                    transform: scale(1.1);
                }

                18% {
                    transform: scale(0);
                }
            }
        }

        .spark2 {
            position: absolute;
            color: $white;
            right: 25%;
            bottom: 15px;
            font-size: 8px;
            animation: blink-shine1 2s infinite ease-in-out;
            animation-delay: 0.2s;
            transform: scale(0);

            @keyframes blink-shine1 {
                0% {
                    transform: scale(0);
                }

                9% {
                    transform: scale(1);
                }

                12% {
                    transform: scale(0);
                }
            }
        }

        .spark3 {
            position: absolute;
            bottom: 22px;
            color: $white;
            left: 20%;
            font-size: 8px;
            animation: blink-shine1 2.5s infinite ease-in-out;
            transform: scale(0);
            animation-delay: 0.3s;

            @keyframes blink-shine3 {
                0% {
                    transform: scale(0);
                }

                50% {
                    transform: scale(1);
                }

                100% {
                    transform: scale(0);
                }
            }
        }

    }


    .fcoin:after {

        animation: shine 1.8s ease-in-out infinite;
        animation-fill-mode: forwards;
        content: "";
        position: absolute;
        top: -110%;
        left: -140%;
        width: 160%;
        height: 140%;
        opacity: 0;
        transform: rotate(30deg);
        z-index: 1;
        overflow: hidden;
        border-radius: 25px 25px 0 0;
        background: rgba(255, 255, 255, 0.13);
        background: linear-gradient(to right,
                rgba(255, 255, 255, 0.10) 0%,
                rgba(255, 255, 255, 0.13) 77%,
                rgba(255, 255, 255, 0.4) 92%,
                rgba(255, 255, 255, 0) 100%);
    }

    .fcoin:active:after {
        opacity: 0;
    }

    @keyframes shine {
        25% {
            opacity: 1;
            top: -30%;
            left: -30%;
            transition-property: left, top, opacity;
            transition-duration: 0.7s, 0.7s, 0.15s;
            transition-timing-function: ease;
        }

        70% {
            opacity: 0;
            top: -30%;
            left: -30%;
            transition-property: left, top, opacity;
        }
    }
}

.how-earn-coins {
    padding-bottom: 40px !important;
    padding-top: 56px;
    min-height: 100vh !important;

    &.mh-100-per {
        min-height: 100% !important;
    }

    .top-profile {
        overflow: hidden;
        background-color: $primary;
        height: 110px;
        border-top: 1px solid rgba(255, 255, 255, 0.29);
        padding: 8px 15px;
        display: flex;
        justify-content: space-between;
        position: relative;

        .profile-v {
            display: flex;
            align-items: center;
            max-height: 60px;
            max-width: 45%;

            img {
                height: 58px;
                width: 58px;
                min-width: 58px;
                border-radius: 100%;
                overflow: hidden;
                border: 2px solid $white;
                object-fit: cover;
                background: $white;
            }

            .v-username {
                font-family: $font-bold;
                font-size: $font-base;
                color: $white;
                margin-left: 12px;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .coins-v {
            display: flex;
            align-items: center;
            max-height: 60px;
            position: relative;

            .redeem {
                text-decoration: underline;
                color: $white;
                font-size: $font-size-small;
                font-family: $font-regular;
                margin-right: 12px;
                cursor: pointer;
            }

            .c-balance {
                background-color: rgba(0, 0, 0, 0.4);
                padding: 0 18px;
                height: 30px;
                border-radius: 15px;
                display: flex;
                align-items: center;
                position: relative;
                cursor: pointer;

                &.show-tooltip {
                    @keyframes scaleIn {
                        from {
                            transform: scale(1.1, 1.12);
                            border: 0.5px solid rgba(255, 255, 255, 0.5);
                            opacity: 1;
                        }

                        to {
                            transform: scale(1.6, 2.0);
                            border: 0.5px solid rgba(255, 255, 255, 0);
                            opacity: 0;
                        }
                    }

                    .pulse-ring {
                        border-radius: 15px;
                        height: 30px;
                        width: 100%;
                        position: absolute;
                        animation: scaleIn 3s infinite cubic-bezier(.36, .11, .89, .32);
                        border: 0.5px solid rgba(255, 255, 255, 0);
                        left: 0;
                        opacity: 0;
                    }
                }


                img {
                    width: 16px;
                    height: 16px;
                    margin-right: 4px;
                }

                span {
                    color: $white;
                    font-size: $font-size-medium;
                    font-family: $font-bold;
                    margin-top: 2px;
                }
            }

        }

        .coin-tooltip {
            background-color: rgba(33, 33, 33, 0.8);
            padding: 8px 14px;
            border-radius: 6px;
            max-width: 160px;
            position: absolute;
            right: 105px;
            top: 15px;
            z-index: 100;

            .inner-text {
                font-family: $font-bold;
                text-align: left;
                font-size: $font-size-small;
                color: $white;
                line-height: 16px;
            }
        }
    }

    .list-type {
        margin: -35px 0 0;
        padding: 0 15px;
        position: relative;

        .shimmer-container {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        li {
            perspective: 100px;
            list-style: none;
            padding: 20px 25px;
            border-radius: 14px;
            overflow: hidden;
            background-color: $white;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 20px;
            height: 127px;
            animation: flip-anim 0.4s linear;
            animation-fill-mode: both;
            transform: rotateY(-90deg);
            transition: all 0.2s cubic-bezier(.36, -0.64, .34, 1.76);

            .list-t {
                text-transform: uppercase;
                font-family: $font-bold;
                color: $body_text_color;
                margin-bottom: 7px;
                line-height: 18px;
            }

            .list-d {
                font-family: $font-regular;
                color: $body_text_color;
                font-size: $font-size-small;
                line-height: 14px;
                margin-bottom: 15px;
                max-width: 170px;
            }

            .list-btn {
                text-transform: uppercase;
                font-family: $font-bold;
                color: $white;
                padding-top: 4px;
                border-radius: 12px;
                background-color: $primary;
                font-size: $font-size-small;
                display: inline-block;
                width: 94px;
                height: 24px;
                text-align: center;
            }

            img {
                width: 85px;
                object-fit: contain;
            }
        }

        @for $i from 1 through 7 {
            li:nth-child(#{$i}) {
                animation-delay: .10s * $i;
            }
        }

    }
}

@keyframes flip-anim {
    0% {
        opacity: 0;
        transform: rotateY(-90deg);
        transition: all 0.5s cubic-bezier(.36, -0.64, .34, 1.76);

    }

    100% {
        opacity: 1;
        transform: none;
        transition: all 0.5s cubic-bezier(.36, -0.64, .34, 1.76);
    }
}

