.modal-dialog{
    &.contest-detail-modal {
        &.primary-h{
            .modal-header{
                background: $primary;
                border-radius: 0;
                .icon-close{
                    color: $white !important;
                }
                .team-header-content{
                    color: $white !important;
                }
                .match-timing{
                    color: $white !important;
                }
                .countdown{
                    color: $white !important;
                }
                .time-line{
                    color: $white !important;
                }
            }
        }
        max-width: $window_width;
        height: 100vh;
        margin: 0 auto;
        .modal-close{
            right: 20px;
            left: auto;
        }
        @media (max-width: 767px){
            margin: 0;
        }
        .collection-description{
            font-size: 12px;
            line-height: 16px;
            font-family: $font-regular;
            color: $body_text_color;
            padding: 45px 15px 15px 15px;
            border-bottom: 1px solid $border_color;
            position: relative;
            .collection-info{
                height: 20px;
                position: absolute;
                left: 0;
                top: 15px;
                background: $primary;
                color: $white;
                border-radius: 0 100px 100px 0;
                font-size: 11px;
                text-transform: uppercase;
                padding: 3px 5px 3px 10px;
                width: 125px;
                font-weight: bold;
                font-family: $font-bold;
                letter-spacing: 1px;
                span{
                    margin-top: 2px;
                    position: relative;
                }
                i{
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    left: 20px;
                    font-size: 13px;
                    line-height: 14px;
                }
                img{
                    transform: rotate(90deg);
                    display: inline-block;
                    transform-origin: 28px 30px;
                }
            }
        }
        .modal-content{
            border: 0;
            border-radius: 0;
            max-height: 100vh;
        }
        .modal-header{
            padding: 8px 0px;
            border: 0;
            background: $white;
            z-index: 9;
            .close {
                top: 50%;
                transform: translateY(-50%);
                left: 15px;
                font-size: 12px;
                right: auto;
            }
            .modal-title{
                line-height: 1;
                position: relative;
            }
            &.header-with-collection{
                padding: 0;
                h4{
                    padding: 8px 10px 15px;
                    position: relative;
                }
                .modal-close{
                    top: 40%;
                    transform: translateY(-40%);
                }
                .fixture-list-content{
                    margin-bottom: 0;
                    box-shadow: none;
                }
            }
        } 
        .match-heading {
            max-width: 250px;
            margin: 0 auto;
            .verses{
                color: $primary_text_color;
                font-size: 14px;
                font-family: $font-bold;
                width: 20px;
                line-height: 14px;
            }
            .match-timing{
                margin-top: 3px;
                .time-line{
                    font-size: 11px;
                    color: $help_text_color;
                }
            }
            &.header-content{
                text-align: center;
                display: flex;
                justify-content: space-around;
                align-items: center;
                .team-img-block{
                    width: 34px;
                    height: 34px;
                    border-radius: 50%;
                    overflow: hidden;
                    display: inline-block;
                    vertical-align: middle;
                    img{
                        max-width: 100%;
                        max-height: 100%;
                    }
                }
                .team-header-detail{
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0 10px;
                    .team-header-content{
                        color: $primary_text_color;
                        font-family: $font-bold;
                        font-size: 14px;
                        display: inline-block;
                        vertical-align: middle;
                        padding-left:10px;
                        padding-right:10px;
                    }
                    @media (max-width: 400px){
                        margin: 0 5px;
                        .team-header-content{
                            padding-left: 0;
                            padding-right: 0;
                        }
                    }
                    @media (max-width: 320px){
                        .team-header-content{
                            font-size: 11px;
                        }
                    }
                }
            }
        }
        .modal-body{
            padding: 0;
            background: $app_bg_color;
            height: calc(100vh - 108px);
            position: relative;
            .nav.nav-tabs{
                border: 0;
                border-bottom: 1px solid $border_color;
                position: relative;
                z-index: 1;
                li{
                    width: 33.33%;
                    a{
                        width: 100%;
                        background: $white;
                        color: $help_text_color;
                        text-align: center;
                        text-transform: uppercase;
                        font-size: 12px;
                        font-family: $font-bold;
                        letter-spacing: 1px;
                        display: inline-block;
                        padding: 15px;
                    }
                    &.active{
                        a{
                            color: $primary;
                            position: relative;
                            &:after{
                                content: '';
                                position: absolute;
                                bottom: 0px;
                                height: 2px;
                                width: 100%;
                                background: $primary;
                                left:0;
                            }
                        }
                    }
                }
            }
            .tab-content{
                max-height: calc(100vh - 92px);
                height: calc(100vh - 106px);
                background-color: $white;
                overflow: auto;
                > .tab-pane{
                    height: 100%;
                }
            }
        }
        &.contest-with-two-tabs{
            .modal-body{
                .nav.nav-tabs{
                    li{
                        width: 50%;
                    }
                }
            }
        }
        .modal-footer{
            padding: 0;
            border: 0;
            .btn{
                border-radius: 0;
                font-size: $font-base;
            }
        }
        .winning-section{
