.section-one{
    height: 100vh;
    width: 100%;
    background: url('../img/section1-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    @media (max-width: 1200px){
        height: 80vh;
    }
    @media (max-width: 767px){
        height: 100%;
        min-height: 100vh;
        position: relative;
    }
}
.count{
    font-size: 60px;
    font-family: $font-bold;
    color: $gray-575;
    @include media-breakpoint-down(sm) {
        font-size: 42px;
    }
}
.section-text{
    padding-top: 80px;
    @media (max-width: 1366px){
        padding-top: 90px;
    }
    @media (max-width: 1200px){
        padding-top: 320px;
    }
    @media (max-width: 991px){
        padding-top: 50px;
    }
    @media (max-width: 767px) {
        padding-bottom: 380px;
    }
    @media (max-width: 414px) {
        padding-bottom: 230px;
    }
}
.text-h3{
    font-size: 24px;
    line-height: 1.4;
    font-family: $font-bold;
    color: $white;
    @media (max-width: 414px) {
        font-size: 20px;
        padding: 0 20px;
    }
}
.section-heading{
    font-size: 60px;
    line-height: 1;
    font-family: $font-bold;
    color: $gray-900;
    text-transform: uppercase;
    margin-bottom: 40px;
    @include media-breakpoint-down(sm) {
        font-size: 44px;
        margin-bottom: 20px;
    }
}
.brand-logo{
    img{
        width: 146px;
    }
}
.modal-download-app-dialog.modal,
.modal-backdrop.modal-download-app{
    max-width: 100vw !important;
}
.modal-download-app-dialog{
    .modal-header{
        border-bottom: 0;
        .modal-title{
            font-size: 26px;
            font-family: $font-black;
            text-transform: uppercase;
            text-align: center;
        }
    }
    .auth-txt.sm{
        padding-bottom: 0;
        a{
            color: $primary;
        }

    }
    .modal-footer{
        padding: 0;
        overflow: hidden;
        border-radius: 0 0 13px 15px;
        border-top: 0;
        .btn{
            width: 100%;
            border-radius: 0;
            &.click-disabled{
                background: linear-gradient(90deg,$btn-disable-bg,$btn-disable-bg 60%,$btn-disable-bg 90%);
                color: $btn-disable-color;
            }
        }
    }
}

.sms-checkbox{
    .custom-checkbox{
        label{
            line-height: 1.8;
        }
        input[type="checkbox"],input[type="radio"]{
            position: absolute; // take it out of document flow
            opacity: 0; // hide it
            padding: 30px 0 50px;
            &~span {
                position: relative;
                cursor: pointer;
                padding: 0;
                display: block;
                width: 100%;
                padding-right: 30px;
                padding-left: 30px;
                max-width: 100%;
                font-size: 10px;
                color: $gray-575;
                &.auth-txt{
                    font-size: 12px;
                    color: $help_text_color;
                    font-family: $font-regular;
                  
                }
            }

            // Box.
            &~span:before {
                content: '';
                margin-right: 10px;
                display: inline-block;
                vertical-align: text-top;
                width: 14px;
                height: 14px;
                background: transparent;
                border-radius: 2px;
                border: 1px solid $white;
                border: 1px solid $gray-550;
                margin: 0;
                position: absolute;
                left: 7px;
                top: 3px;
            }

            // Box checked
            &:checked~span{
                // color: $white;
                &:before {
                    background: $primary !important;
                    border: none;
                }
            }

            // Disabled state label.
            &:disabled~span {
                color: #b8b8b8;
                cursor: auto;
            }

            // Disabled box.
            &:disabled~span:before {
                box-shadow: none;
                background: #ddd;
            }

            // Checkmark. Could be replaced with an image
            &:checked~span:after {
                content: '';
                position: absolute;
                left: 9px;
                top: 9px;
                background: $white;
                width: 2px;
                height: 2px;
                box-shadow:
                    2px 0 0 $white,
                    4px 0 0 $white,
                    4px -2px 0 $white,
                    4px -4px 0 $white,
                    4px -6px 0 $white,
                    4px -8px 0 $white;
                transform: rotate(45deg);
            }
        }
    }
    .fantasy-expirence-text{
        color: $primary_text_color;
        font-family: $font-bold;
        margin-left: 20px;
        margin-right: 20px;
        font-size: 40px;

    }
}

.how-works{
    background-color: #F9F9F9;
    padding: 90px 0;
    
    h2{
        font-size: 30px;
        line-height: 46px;
        text-align: center;
        color: #333333;
        font-weight: bold;
        text-transform: uppercase;
        font-family: $font-black;
    }
    p{
        color: #4A4A4A;
        font-family: $font-italic;
        font-size: 16px;
        text-align: center;
        @media (max-width: 767px){
            padding: 0 10px;
        }
    }
    .how-works-list{
        text-align: center;
        padding: 90px 0 40px 0;
        h4{
            color: #333333;
            font-size: 16px;
            font-weight: bold;
            margin: 40px 0 0 0;
            line-height: 24px;
            text-transform: uppercase;
            font-family: $font-black;
        }
    }
    @media screen and (max-width:767px){
        padding: 40px 0;
        .how-works-list{
            padding: 40px 0 0 0;
        }
    }
}
.features-block{
    background-position: center;
    background-size: cover;
    padding: 104px 0;
    .container{
        @media (max-width: 767px){
            padding-right: 15px;
            padding-left: 15px;
        }
    }
}
.list-features-home{
    margin: 0;
    padding: 0;
    list-style-type: none;
    li{
        float: left;
        width: calc(100%/3);
        padding: 32px;
        min-height: 290px;
        text-align: center;
        @media (max-width: 991px){
            width: 33%;
            min-height: 320px;
        }
        h4{
            color: #333333;
            font-size: 16px;
            font-weight: bold;
            margin: 20px 0 10px 0;
            line-height: 24px;
            text-transform: uppercase;
            font-family: $font-black;
        }
        p{
            color: #4A4A4A;
            font-family: $font-italic;
            font-size: 14px;
           margin: 0;
        }
        &.bg-white{
            background-color: #FFFFFF;
        }
        &.bg-light{
            background-color: rgba(255,255,255,0.9);
        }
        &.rad-rt{
            border-radius: 0 20px 0 0; 
        }
        &.rad-rb{
            border-radius:0 0 20px 0;
        }
        &.rad-tl{
            border-radius: 20px 0 0 0;
        }
        &.rad-lb{
            border-radius: 0 0 0 20px;
        }
        &.outer-box{
            border-radius: 20px;
            transform: scale(1.1);
            -webkit-transform: scale(1.1);
            -moz-transform: scale(1.1);
            box-shadow: 0 16px 16px 0 rgba(0,0,0,0.05);
            position: relative;
            .inner-box{
                transform: scale(0.8);
                -webkit-transform: scale(0.8);
                -moz-transform: scale(0.8);
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                width: 100%;
            }
            h2{
                font-size: 30px;	
                line-height: 46px;
                font-family: $font-black;
                color: #333333;
                text-transform: uppercase;
                @media (max-width: 767px){
                    font-size: 24px;
                }
            }
            span{
                color: #4A4A4A;
                font-family: $font-italic;
                font-size: 16px;
                 margin: 10px 0 34px 0;
                 display: block;
            }
            .btn-primary{
                min-width: 178px;
                padding: 13px 12px;
                text-transform: uppercase;
            }
        }
    }
    @media screen and (max-width:767px){
        li{
            display: block;
            width: 100%;
            border-radius: 0 !important;
            &.outer-box{
                transform: none;
                
            }
        }
    }
}
