.multi-game-info{
    text-align: center;
    color: $primary_text_color;
    font-size: 20px;
    font-family: $font-bold;
    font-weight: bold;
    background-color: white;
    .multi-game-detail{
        margin-top: 10px;
        color: $body_text_color;
        font-size: 12px;
        font-family: $font-regular;
       
    }
    .collection-image{
        margin-top: 25px;
    }
    .select-multigame{
        color: $primary_text_color;
        font-family: $font-semibold;
        font-size: 13px;
         margin-top: 25px;
    }
    .select-multigame-detail{
        margin-top: 5px;

        color: $body_text_color;
        font-size: 12px;
        font-family: $font-regular;
       
    }
    span{
        font-size: 20px;
    font-family: $font-bold;
    font-weight: bold;
        color: $primary_text_color;
    }
    p{
        
        margin: 0 0 5px 0;
        font-size: 12px;
        color: $primary_text_color;
        span{
            color: $primary_text_color;
        }
    }
}