
.center-modal{
    &.modal-dialog{
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%) !important;
        margin: 0;
    }
    &.switch-team-modal{
        .modal-content{
            margin: 0 auto;
        }
    }
    &.coin{
        overflow: hidden !important;
        top: auto !important;
    }
    &.redeem{
        overflow: hidden !important;
    }
}
.custom-modal-footer {
    background: linear-gradient(90deg, $primary 0%, $primary 60%, $primary 90%);
    border-bottom-left-radius: $modal-border-radius;
    border-bottom-right-radius: $modal-border-radius;
    padding: 0;
    border: none;
    .btn {
        width: 100%;
        border: none;
        @include button-size(0.75rem, $btn-padding-x, 0.875rem, $btn-line-height, $btn-border-radius);
        border-bottom-left-radius: $modal-border-radius;
        border-bottom-right-radius: $modal-border-radius;
        text-transform: uppercase;
        color: $white;
        font-family: $font-bold;
       box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.2);
        
    }
}

body.modal-open {
    .center-modal {
        overflow: hidden;
        @media (max-width: 767px){
            overflow-y: auto;
        }
    }
}

.center-modal {
    .modal-dialog {
        display: inline-block;
        vertical-align: middle;
        margin: 0 auto;
        width: 100%;
        text-align: left;
        .modal-content {
            box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.1);
        }
    }
    text-align: center;
    &:before {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: 0;
        max-height: 100vh;
    }
}

.edit-input-modal{
    max-width: 350px;
    @media (min-width: 1200px){
        .modal-content{
            min-height: 65vh;
        }
    }
    @media (max-width: 1200px) and (min-width: 968px){
        .modal-content{
            min-height: 45vh;
        }
    }
    @media (max-width: 767px){
        &.modal-dialog{
            max-width: 315px;
        }
    }
    &.edit-input-modal-lg{
        .modal-body{
            min-height: 330px;
            @media (max-width: 360px){
                min-height: 300px;
            }
            @media (max-width: 1366px) and (max-height: 767px) {
                min-height: 260px;
            }
        }
    }
    .modal-header{
        text-align: center;
        padding: 58px 15px 20px;
        border-bottom: none;
        h2{
            font-size: $font-size-medium;
            font-family: $font-bold;
            color: $primary_text_color;
            text-transform: uppercase;
            margin-top: 50px;
        }
        p{
            margin: 0;
            text-align: center;
            font-size: $font-size-small;
            color: $help_text_color;
            margin-top: 10px;
        }
        @media (max-width: 1366px) and (max-height: 767px) {
            padding: 10px 0px 0px;
          }
    }
    .modal-body{
        min-height: 260px;
        position: relative;
    }
    .button{
        cursor: pointer;
        display: block;
        &.btm-fixed{
            position: absolute;
            bottom: 0;
            left: 0;
            text-align: center;
        }
    }
    .modal-content{
        overflow: hidden;
    }
    .opt-block{
        margin-top: 20px;
        max-width: 245px;
    }
    .phone-number-style{
        margin-top: 30px;
    }
    &.edit-mobile-no-modal{
        .modal-header{
            padding-top: 40px;
            padding-bottom: 10px;
            h2{
                margin-top: 40px;
            }
        }
        .modal-body{
            min-height: 350px;
        }
    }
}
.button.button-block{
    display: block;
    width: 100%;
}
.icon-section{
    background: $primary;
    border-radius: 100%;
    font-size: 40px;
    height: 74px;
    width: 74px;
    color: $secondary_text_color;
    text-align: center;
    margin: 0 auto;
    padding: 15px;
    position: relative;
    i,img{
        position: absolute;
        left: 0;
        right: 0;
        top: 50%;
        margin: 0 auto;
        transform: translateY(-50%);
    }
}
.remove-applied-code{
    color: $help_text_color;
    position: absolute;
    right: 0;
    top: 18px;
    font-size: $font-size-medium;
    &.small-image{
        width: 15px;
    }
}

.email-verification-modal{
    &.modal-dialog{
        max-width: 315px;
    }
    .modal-header{
        text-align: center;
        padding: 58px 15px 0;
        border-bottom: none;
        h2{
            font-size: $font-size-medium;
            font-family: $font-bold;
            color: $primary_text_color;
            text-transform: uppercase;
            margin-top: 50px;
        }
    }
    .information{
        font-size: $font-size-medium;
        font-family: $font-regular;
        color: $body_text_color;
        max-width: 220px;
        margin: 0 auto;
        text-align: center;
    }
    .modal-body{
        min-height: 260px;
        position: relative;
    }
    .button{
        display: block;
        &.btm-fixed{
            position: absolute;
            bottom: 0;
            left: 0;
            text-align: center;
        }
    }
    .modal-content{
        overflow: hidden;
    }
}
.modal-action-wrap{
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .button{
        width: 100%;
        &:nth-child(2){
            border-left: 1px solid rgba(255, 255, 255, 0.3);
        }
    }
}
.check-availability{
    font-size: $font-size-small;
    a{
        color: $primary;
        &.disabled{
            color: $help_text_color;
            cursor: not-allowed;
        }
    }
}
.input-action-btn.button.button-primary-rounded-sm{
    font-size: $font-size-h6;
    padding: 9px 8px 8px;
    text-transform: uppercase;
    position: absolute;
    right: 0;
    top: 15px;
    cursor: pointer;
    line-height: 1;
}
.edit-input-form{
    &:not(.edit-email-form){
        .input-action-btn.button.button-primary-rounded-sm{
            top: 0;
        }
    }
    .input-with-btn.input-label-spacing .form-group input{
        padding-right: 78px !important;
    }
    &.edit-Mobile-form{
        .z-idx-unset{
            z-index: unset;
        }
        &:not(.edit-email-form){
            .input-action-btn.button.button-primary-rounded-sm{
                top: 14px;
            }
        }
    }
}
.button-primary{
    &.disabled{
        background: $disabled_text_color !important;
        color: rgba(153, 153, 153, 0.4) !important;
    }
}
.spin2win-modal{
    overflow: hidden !important;
    .modal-dialog{
        width: 100%;
        max-width: 100%;
        padding: 0;
        margin: 0;
        height: 100vh;
        overflow: hidden !important;
    }
    .modal-content{
        border-radius: 0;
        background: transparent;
        box-shadow: none;
    }
    .modal-body{
        padding: 0 35px;
        overflow: hidden !important;
        // background: #201D51;
    }
}



.header-circular-modal{
    &.modal-dialog{
        max-width: 300px;
        .modal-header{
            font-size: $font-size-h5;
            font-family: $font-bold;
            color: $primary_text_color;
            text-transform: uppercase;
            text-align: center;
            position: relative;
            padding: 60px 1.2rem 10px;
            border-bottom: none;
            .modal-img-wrap {
                border-radius: 100%;
                padding: 13px;
                background: $white;
                position: absolute;
                top: -24px;
                left: 50%;
                transform: translateX(-50%);
                width: 86px;
                height: 86px;
                .wrap {
                    border-radius: 100%;
                    padding: 15px;
                    background: $primary;
                    width: 60px;
                    height: 60px;
                    i {
                        font-size: 28px;
                        color: $white;
                    }
                }
            }
        }
        .modal-body{
            background: $white;
            border-radius: 0 0 16.5px 16.5px;
            text-align: center;
            h2{
                font-size: $font-size-h4;
                color: $primary_text_color;
                font-family: $font-bold;
                text-transform: uppercase;
                margin-bottom: 30px;
            }
            p{
                max-width: 190px;
                margin: 0 auto 50px;
            }
            .download-anchor{
                img{
                    max-width: 200px;
                    margin-bottom: 20px;
                }
            }
        }
    }
    .modal-content{
        overflow: visible;
    }
}

.center-modal{
    .htp-reverse-modal{
        &.custom-modal.modal-dialog{
            background: $white;
            max-width: 100%;
            height: 100%;
            .modal-content{
                background: $white;
                box-shadow: none;
                border-radius: 0;
            }
        }
        .modal-body{
            padding: 30px;
        }
        .close-rev{
            position: absolute;
            right: 20px;
            top: 20px;
            font-size: $font-base;
        }
        .rev-htp-img{
            text-align: center;
            img{
                max-width: 290px;
            }
        }
        .rev-text-sec{
            .heading{
                font-size: $font-size-h1 - 2;
                line-height: 1;
                font-family: $font-bold;
                color: $primary_text_color;
                max-width: 270px;
                margin: 40px auto 33px;
                text-align: center;
                @media (max-width: 360px){
                    font-size: $font-size-h2;
                }
            }
            .head{
                color: $primary_text_color;
                font-size: $font-size-medium;
                text-transform: capitalize;
            }
            .desc{
                font-size: $font-base;
                color: $body_text_color;
                max-width: 230px;
            }
            .odd-rev-sec{
                padding: 26px 30px 23px;
                text-align: left;
                transform: skew(-15deg);
                background: linear-gradient(-80deg,hsla(0,0%,100%,0) 50%,#555 250%);
                border-radius: 12px;
                padding: 2px;
                height: 115px;
                width: 80%;
                margin: 0 auto;
                @media (max-width: 767px){
                    width: 100%;
                }
                .inner-sec{
                    padding: 26px 30px 23px;
                    background: #fff;
                    border-radius: 12px;
                    height: 111px;
                }
                .head,.desc{
                    transform: skew(15deg);
                }
                .desc{
                    position: relative;
                    right: -10px;
                }
            }
            .even-rev-sec{
                padding: 26px 30px 23px;
                text-align: right;
                transform: skew(-15deg);
                background:linear-gradient(90deg,#fff 25%,#555 250%);
                border-radius: 12px;
                padding: 2px;
                height: 115px;
                width: 80%;
                margin: 0 auto;
                @media (max-width: 767px){
                    width: 100%;
                }
                .inner-sec{
                    padding: 26px 30px 23px;
                    background: #fff;
                    border-radius: 12px;
                    height: 111px;
                }
                .head,.desc{
                    transform: skew(15deg);
                }
                .desc{
                    float: right;
                    position: relative;
                    right: -10px;
                }
            }
        }
        .btn-rounded{
            font-size: $font-size-h4;
            padding: 15px 80px 13px;
            border-radius: 22px;
        }
        .see-rev-rules{
            font-size: $font-size-small;
            color: $help_text_color;
            text-transform: capitalize;
            text-align: center;
            a{
                color: $primary;
                text-decoration: underline;
            }
        }
        .rev-btn-wrap{
            margin: 30px auto 26px;
        }
    }
}