.edit-ref{
.center-view {
  width: 80%;
  height: max-content;
}

.font-xl {
  color: $primary_text_color;
  font-size: 30px;
  font-family: "Geominist-Bold";
  line-height: 37px;
  text-align: center;
}
.m-t-30-p {
  margin-top: 30%;
}
.m-t-20-p {
    margin-top: 20%;
  }

.title {
  font-size: 12px;
  color: $help_text_color;
  font-family: "Geominist-Regular";
  line-height: 14px;
  text-align: center;
  padding-top: 5px;
}

.box-view {
  border: 1px solid $disabled_text_color;
  border-width: 1px;
  border-radius: 6px;
}

.text-f-view {
  margin-top: 10%;
}

.ref-input-text {
  background-color: transparent;
  border: none;
  width: 100%;
  text-align: center;
  font-size: 20px;
  color: $primary_text_color;
  font-family: "Geominist-Regular";
  line-height: 24px;
  text-align: center;
}

.horizontal-line {
  height: 1px;
  width: 100%;
  background-color: $disabled_text_color;
}


.display-color{
    color: $primary;
}
.display-color-disable{
  color: $btn-disable-bg;
  pointer-events: none;
}

.friend-sign-up-blue{
    font-size: 14px;
    color: $primary;
    font-family: "Geominist-Bold";
    line-height: 18px;
    text-align: center;
    text-transform: uppercase;
}

.title-sm {
    font-size: 10px;
    color: $help_text_color;
    font-family: "Geominist-Regular";
    line-height: 12px;
    text-align: center;
    padding-top: 5px;
  }

  .lower-div{
      text-align: center;
      margin-top: 20px;
   
 
  }

}

.j-c-c {
  justify-content: center;
}
.d-f {
  display: flex;
}