.collection-list-wrapper{
    padding: 0 15px;
    .slick-slide{
        width: 100%;
    }
}
.lobby-anim {
    li {
        perspective: 100px;
        list-style: none;
    }
    .lobby-pin {
        .fixture-card-wrapper{
        border: 1px solid $primary;
        }
    .contest-pin {
        position: absolute;
        top: 0px;
        left: 0px;

        i {
            border-radius: 90px;
            color: $primary;
            font-size: 22px;
        }
    }
}
}

.no-data-container{
    text-align: center;
    padding-top: 20%;
    padding-bottom: 10px;

    .background-image{
        position: relative;
        width: 100%;
        height: 80px;

        margin-bottom: 20px;
        .bg-image{
            text-align: center;            
            width: 100%;
            height: 255px;
            vertical-align: middle;
            object-fit: cover;
        }

        .center-image{
            text-align: center;  
            vertical-align: middle;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    h3{
        color: $primary_text_color;
        font-family: $font-regular;
        font-size: 16px;
        font-weight: bold;
        line-height: 26px;
        text-align: center;
        text-transform: unset;
        padding: 0px 10px;

        &:last-child{
            margin-bottom: 30px;
        }
    }

    h2{
        color: $body_text_color;
        font-family: $font-regular;
        font-size: 14px;
        font-weight: bold;
        line-height: 20px;
        text-align: center;
        padding: 0px 10px;
    }

    .no-data-button{
        width: 200px;
        border-radius: 22px;
        background-color: $primary;
        padding: 9px 8px 8px;
        margin: auto;
        cursor: pointer;

        span{
            color: $white;
            font-family: $font-bold;
            font-size: 14px;
            font-weight: 800;
            line-height: 16px;
            text-align: center;
            text-shadow: 0 0 4px 0 rgba(0,0,0,0.4);
        }
        &:active {
            background-size: 100%;
            transition: 0s;
        }
    }
}

.collection-list{
    border-radius: 14px;
    background: $white;
    padding: 25px;
    margin-bottom: 20px;
    cursor: pointer;
    .display-table-cell{
        &.circle{
            span{
                border-radius: 50%;
            }
        }
        &:first-child,&:last-child{
            width: 25%;
        }
        .team-img{
            max-width: 54px;
            max-height: 54px;
            @media screen and(max-width:360px){
                max-width: 42px;
                max-height: 42px;
            }
        }
    }
}
.match-timing{
    margin: 2px 0;
}
.match-timing {
    color: $help_text_color;
    font-size: 10px;
    font-family: $font-bold-italic;
}
.match-type{
    color: $help_text_color;
    font-size: 10px;
    font-family: $font-bold-italic;

    @media screen and (max-width: 332px){
        font-size: 9px;        
    }
}
.team-block{
    margin-top: 3px;
}
.team-name{
    font-family: $font-bold;
    color: $primary_text_color;
    font-size: 16px;
}
.contest-action{
    margin: 0 0 5px;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 0;
    text-align: center;
    .btn{
        width: 48.5%;
        text-transform: none !important;
        color: $white;
        font-size: 12px;
        font-family: $font-bold;
        font-weight: 800;
        &:last-child{
            margin-left: 10px;
        }
        @media (max-width: 360px){
            font-size: 11px;
            &:last-child {
                margin-left: 5px;
            }
        }
        @media (max-width: 320px){
            font-size: 10px;
        }
        &:hover{
            color: $white;
        }
        background-position: center;
        transition: 0.8s;
        &:active {
            @include background-opacity($primary,0.8);
            background-size: 100%;
            transition: 0s;
        }
    }
}
.collection-list-footer{
    border-top: 1px solid $border_color;
    text-align: center;
    padding: 5px 12px;
    color: $help_text_color;
    font-family: $font-regular;
    font-weight: 800;
    font-size: 14px;
    span{
        color: $primary;
    }
}
.single-banner-wrap{
    .banner-container{
        .banner-item{
            width: 93%;
            min-width: 93%;
            cursor: pointer;
        }
    }
}
.banner-v {
    overflow: hidden;
    transition: height 0.5s;
    transition-delay: 0.1s;
    height: 15px;
    margin-top: -15px;
    opacity: 0;

    &.animation {
        height: 90px;
        opacity: 1;
        margin-top: 0;
    }
}
.banner-container{
    width: 100%;
    background: transparent;
    margin: 15px 0;
    border-radius: 6px;
    
    .banner-item{
        display: inline-block;
        background-color: $white;
        border-radius: 6px;
        width: 98%;
        min-width: 98%;
        margin: 0 auto;
        overflow: hidden;
        height: 60px;
        display: flex;
        cursor: pointer;
        img{
            width: 100%;
            height: 100%;
            object-fit: fill;

        }

        .banner-logo{
            box-sizing: border-box;	
            height: auto;
            width: 52px;   
            margin: 6px 5px 6px 5px;
            border: 2px solid rgba(255,255,255,0.2);	
            background-color:$white;
            @media (max-width: 320px){
                margin: 6px 0;
            }
        }
        .info-container{
            margin-top: 10px;
            margin-bottom: 10px;
            .title-style{
                color:$primary_text_color;
                font-size: $font-size-small;
                font-family : $font-bold !important;
                text-transform: uppercase;
                padding-top: 5px;
            }
            .message-style{
                color:$help_text_color;
                font-size: $font-size-small;
                line-height: normal;
                font-family: $font-regular !important;
                .coin-img{
                    display: unset;
                    max-width: 12px;
                    position: relative;
                    top: -1px;
                    margin: 0 2px 0 0;
                }
                @media (max-width: 320px){
                    font-size: 10px;
                }
                .highlighted-text{
                    color: $primary_text_color;
                    font-size: 12px;
                    font-family: $font-bold;
                    
                }
            }
        }
    }
    &.RF-banner-container{
        .banner-item {
            .banner-logo{
                width: 72px;
            }
            .info-container .title-style{
                font-size: $font-base;
                padding-top: 2px;
            }
        }
    }
    
}
.bonus-ic{
    position: relative !important;
    top: -1px !important;
}
.match-delay-info{
    border-radius: 0 0 15px 15px;
    background-color: $body_text_color; 
    color: $white; 
    font-size: 9px; 
    font-weight: 800; 
    line-height: 10px; 
    padding: 3px 22px 2px; 
    display: block; 
    margin: 0 auto;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: inline-block;
    z-index: 2;
    width: auto;
    max-width: 100%;
    white-space: nowrap;
    .match-delay-info{
        margin: 0 5px;
    }
}
.fixture-list-content{
    border-radius: 14px;
    background: $white;
    margin-bottom: 20px;
    .collection-list{
        box-shadow: none;
        background: transparent;
        border-radius: 0;
        margin-bottom: 0;
        padding: 15px 0;
    }
    .match-type,.collection-list-prize-pool{
        display: inline-block;
        width: 50%;
        vertical-align: middle;
    }
    .match-type{
        text-align: left;
    }
    .collection-list-footer{
        text-align: left;
        .match-type{
            @media (max-width: 414px){
                max-width: 200px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
        }
    }
    .collection-list-prize-pool{
        text-align: right;
        font-size: 14px;
        font-family: $font-bold;

    }
}
.collection-wrap{
    border-radius: 14px;
    background: $white;
    margin-bottom: 20px;
    overflow: hidden;
    border: 1px solid $white;
    padding-left: 6px;
    position: relative;
    .collection-info{
        background: $primary;
        color: $white;
        font-size: 12px;
        font-family: $font-bold;
        text-transform: uppercase;
        padding: 5px 10px;
        left: 0;
        bottom: -26px;
        position: absolute;
        transform: rotate(270deg);
        transform-origin: 0 0;
        width: 52%;
        z-index: 2;
        height: 25px;
        border-radius: 14px 14px 0 0;
        cursor: pointer;
        span{
            position: relative;
            top: -1px;
        }
        .icon-info{
            float: right;
            position: relative;
            top: 2px;
        }
        img{
            transform: rotate(90deg);
            display: block;
            transform-origin: 36px 36px;
            position: relative;
            left: -4px;
            top: 2px;
        }
    }
    .collection-list{
        .team-name{
            font-size: 12px;
        }
        .match-timing{
            margin: 0;
        }
    }
    .collection-header{
        padding: 13.5px;
        width: 100%;
        display: table;
        .collection-info-icon{
            position: absolute;
            left: 5px;
            top: 10px;
            z-index: 3;
            font-size: 15px;
            color: $white;
        }
        .collection-header-left,.collection-list-prize-pool{
            display: table-cell;
            vertical-align: middle;
        }
        .collection-header-left{
            width: 80%;
            @media (max-width: 320px){
                max-width: 74%;
            }
        }
        h1{
            color: $primary_text_color;
            font-size: 14px;
            font-family: $font-bold;
            margin-bottom: 0;
            text-transform: capitalize;
            max-width: 350px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            @media (max-width: 767px){
                max-width: 250px;
            }
            @media (max-width: 400px){
                max-width: 180px;
            }
        }
        .collection-count{
            font-size: 10px;
            font-family: $font-regular;
            color: $help_text_color;
            margin-top: 4px;
            .dot-divider{
                width: 4px;
                height: 4px;
                display: inline-block;
                background-color: $help_text_color;
                border-radius: 50%;
                margin: 1px 15px;
            }
        }
        .collection-list-prize-pool{
            width: 20%;
            @media (max-width: 320px){
                max-width: 26%;
            }
            text-align: right;
            font-size: 10px;
            font-family: $font-bold-italic;
            color: $help_text_color;
            span{
                display: block;
                color: $primary;
                font-size: 14px;
                font-family: $font-bold;
            }
        }
    }
}
.collection-body{
    .slick-arrow.slick-next, .slick-arrow.slick-prev{
        display: none !important;
    }
}
.fixture-list-content{
    .collection-list-slider{
        .collection-list{
            padding: 0 5px 10px;
            .team-block{
                margin-top: 0;
            }
            > .display-table{
                border: 1px solid $border_color;
                background-color: white;
                padding: 8px 0;
                border-radius: 10px;
                &.selection-border{
                    border: 3px solid #0095B8;
                  }
                .team-name{
                    font-size: 11px;
                }
                .team-img{
                    margin: 0 auto;
                    max-width: 35px;
                    max-height: 35px;
                }
            }
            @media (max-width: 767px){
                padding: 0 5px 10px;
                > .display-table{
                    padding: 8px 0;
                    .team-img{
                        max-width: 25px;
                        max-height: 25px;
                    }
                    .team-block{
                        margin: 0;
                    }
                }
            }
        }   
    }
}
.collection-league-name{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: inline-block;
    font-family: $font-regular;
    vertical-align: middle;
    max-width: 250px;
    @media (max-width: 767px){
        max-width: 130px;
    }
    @media (max-width: 320px){
        max-width: 100px;
    }
}
.prd-card-akhada{
    margin-bottom: 20px;
    .img-shape{
        max-width: 100%;
        width: 100%;
        max-height: 145px;
        min-height: 120px;
        object-fit: cover;
        border-radius: 14px;
        overflow: hidden;
        object-position: top;
    }
}
.prd-prize-card {
    position: relative;
    margin-bottom: 20px;

    .img-prize-card {
        height: 130px;
        width: 100%;
    }

    .dfs-c {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        z-index: 2;
        width: 91%;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);

        .img-dfs {
            max-width: 120px;
        }

        p {
            margin: 0;
            font-size: 21px;
            font-family: $font-bold;
            line-height: 24px;
            max-width: 50%;
            color: $primary_text_color;
            @media (min-width: 450px){
                font-size: $font-size-h3;
                line-height: 27px;
                max-width: 42%;
            }
        }

        @media (max-width: 350px) {
            .img-dfs {
                width: 100px;
            }

            p {
                font-size: 18px;
                line-height: 21px;

            }
        }
    }

}
.prd-card-img-only{
    margin-bottom: 20px;
    .img-shape{
        max-width: 100%;
        width: 100%;
        max-height: 130px;
        min-height: 126px;
        object-fit: cover;
        border-radius: 14px;
        overflow: hidden;
    }
    &.is-card{
        .img-shape{
            max-height: 160px;
        }
    }
}
.dfs-card {
    width: 100%;
    height: 126px;
    margin-bottom: 20px;
    cursor: pointer;
    background: linear-gradient(30deg, #00BFEB30 0%, $white 98%);
    border-radius: 14px;
    -webkit-box-shadow: inset 0px 0px 0px 1px #00BFEB;
    -moz-box-shadow: inset 0px 0px 0px 1px #00BFEB;
    box-shadow: inset 0px 0px 0px 1px #00BFEB;
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;

    .dfs-c {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        z-index: 2;
        position: relative;
        width: 90%;

        .img-dfs {
            width: 38%;
            max-width: 140px;
        }

        p {
            margin: 0;
            font-family: $font-bold;
            line-height: 18px;
            text-transform: uppercase;
            max-width: 50%;
            color: $primary_text_color;
            position: relative;
            top: 3px;
            @media (min-width: 450px){
                font-size: $font-size-h5;
                line-height: 22px;
            }
        }
    }

    .img-dfs-shape {
        width: 70px;
        position: absolute;
        right: 0;
        top: 0;
    }

    &.prd-card {
        background: linear-gradient(30deg, #F4B55330 0%, $white 98%);
        -webkit-box-shadow: inset 0px 0px 0px 1px #F4B553;
        -moz-box-shadow: inset 0px 0px 0px 1px #F4B553;
        box-shadow: inset 0px 0px 0px 1px #F4B553;

        .dfs-c {
            .img-dfs {
                max-width: 106px;
            }
        }
    }
}
.leader-board-card {
    width: 100%;
    height: 60px;
    margin-bottom: 20px;
    cursor: pointer;
    background: linear-gradient(30deg, #00BFEB30 98%, $white 0%);
    border-radius: 8px;
        position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;

    .dfs-c {
        justify-content: space-evenly;
        align-items: center;
        z-index: 2;
        position: relative;
        width: 80%;

       

        p {
            margin: 10px;
            font-family: $font-bold;
            line-height: 18px;
            font-size: 14px;
            text-transform: uppercase;
            max-width: 100%;
            color: $white;
            position: relative;
            top: 3px;
        }
    }

    .img-leader-board-shape {
        width: 40px;
        position: flex;
        right: 0;
        top: 0;
    }

    &.prd-card {
        background: url("../img/hall-of-fame-banner.png") no-repeat;
        .dfs-c {
            .img-dfs {
                max-width: 106px;
            }
            p {
                margin: 10px;
                font-family: $font-black;
                line-height: 18px;
                font-size: 14px;
                text-transform: uppercase;
                max-width: 100%;
                color: $white;
                position: relative;
                top: 3px;
            }
        }
    }
}
.top-section-heading {
    font-size: $font-size-small;
    font-family: $font-bold;
    color:$primary_text_color;
    text-transform: uppercase;
    padding: 20px 15px 0;
}
