.roster-postion-header {
  background-color: $white;
  position: fixed;
  top: 56px;
  width: 100%;
  max-width: $window_width;
  z-index: 2;
  &.roster-position-football {
    ul {
      li {
        width: calc(100% / 6);
      }
    }
  }
  &.roster-postion-ncss {
    ul {
      li {
        width: calc(100% / 3);
      }
    }
  }
  &.roster-position-basketball {
    ul {
      li {
        width: calc(100% / 5);
      }
    }
  }
  ul {
    display: block;
    margin: 0;
    padding: 8px 0;
    border-bottom: 1px solid $border-light;
    li {
      display: inline-block;
      width: calc(100% / 4);
      &.three-position {
        width: calc(100% / 3);
      }
      text-align: center;
      position: relative;
      a {
        display: block;
      }
      h4 {
        color: $help_text_color;
        font-size: 11px;
        margin: 0;
        font-family: $font-bold;
        display: inline-block;
        position: relative;
        i {
          &.icon-tick {
            color: $primary;
          }
          color: $icon_color;
          position: absolute;
          right: -17px;
          top: 0;
        }
      }
      p {
        margin: 0;
        font-size: 12px;
        color: $help_text_color;
        span {
          display: inline-block;
          font-family: $font-regular;
          color: $help_text_color;
          font-size: 12px;
          margin: 0 2px;
        }
      }
      .roster-selected-count {
        font-size: 12px;
        color: $help_text_color;
        font-family: $font-regular;
        padding-left: 3px;
      }
      &.active {
        &::after {
          content: "";
          position: absolute;
          bottom: -7px;
          left: 0;
          height: 2px;
          display: block;
          width: 100%;
          background-color: $primary;
        }
        h4 {
          color: $primary;
        }
      }
    }
  }
}
.primary-table {
  background-color: $white;
  thead {
    background-color: $app_bg_color;
    color: $primary_text_color;
    margin-top: 10px;
    font-size: 12px;
    font-family: $font-bold;

    @media screen and (max-width: 320px) {
      font-size: 10px;
    }

    tr {
      border: none;
    }

    th {
      border: 0;
      vertical-align: middle;
      padding: 7px 15px;

      @media screen and (max-width: 360px) {
        font-size: 10px;
      }
    }
  }
  td {
    border-top: 0;
    vertical-align: middle;
    border-bottom: 1px solid $border_color;
    padding: 0px;

    &.btn-roster-td {
      padding: 0 5px;
    }
  }
  .btn-roster-action {
    display: inline-block;

    &.added {
      background-color: $primary;

      i {
        color: $white;
      }
    }
  }
    tr {
        border: 1px solid $border_color;
        cursor: pointer;
    :first-child {
      border: 0px solid $border_color;
    }
    &.disabled {
      pointer-events: none;
      position: relative;
      opacity: 0.5;
      .btn-roster-action {
        background: rgba(0, 0, 0, 0.05);
        box-shadow: none;
        i {
          opacity: 0.7;
        }
      }
    }
    &:last-child {
      td {
        border-bottom: 0;
      }
    }
    &.selected-tr {
      @include background-opacity($primary, 0.15);
    }
  }
}

.roster-player-detail {
    .roster-player-image {
        height: 40px;
        width: 40px;
        background-color: $white;
        border-radius: 50%;
        overflow: hidden;
        padding: 8px;
        border: 1px solid $border_color !important;
        img {
            max-width: 100%;
            position: relative;
            top: 50%;
            transform: translateY(-50%);
        }
    img {
      max-width: 100%;
    }
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
    margin-bottom: 14px;
  }
  .roster-player-content {
    display: inline-block;
    vertical-align: middle;
    margin-top: 4px;
    span {
      display: inline-block;
      color: $body_text_color;
      font-size: $font-base;
      font-family: $font-bold;
    }
    h4 {
      margin-bottom: 0;
      a {
        color: $primary_text_color;
        font-size: $font-base;
        font-family: $font-book;
        margin: 0;
        display: block;
        @media screen and (max-width: 420px) {
          max-width: 110px;
          overflow: hidden;
          font-size: $font-base;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        @media screen and (max-width: 360px) {
          max-width: 100px;
          overflow: hidden;
          font-size: $font-base;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        @media screen and (max-width: 320px) {
          max-width: 80px;
          overflow: hidden;
          font-size: $font-size-small;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
    .roster-player-team {
      font-size: $font-size-small;
      color: $help_text_color;
      font-family: $font-regular;
      @media screen and (max-width: 320px) {
        font-size: 10px;
      }
    }
  }
}
.roster-player-salary {
  font-family: $font-regular;
  color: $body_text_color;
  list-style-type: none;
  padding-left: 0px;
  padding-right: 0px;
  margin-top: 5px;
  font-size: $font-base;
  @media screen and(max-width:320px) {
