}
.pts-style {
  color: #838383;
  font-family: $font-family-sans-serif;
  font-size: $font-size-xsmall;
  font-weight: $font-bold;
  line-height: 30px;
}
.btn-roster-action {
  background-color: $white;
  border: 1px solid $border_color !important;
  border-radius: 5px;
  font-size: $font-size-small;
  padding: 3px 6px;
  margin-right: 10px;

  i {
    color: $primary;
  }
  &.added {
    background-color: $primary;

    i {
      color: $white;
    }
  }
  &.selected {
    background-color: $primary;

    i {
      color: $white;

      &:before {
