  z-index: 10;
  color: $white;
  font-family: $font-bold;
  font-weight: 800;
  font-size: $font-base;
  border-radius: 25px;
  -webkit-position: fixed;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.2);
  padding: 15px 12px 13px;
  line-height: 16px;
}
