}
.player-count-slider {
  padding: 2px 5px;
  max-height: 25px;
  div {
    border: 1px solid $primary;
    border-left: none;
    @include background-opacity($black, 0.09);
    height: 15px;
    display: inline-block;
    vertical-align: middle;
    &:first-child {
      &.active {
        &:before {
          border-left: 1px solid $white;
        }
      }
    }
    &:not(.show-number) {
      span {
        display: none;
        opacity: 0;
      }
    }
    &.active {
      position: relative;
      text-align: center;
      &.show-number {
        &:before {
          animation: fillColor 0.6s linear;
          -webkit-animation: fillColor 0.6s linear;
        }
      }
      &:before {
        content: "";
        left: -1px;
        top: -1px;
        width: calc(100% + 1px);
        height: calc(100% + 2px);
        position: absolute;
        background: $success;
        border: 1px solid $white;
        border-left: none;
      }
      &.show-number {
        span {
          color: $white;
          font-size: 12px;
          position: relative;
          font-family: $font-bold;
          top: -2.5px;
          opacity: 1;
          animation: showNo 0.6s linear;
          -webkit-animation: showNo 0.6s linear;
        }
      }
      @keyframes showNo {
        0% {
          opacity: 0;
        }

        100% {
          opacity: 1;
        }
      }
      @-webkit-keyframes showNo {
        0% {
          opacity: 0;
        }

        100% {
          opacity: 1;
        }
      }
    }
    @keyframes fillColor {
      0% {
        width: 0;
      }

      100% {
        width: calc(100% + 1px);
      }
    }
    @-webkit-keyframes fillColor {
      0% {
        width: 0;
      }

      100% {
        width: calc(100% + 1px);
      }
    }
  }
}
.roster-web-container {
