.roster-header {
  background: transparent;
  overflow: hidden;
  position: relative;
  .step-section {
    padding: 10px 0;
    text-align: center;
    transition: transform 0.2s;
    max-height: 60px;
    .inner-step-section {
      position: relative;
      display: inline-block;
      width: 33.333%;
      &:after {
        content: "";
        position: absolute;
        height: 2px;
        width: 93%;
        left: 53.5%;
        top: 10px;
        transform: translateY(-50%);
        background: $primary_dark;
      }
      &:before {
        content: "";
        position: absolute;
        height: 2px;
        width: 93%;
        left: 53.5%;
        top: 10px;
        transform: translateY(-50%);
        background: $white;

        z-index: 2;
      }
      &.pre-active {
        &:before {
          animation: bgFill 1s linear;
        }
      }
      @keyframes bgFill {
        0% {
          width: 0%;
        }

        100% {
          width: 100%;
        }
      }
      &.non-active-step {
        animation: none;

        &:after {
          left: -44.5%;
        }

        span.step-circle {
          background: $primary_dark;
          color: $primary_text_color;
        }

        &:before {
          display: none;
        }
      }
      &.active-step {
        &:after {
          display: none;
        }

        &:before {
          display: none;
        }

        font-size: $font-size-small;
        font-family: $font-book;
        line-height: 20px;
        top: 1px;

        .step-circle {
          width: 20px;
          height: 20px;
          text-align: center;
          background: $white;
          color: $primary_text_color;
          animation: changeTColor 1.2s linear;
        }

        .label {
          color: $white;
          animation: changeLColor 1.2s linear;
        }

        @keyframes changeTColor {
          0% {
            color: $white;
            background: $primary_dark;
          }

          60% {
            color: $white;
            background: $primary_dark;
          }

          100% {
            background: $white;
            color: $primary_text_color;
          }
        }

        @keyframes changeLColor {
          0% {
            @include color-opacity($white, 0.58);
          }

          60% {
            @include color-opacity($white, 0.58);
          }

          100% {
            color: $white;
          }
        }
      }
    }
    span {
      &.step-circle {
        border-radius: 50%;
        display: inline-block;
        width: 12px;
        height: 12px;
        background: $white;
        position: relative;
        z-index: 2;
        vertical-align: middle;
      }
    }
    .label {
      font-size: $font-size-xsmall;
      font-family: $font-book;
      @include color-opacity($white, 0.58);
    }
  }
  .max-player-alowed-section {
    text-align: center;
    padding: 10px;
    transition: transform 0.2s;
    max-height: 41px;
    .max-player-inner-section {
      border-radius: 10px;
      @include background-opacity($primary_text_color, 0.15);
      padding: 2px 20px 3px;
      font-size: $font-size-small;
      color: $white;
      font-family: $font-book;
      text-align: center;
    }
  }
}
.whole-team-info {
  padding: 0 20px;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  position: relative;
  max-height: 61px;
  .player-selected,
  .salary-count {
    color: $white;
    min-width: 76px;
    .label {
      font-size: $font-size-xsmall;
      text-transform: capitalize;
      font-family: $font-book;
    }
    .player-count {
      font-size: $font-base;
      display: flex;
      .span {
        font-size: $font-size-h2;
        font-family: $font-bold;
        display: inline-block;
        transition: transform 0.2s;
        transform-origin: left top;
      }
      .total-c {
        transition: transform 0.2s;
        transform-origin: left top;
        padding-top: 15px;
      }
    }
  }
  .salary-count {
    text-align: right;
    .player-count {
      display: block;
      .span {
        transform-origin: right top;
      }
    }
  }
  .team-player-info {
    .away-team-info,
    .home-team-info {
      position: relative;
      color: $white;
      font-family: $font-bold;
      display: inline-block;
      vertical-align: middle;
      img {
        border-radius: 50%;
        border: 3px solid $white;
        width: 48px;
        height: 48px;
        background: $help_text_color;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
      .team-nm {
        font-size: $font-size-small;
        text-transform: uppercase;
      }
      .team-player-count {
        font-size: $font-size-h4;
      }
    }
    .home-team-info {
      padding: 0 10px 0 65px;
      img {
        left: 5px;
      }
    }
    .away-team-info {
      text-align: right;
      img {
        right: 5px;
      }
      padding: 0 65px 0 10px;
    }
    @media (min-width: 767px) {
      .home-team-info {
        padding: 0 20px 0 75px;
      }
      .away-team-info {
        text-align: right;
        img {
          right: 5px;
        }
        padding: 0 75px 0 20px;
      }
    }
  }
  @media (max-width: 428px) {
    padding: 0 10px;
    .player-selected,
    .salary-count {
      min-width: 56px;
      .player-count {
        .span {
          font-size: 25px;
        }
        .total-c {
          padding-top: 12px;
          font-size: $font-size-small;
        }
      }
    }
    .team-player-info {
      .home-team-info {
        padding: 0 8px 0 55px;
        img {
          width: 38px;
          height: 38px;
        }
      }
      .away-team-info {
        padding: 0 55px 0 8px;
        img {
          width: 38px;
          height: 38px;
        }
      }
    }
  }
  @media (max-width: 320px) {
    .team-player-info {
      .home-team-info,
      .away-team-info {
        .team-nm {
          font-size: $font-size-xsmall;
        }
        .team-player-count {
          font-size: $font-size-medium;
        }
      }
    }
    .team-player-info {
      .home-team-info {
        padding: 0 8px 0 50px;
      }
      .away-team-info {
        padding: 0 50px 0 8px;
      }
    }
  }
}
.roster-footer {
    padding: 20px 35px;
    background: transparent;
    box-shadow: none;
    text-align: center;
    transition: transform 0.3s;
    pointer-events: none;
    &.hideBottomBtn {
        transform: translateY(70px);
    }
    .btn-wrap {
        width: 80%;
        pointer-events: all;
        @media (max-width: 414px) {
            width: 100%;
        }
        text-align: center;
        vertical-align: middle;
        .btn-primary {
            height: 40px;
            vertical-align: middle;
            width: calc((100% - 10px) / 2);
            &.team-preview{
                background: $white;
                color: $primary;
            }
            &.completed-league-preview{
              background: $white;
              color: $primary;
          }
            background: $primary;
            color: $white;
            padding: 12px 12px 8px;
            box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2) !important;

            &:disabled {
                box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2) !important;
                background: $help_text_color;
                @include color-opacity($white, 0.4);
            }
            &+.btn-primary {
                margin-top: 0;
                margin-left: 10px;
            }
            @media (min-width: 1024px) {
                &.team-preview {
                    display: none;
                }
            }
        }
    }
}
.right-fieldview{
    position: fixed;
    top: 0;
    left: 550px;
    z-index: 999999;
    .field-view-cross-button-container i{
        display: none;
    }
    @media (max-width: 992px){
        display: none;
        left: 0;
        .field-view-cross-button-container i{
            display: block;
        }
    }
    &.show-rfv{
        display: block !important;
    }
}
.played-last-match-text{
  display: block;
  margin-bottom: 5px;
  color: $primary;
  .playing_indicator{
    background: $primary;
  }
}