.button{
    &.button-primary{
        background: $primary;
        font-family: $font-bold;
        padding: 15px 30px 14px;
        text-transform: uppercase;
        color: $white;
        font-size: $font-base;
        border-radius: 0;
        background-position: center;
        transition: 0.8s;
        &.button-with-icon{
            i{
                font-size: $font-base;
                color: $white;
                margin-right: 10px;
            }
        }
        &:active {
            @include background-opacity($primary,0.8);
            background-size: 100%;
            transition: 0s;
        }
        &.disabled {
            pointer-events: none !important;
          }
    }
    &.button-disabled{
        background: $disabled_text_color;
        font-family: $font-bold;
        border-radius: 24px;
        padding: 13px 30px 12px;
        text-transform: uppercase;
        font-size: $font-base;
        color: $help_text_color;
        border-radius: 0;
        &.button-with-icon{
            i{
                font-size: $font-base;
                color: $help_text_color;
                margin-right: 10px;
            }
        }
    }
    &.button-primary-rounded{
        @extend .button-primary;
        padding: 12px 30px 11px;
        border-radius: 24px;
        &.height-b{
            height: 30px;
            text-align: center;
        }
        &.disabled {
            pointer-events: none !important;
          }
    }
    &.button-disabled-rounded{
        @extend .button-disabled;
        padding: 12px 30px 11px;
        border-radius: 24px;
    }
    &.button-primary-sm{
        @extend .button-primary;
        padding: 11px 30px 10px;
        font-size: $font-size-small;
        &.button-with-icon{
            i{
                font-size: $font-size-small;
            }
        }
    }
    &.button-primary-xs{
        @extend .button-primary;
        padding: 7px 22px 5px;
        font-size: $font-size-small;
        &.button-with-icon{
            i{
                font-size: $font-size-small;
            }
        }
        &.button-abs{
            position: absolute;
            right: 16px;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    &.button-primary-rounded-xs{
        @extend .button-primary-xs;
        border-radius: 15px;
    }
    &.disabled-touch{
        pointer-events: none;
        touch-action: none;
    }
    &.button-disabled-sm{
        @extend .button-disabled;
        padding: 11px 30px 10px;
        font-size: $font-size-small;
        &.button-with-icon{
            i{
                font-size: $font-size-small;
            }
        }
    }
    &.button-primary-rounded-sm{
        @extend .button-primary-sm;
        border-radius: 24px;
    }
    &.button-disabled-rounded-sm{
        @extend .button-disabled-sm;
        border-radius: 24px;
    }
    &.button-primary-link{
        color: $primary;
        font-size: $font-base;
        font-family: $font-bold;
        text-transform: uppercase;
        background: transparent;
    }
    &.button-disabled-link{
        color: $help_text_color;
        font-size: $font-base;
        font-family: $font-bold;
        text-transform: uppercase;
        background: transparent;
    }
    &.button-primary-link-sm{
        @extend .button-primary-link;
        font-size: $font-size-small;
    }
    &.button-disabled-link-sm{
        @extend .button-disabled-link;
        font-size: $font-size-small;
    }
}


.default-tab{
    ul{
        width: 100%;
        border: 0;
        @include box-shadow-mixin($box-shadow-sm);
        max-width: 550px;
        overflow-x: auto;
        white-space: nowrap;
        height: 42px;
        display: flex;
        justify-content: center;
        flex-wrap: nowrap;
        overflow-y: hidden;
        li{
            text-transform: uppercase;
            flex-grow: 1;
            &.active{
                a{
                    color: $primary;
                    position: relative;
                    &:after{
                        content: "";
                        position: absolute;
                        bottom: 0;
                        height: 2px;
                        width: 100%;
                        background: $primary;
                        left: 0;
                    }
                }
            }
            a{
                width: 100%;
                background: $view_bg_color;
                color: $help_text_color;
                text-align: center;
                text-transform: uppercase;
                font-size: $font-size-small;
                font-family: $font-bold;
                display: inline-block;
                padding: 13px;
                line-height: 16px;
                @media (max-width: 360px){
                    padding: 13px 6px;
                }
            }
        }
    }
}

.circular-tab{
    ul{
        width: 100%;
        border: 0;
        @include box-shadow-mixin($box-shadow-sm);
        max-width: 550px;
        overflow-x: auto;
        white-space: nowrap;
        display: flex;
        border-radius: 22px;
        overflow: hidden;
        justify-content: center;
        li{
            text-transform: uppercase;
            flex-grow: 1;
            border-radius: 22px;
            overflow: hidden;
            &.active{
                a{
                    color: $white;
                    position: relative;
                    background: $primary;
                }
            }
            a{
                width: 100%;
                background: $view_bg_color;
                color: $help_text_color;
                text-align: center;
                text-transform: uppercase;
                font-size: $font-size-small;
                font-family: $font-bold;
                display: inline-block;
                padding: 13px;
                padding: 6px 13px;
                line-height: 16px;
            }
        }
    }
}

.progress-bar-default{
    .progress-bar{
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: $view_bg_color;
        text-align: center;
        white-space: nowrap;
        background-color: $progress-bar-bg;
        -webkit-transition: width 0.6s ease;
        transition: width 0.6s ease;
        border-radius: $progress-border-radius;
    }
    .progress{
        background: $progress-bg;
        border-radius: $progress-border-radius ;
        height: $progress-height;
        width: 90%;
        &.progress-bar-error{
            .progress-bar{
                background: $progress-bar-error;
            }
            .total-output{
                color: $progress-bar-error;
            }
        }
        .progress-bar{
            background: $green;
        }
        &.danger-area{
            .progress-bar{
                background: $help_text_color;
            }
        }
    }
    .progress-bar-value{
        font-size: $progress-bar-value;
        font-family: $font-regular;
        color: $help_text_color;
        padding-top: 5px;
    }
    .total-output{
        font-size: $progress-bar-value;
        color: $primary;
    }
    .user-joined{
        color: $primary_text_color;
        font-family: $font-book;
    }
    .min-entries{
        color: $help_text_color;
        position: relative;
        font-family: $font-regular;
        right: 9%;
        top: 0;
        float: right;
        font-size: $progress-bar-value;
        &.min-entry-free-to-play{
            right: 0%;

        }
        @media (min-width: 415px){
            right: 10%;
            width: auto;
        }
    }
}

.match-info-section{
    justify-content: space-around;
    display: flex;
    text-align: center;
    align-items: center;
    .section-left,
    .section-right,
    .section-middle{
        display: inline-block;
        vertical-align: middle;
    }
    .section-middle{
        padding: 0 10px;
    }
    &.match-time-only{
        .team-home,.vs-text,.team-away{
            color: white;
        }
        .match-timing{
            .countdown.time-line{
                font-size: $font-base;
                font-family: $font-bold;
                color: $white;
            }
        }
    }
}
.team-home,
.team-away{
    color: $primary_text_color;
    font-size: $font-size-medium;
    text-transform: uppercase;
    font-family: $font-bold;
    &.no-transform{
        text-transform: none;
    }
}
.team-home-rf-leaderboard,
.team-away-rf-leaderboard{
    color: $white;
    font-size: $font-base;
    font-family: $font-bold;
    &.no-transform{
        text-transform: none;
    }
}

.vs-text{
    font-family: $font-bold;
    color: $primary_text_color;
    font-size: $font-base;
    display: inline-block;
    margin: 0 3px;
}
.home-team-flag,
.away-team-flag{
    max-width: 34px;
    max-height: 34px;
}
.match-timing{
    font-size: $font-size-xsmall;
    font-family: $font-regular;
    color: $help_text_color;
    margin: 0;
}
.match-timing-rf-leaderboard{
    font-size: $font-size-xsmall;
    font-family: $font-book;
    color: $white;
    margin: 0;
}

.header-title{
    font-size: $font-size-medium;
    color: $primary_text_color;
    font-family: $font-bold;
    margin: 0;
    text-transform: capitalize;
}
.fixture-card-body{
    padding: 10px 0;
    position: relative;
}
.fixture-card-wrapper{
    overflow: hidden;
    border-radius: $border-radius-lg;
    background: $view_bg_color;
    margin-bottom: 20px;
    .left-section{
        display: inline-block;
        vertical-align: middle;
        width: 20%;
        text-align: center;
        padding: 0 20px;
        img{
            max-width: 54px;
            max-height: 54px;
        }
    }
    .middle-section{
        text-align: center;
        display: inline-block;
        vertical-align: middle;
        width: 60%;
    }
    .right-section{
        padding: 0 20px;
        text-align: center;
        display: inline-block;
        vertical-align: middle;
        width: 20%;
        img{
            max-width: 54px;
            max-height: 54px;
        }
    }
    .fixture-card-footer{
        border-top: 1px solid $border_color;
        padding: 5px 15px;
        color: $help_text_color;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &.height-league-card{
            height: 31px;
        }
        .match-type{
            font-size: $font-size-small;
            font-family: $font-regular;
            width: 50%;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            max-width: 200px;
            @media (max-width: 767px){
                max-width: 140px;
            }
        }
        .winning-section{
            font-family: $font-bold;
            font-size: $font-base;
            span{
                color: $primary;
            }
        }
        &.justify-center{
            justify-content: center;
        }
        
       
    }
    .free-to-play-info{
        width: 100%;
        display: flex;
        padding: 5px 15px;
        align-items: center;
        height: 30px;
        background: #C2E3EE;
        .hall-of-fame-img {
            width: 25px;
            height: 25px;
          
        }
        .text_hall_of_fame{
            margin-left: 12px;
            margin-top: 10px;
            font-family: $font-regular;
            font-size: 12px;
            margin-bottom: 5px;
            font-weight: 300;
            letter-spacing: 0;
            width: 100%;
            overflow: hidden;
            white-space: nowrap;            
        }
    }
    .match-timing{
        font-family: $font-regular;
    }
    &.fixture-card-wrapper-lg{
        .fixture-card-body{
            padding: 20px 0;
            .home-team-flag, .away-team-flag{
                max-width: 54px;
                max-height: 54px;
            }
        }
    }
}
.countdown{
    font-size: $font-size-xsmall;
    color: $alert_danger_color;
    font-family: $font-bold;
}

.inner-view{
    border-radius: 5px;
    border-color: $border_color;
    border-style: solid;
    display: flex !important;
   max-width: 250px;
    padding: 6px;
    margin-left: 20px;
    background-color: $secondary_text_color;
    border-width: 0.5px;
}

.img-height{
    height: 38px;
}

.amt-text-blue{
    font-size: 14px;
    font-family: "Geominist-Regular";
    font-weight: 600;
    line-height: 14px;
    color: $primary-link-color;
}

.amt-text-blue-slider{
    font-size: 14px;
    font-family: "Geominist-Bold";
    font-weight: 600;
    line-height: 14px;
    color: $primary-link-color;
}

.scroll-view{
    scroll-behavior: smooth;
    display: -webkit-box;
    overflow: scroll;
    margin-right: 10px;
}