  }
}

.margin-postion {
  @media (min-height: 650px) and (max-height: 680px) {
    margin-top: 1px;
    position: relative;
  }
  @media (min-height: 800) and (max-height: 815px) {
    margin-top: -2px;
    position: relative;
  }
  @media (max-height: 767px) {
    margin-top: -2px;
    position: relative;
  }
}
.round-line-d {
