}

.round-big-btn {
  height: 50px;
  color: $secondary_text_color;
  background-color: $primary;
  font-size: 14px;
  font-family: "Geominist-Bold";
  line-height: 14px;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: flex;
  border-radius: 25px;
  margin-right: 30px;
  margin-top: 45%;
  margin-left: 30px;
  margin-bottom: 20px;
  @media (max-width: 1366px) and (max-height: 767px) {
    margin-bottom: 5px;
    margin-top: 40px;
  }
  @media (min-width: 411px) and (max-height: 823px) {
    margin-bottom: 20px;
    margin-top: 25%;
  }
  @media (width: 375px) and (height: 812px) {
    margin-bottom: 30px;
    margin-top: 80px;
  }
}

