.prediction-wrap-v {

    &.upcoming-contest-card-new {
        .contest-card-header {
            margin: 0 0px -5px 0px !important;
        }
    }

    .bg-primary {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        max-width: 550px;
        height: 100px;
    }

    @for $i from 1 through 12 {
        li:nth-child(#{$i}) {
            animation-delay: .15s * $i !important;
        }
    }


    .slick-prediction {
        .slick-slide{
            max-width: 181px;
            @media (max-width:350px) {
                max-width: 157px;
            }
        }
        .slick-list {
            padding: 10px !important;
        }

        &.only-one {
            .open-prediction-card-wrapper{
                margin: 0;
            }
            .match-info-section {
                max-width: 100% !important;

                .section-middle {
                    width: 100% !important;
                }

                // .leag-name {
                //     max-width: 100% !important;
                // }
            }
        }
    }

    .status-live {

        .status-text {
            color: $alert_danger_color;
            font-size: 8px;
            font-family: $font-bold;
            text-transform: uppercase;
        }

        .live-indicator {
            height: 4px;
            width: 4px;
            margin: 0 3px 0.5px 0;
        }
    }

    .prediction-card-wrapper {
        position: relative;
        list-style: none;
        padding: 12px 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 72px;
        border-radius: 6px;
        margin: 0 4px;
        box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.1);
        background: $app_bg_color;

        &.active-item {
            background: $white;
            position: relative;
            &:after{
                content: '';
                height: 4px;
                background: $primary;
                width: 100%;
                position: absolute;
                left: 0;
                bottom: 0;
                z-index: 2;
            }
        }

        &.no-bg {
            background: none;
        }


        .fixture-card-body {
            padding: 0;
            z-index: 2;

            .home-team-flag,
            .away-team-flag {
                max-width: 25px;
                max-height: 25px;
            }

            .match-info-section {
                padding: 0;
                max-width: 155px;

                .section-middle {
                    line-height: 14px;
                    width: 73%;
                    padding: 0 6px;
                    @media (max-width:350px) {
                        width: 56%;
                        padding: 0;
                    }

                    white-space: nowrap;

                    .team-home {
                        color: $gray-900 !important;
                        font-size: $font-size-small !important;
                    }

                    .vs-text {
                        color: $gray-900 !important;
                        font-size: $font-size-small !important;
                    }

                    .team-away {
                        color: $gray-900 !important;
                        font-size: $font-size-small !important;
                    }
                }
            }
        }

        .match-timing {
            font-size: $font-size-xsmall;
            line-height: 15px;
            margin: 2px 0 0;

            &.league-n {
                display: flex;
                justify-content: space-evenly;
                white-space: nowrap;

                .leag-name {
                    max-width: 80px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    margin-right: 3px;
                }
            }
        }

        .curve-pre-img {
            position: absolute;
            top: 0px;
            left: -2px;
            width: 105%;
            height: 90%;
            cursor: none;
            pointer-events: none;
        }

        .btn-predict {
            height: 30px;
            border-radius: 15px;
            font-size: $font-size-small;
            padding: 3px 30px 0;
            margin: 0 auto;
            display: block;
        }


    }

    .collection-list-wrapper {
        padding: 0 15px 5px;

        .shimmer-fixture {
            display: flex;
            justify-content: space-between;
            align-items: center;
            text-align: center;
            margin-bottom: 16px;

            span {
                width: 100%;

                span {
                    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.1);
                }
            }
        }
    }

    .p_view-container {
        position: relative;

        .contest-action {
            padding-top: 5px;

            .btn {
                text-transform: uppercase !important;
                background: $white;
                color: $primary;
            }
        }

        .button-white {
            background: $white;
            border: none;
            height: 44px;
            color: $primary;
            font-size: $font-size-small;
            padding-top: 3px;
            cursor: pointer;
            transition: bottom 0.5s;
            transition-delay: 0.1s;

            &:active {
                @include background-opacity($white, 0.5);
            }

            &.hideBtmBtn {
                bottom: -50px;
            }
        }


    }

    .list-pred {
        padding: 10px 15px 0;
        position: relative;

        &.my-pre {
            padding: 0 15px;

            li {
                border: 1px solid $border_color;
            }
        }

        li {
            overflow: hidden;
            border-radius: $border-radius-lg;
            background-color: $view_bg_color;
            background-image: url('../img/card-back-img.png');
            background-position: bottom right;
            background-repeat: no-repeat;
            margin-bottom: 20px;
            padding: 20px 20px 15px;
            min-height: 180px;
            perspective: 100px;
            list-style: none;
            animation: flip-anim 0.4s linear;
            animation-fill-mode: both;
            transform: rotateY(-90deg);
            transition: all 0.2s cubic-bezier(.36, -0.64, .34, 1.76);
            position: relative;
            .attached-url{
                position: absolute;
                left: 0;
                top: 0;
            }
            .icon-share{
                ~ .que-info{
                    margin-right: 30px;
                }
            }
            .que-info{
                position: absolute;
                right: 13px;
                top: 10px;
                font-size: $font-size-h5;
                cursor: pointer;
            }
            &.pinned{
                border: 1px solid $primary;
            }

            &.is-card {
                min-height: auto;
                padding: 0;
                background: transparent;
                margin-bottom: 0;
                cursor: pointer;
                &.dfs-card-akhada{
                    margin-bottom: 20px;
                    .img-shape{
                        width: 100%;
                        min-height: 120px;
                        object-fit: cover;
                    }
                }
            }

            .contest-pin {
                position: absolute;
                top: 0;
                left: 0;

                img {
                    width: 25px;
                }
            }

            .icon-share {
                position: absolute;
                right: 10px;
                top: 5px;
                padding: 6px;
                color: $gray-101;
                cursor: pointer;
                font-size: $font-base + 1;
            }

            .questions {
                color: $primary_text_color;
                font-family: $font-bold;
                margin-bottom: 14px;
                margin-left: 1px;
                line-height: 18px;
                font-size: 15px;
                width: 90%;
                word-wrap: break-word;
                word-break: break-word;
                
                @media (max-width:414px) {
                    width: 85%;
                    font-size: $font-base;
                }
                @media (max-width:320px) {
                    width: 80%;
                }
            }
            .category_name{
                color: $help_text_color;
                font-size: $font-size-small;
                font-family: $font-book;
                margin-bottom: 14px;
                display: inline-block;
            }

            .corrected-ans {
                display: flex;
                z-index: 4;

                span {
                    color: $help_text_color;
                    font-size: $font-size-small;
                    margin-right: 10px;
                }

                i {
                    margin-right: 10px;
                    font-size: $font-size-xsmall;
                    line-height: 16px;
                }
            }

            .estimate-win {
                .icon-info {
                    cursor: pointer;
                    padding: 4px;
                    font-size: $font-size-small;
                }

                .est-price-pool {
                    font-size: $font-size-xsmall;
                    margin-bottom: 12px;
                    color: $help_text_color;
                    font-family: $font-book;

                    .value {
                        color: $primary;
                    }

                    img {
                        width: 11px;
                        margin-right: 2px;
                        margin-top: -1px;
                    }
                }
            }

            .footer-vc {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 16px 1px 0;

                .price-container {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                }

                .my-pre-date {
                    &.won {
                        margin-right: 10px;

                        .price-pool {
                            color: $success;
                        }
                    }
                    .view-proof-section{
                        cursor: pointer;
                        color: $primary;
                        font-size: $font-size-small;
                        margin-bottom: 0;
                        font-family: $font-book;
                    }

                    .price-pool {
                        font-size: $font-size-small;

                        img {
                            width: 13px;
                        }
                    }

                    .date-v {
                        text-align: right;

                        span {
                            width: 100%;
                        }
                    }
                }

                .price-pool {
                    font-family: $font-bold;
                    color: $primary;
                    margin: 0;

                    img {
                        width: 15px;
                        margin: 0 5px 2px;
                    }
                }

                .price-pool-first {
                    color: $help_text_color;
                    margin: 0;
                    font-family: $font-book;
                }

                .date-v {
                    display: flex;
                    &.right-side{
                        display: block;
                        text-align: right;
                        line-height: 14px;
                    }

                    &.new-fc {
                        display: block;

                        .view-part {
                            font-family: $font-book;
                        }
                    }

                    .view-part {
                        cursor: pointer;
                    }

                    .match-timing {
                        font-size: $font-size-small;
                        color: $help_text_color;
                        margin-top: 1px;

                        .countdown {
                            letter-spacing: 1.5px;
                            font-size: $font-size-small;
                            font-family: $font-regular;
                            min-width: 60px;
                        }
                    }

                    .span-round {
                        display: inline-block;
                        width: 4px;
                        height: 4px;
                        border-radius: 100%;
                        overflow: hidden;
                        margin: 0 10px 1px;
                        background: $gray-575;
                    }
                }

                .btn-rounded {
                    padding: 9px 22px 6px;
                    font-size: $font-size-small;

                    &.disabled {
                        pointer-events: none;
                        @include color-opacity($help_text_color, 0.4);
                        background: linear-gradient(90deg, $btn-disable-bg 0%, $btn-disable-bg 60%, $btn-disable-bg 90%);
                    }
                }
            }
        }

    }
    
}

.prediction-bar {
    white-space: nowrap;
    padding: 8px 15px 6px;
    border-radius: $border-radius;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-bottom: 10px;
    cursor: pointer;
    border: 1px solid $secondary_border_color;

    @media (max-width:414px) {
        padding: 6px 15px 4px;
    }

    .filled-bar {
        position: absolute;
        bottom: 0;
        left: 0;
        height: 100%;
        width: 0%;
        z-index: 3;
        background: $white;
        animation: fill-c 1.5s ease-in-out;

        @keyframes fill-c {
            0% {
                width: 0;
            }
        }

        &:after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            height: 100%;
            width: 100%;
            z-index: 2;
            border-radius: 0 $border-radius $border-radius 0;
            overflow: hidden;
            @include background-opacity($app_bg_color, 1);
        }
    }

    &:before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 1;
        border-radius: $border-radius;
        overflow: hidden;
    }

    p {
        margin: 0;
        font-size: $font-size-small;
        color: $help_text_color;
        z-index: 4;

        &.answer {
            position: relative;
            z-index: 5;
            color: $body_text_color;
            font-family: $font-bold;
            max-width: 50%;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    &.selected {
        border: 1px solid $primary;
        transition: before 1s, border 1s;

        .filled-bar {
            &:after {
                @include background-opacity($primary, 0.15);
            }
        }

        p {
            &.answer {
                color: $primary;
            }
        }
    }

    &.success {
        border: 1px solid $success;

        p {
            &.answer {
                color: $body_text_color;
            }
        }

        .filled-bar:after {
            @include background-opacity($success, 0.15);
        }

        .corrected-ans i {
            color: $success;
        }
    }

    &.failure {
        border: 1px solid $danger;

        p {
            &.answer {
                color: $body_text_color;
            }
        }

        .filled-bar:after {
            @include background-opacity($danger, 0.15);
        }

        .corrected-ans i {
            color: $danger;
        }
    }
}

.modal-pred-confirm {
    padding: 0 40px;

    .modal-content {
        border-radius: $border-radius-lg;
        overflow: hidden;
    }

    .modal-body {
        padding: 0;
    }

    .no-coins-msg {
        display: flex;
        padding: 9px 12px 6px;
        background-color: $app_bg_color;
        border-radius: 7px;
        font-size: 11px;
        color: $help_text_color;
        margin-top: 15px;

        a {
            margin-left: 4px;
            color: $primary;
            font-family: $font-bold;
            text-transform: capitalize;
        }
    }

    .container {
        padding: 50px 28px 55px;

        .title-pp {
            color: $primary_text_color;
            font-family: $font-bold;
            font-size: $font-size-h5;
            text-transform: uppercase;
            line-height: 19px;
            text-align: center;
        }

        .desc-pp {
            color: $body_text_color;
            font-family: $font-regular;
            font-size: $font-base;
            text-align: center;
            line-height: 15px;
            margin: 0 auto;
        }

        .your-pre {
            border-radius: $border-radius;
            overflow: hidden;
            padding: 16px 0;
            text-align: center;
            margin: 30px 0 35px;
            background: $app_bg_color;
            position: relative;

            .shimmer-v {
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;

                span {
                    height: 100%;
                    margin: 0;
                }
            }

            span {
                display: block;
                margin: 4px 0;
                font-size: $font-size-small;
                position: relative;

                &.option {
                    font-family: $font-bold;
                    color: $primary;
                    font-size: $font-base;
                }
            }

            .match-timing {
                span {
                    font-size: $font-size-h6;
                    font-family: $font-bold;
                    display: inline;
                }

                .countdown {
                    letter-spacing: 1px;
                }
            }
        }

        .available-bal {
            display: flex;
            justify-content: space-between;
            align-items: center;

            span {
                color: $help_text_color;

                &.bal {
                    color: $primary_text_color;
                }
            }

            img {
                width: 16px;
                margin: 4px 6px;
                position: relative;
                top: -1px;
            }
        }
    }

    .btn-m-p {
        height: 44px;
        width: 100%;
        border-radius: 0;
        background: $primary;
        color: $white;
        font-size: $font-base;

        &:active {
            @include background-opacity($primary, 0.9);
        }

        &.disabled {
            pointer-events: none;
            @include color-opacity($help_text_color, 0.4);
            background: linear-gradient(90deg, $btn-disable-bg 0%, $btn-disable-bg 60%, $btn-disable-bg 90%);
        }
    }

    @media (max-width:414px) {
        padding: 0 30px;

        .title-pp {
            font-size: $font-size-medium !important;
        }

        .desc-pp {
            font-size: $font-base !important;
            line-height: 13px !important;
        }
    }

    @media (max-width:350px) {
        padding: 0 20px;

        .container {
            padding: 40px 20px 45px;
        }
    }
}

.modal-pre-lm {
    overflow: hidden !important;

    .modal-dialog {
        max-width: 100%;
    }

    .modal-body {
        padding: 0 2% !important;
        overflow: auto;
        overflow-x: hidden;

        @media (max-width: 414px) and (max-height: 767px) {
            padding-bottom: 80px !important;
        }

        &.affiliate-prog{
            padding: 0 3% !important;
            .lm-title{
                margin: 17% 0 16px;
            }

            .img-view-c {
                justify-content: space-around;
                img {
                    width: 35%;
                }
            }
            .easy-p{
                white-space: normal !important;
                margin-bottom: 4px !important;
            }
            .details{
                font-size: $font-base;
                line-height: 18px;
            }
        }
    }

    .modal-close {
        position: absolute;
        top: 30px;
        right: 20px;
        left: auto;
    }

    .lm-title {
        display: inline-block;
        width: 100%;
        text-align: center;
        margin: 16% 0 14%;
        font-family: $font-bold;
        font-size: $font-size-h3;
        line-height: 24px;
        color: $primary_text_color;

        @media (max-width:414px) {
            font-size: $font-size-h4;
        }
    }

    .lm-desc{
        color: $help_text_color;
        font-size: $font-base;
        text-align: center;
        line-height: 24px;
        text-transform: none;
        font-family: $font-regular;

        @media (max-width:414px) {
            font-size: $font-size-small;
            line-height: 20px;
        }
    }

    .line-dashed {
        width: 40%;
        display: block;
        margin: auto;

        @media (max-width:414px) {
            width: auto;
        }
    }

    .img-view-c {
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        img {
            width: 32%;
            animation: fadeing-scale 1.5s ease;
            max-width: 150px;

            @keyframes fadeing-scale {
                0% {
                    opacity: 0.5;
                    transform: scale(0.9);
                }

                100% {
                    opacity: 1;
                    transform: scale(1);
                }
            }
        }

        .text-container {
            max-width: 50%;
            text-align: left;

            p {
                margin: 0;
                font-size: $font-size-small;
                line-height: 14px;
                color: $body_text_color;
            }

            .details {
                position: relative;
                top: 0;
                animation: slide-t 1.5s ease;

                @keyframes slide-t {
                    0% {
                        top: 20px;
                        opacity: 0;
                    }

                    100% {
                        top: 0;
                        opacity: 1;
                    }
                }
            }

            .easy-p {
                font-size: $font-base;
                line-height: 18px;
                color: $primary_text_color;
                font-family: $font-bold;
                margin-bottom: 8px;
                white-space: nowrap;
                word-wrap: normal;
                word-break: normal;
                position: relative;


                &.right-s {
                    right: 0;
                    animation: slide-t-r 1.5s ease;

                    @keyframes slide-t-r {
                        0% {
                            right: -100%;
                            opacity: 0;
                        }

                        100% {
                            right: 0;
                            opacity: 1;
                        }
                    }
                }

                &.left-s {
                    left: 0;
                    animation: slide-t-l 1.5s ease;

                    @keyframes slide-t-l {
                        0% {
                            left: -100%;
                            opacity: 0;
                        }

                        100% {
                            left: 0;
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }

    .btn {
        display: block;
        margin: calc(100vh - 90vh) auto 10px;
        width: 80%;
        border-radius: 22px;
    }

}

