                    overflow: hidden;
                    margin: 0 10px 3px;
                    background: $gray-575;
                }
            }

            .btn {
                display: block;
                margin: calc(100vh - 92vh) auto 10%;
                width: 95%;
                border-radius: 22px;
            }

        }
    }
}

.prediction-part-v {
    background: $white !important;
    padding: 0 !important;
    padding-top: 90px !important;
    min-height: 100vh !important;

    &.is-leaderboard {

        .header-v {
            span {
                width: 36% !important;

                &.amount {
                    width: 22% !important;
                }

                &.u-rank {
                    width: 10% !important;
                }
            }
        }

        .list-item {
            span {
                width: 32% !important;

                &.amount {
                    width: 22% !important;
                }

                &.u-rank {
                    width: 10% !important;
                }
            }
        }
    }

    .header-v {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 15px;
        background: $app_bg_color;
        position: fixed;
        top: 56px;
        width: 100%;
        max-width: 550px;
        z-index: 999;

        span {
            color: $help_text_color;
            font-size: $font-size-small;
            line-height: 14px;
            position: relative;
            top: 2px;
            width: 40%;

            &.amount {
                width: 30%;
                left: 2px;
                white-space: nowrap;
            }
        }
    }

    .list-view {
        padding-bottom: 30px;
    }

    .list-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px;
        border-bottom: 1px solid $border_color;

        &.own-v {
            @include background-opacity($primary, 0.08);
        }

        span {
            color: $primary_text_color;
            font-size: $font-base;
            line-height: 18px;
            position: relative;
            top: 3px;
            width: 40%;
            text-overflow: ellipsis;
            overflow: hidden;

            &.shimmer {
                line-height: 12px !important;
                top: 2px;

                span {
                    line-height: 12px !important;
                    top: 2px;
                }
            }

            &.amount {
                line-height: 18px;
                top: 2px;
                font-size: $font-size-small;
                font-family: $font-bold;
                width: 30%;

                .val {
                    min-width: 30px;
                    display: inline-block;
                    text-align: left;
                }

                img {
                    width: 16px;
                    position: relative;
                    margin-right: 4px;
                    top: -1px;
                }
            }
        }
    }
}

.new-data-toast {
    background: linear-gradient(135deg, $app_bg_color 40%, $white 90%);
    position: absolute;
    top: 10px;
    right: 0;
    height: 42px;
    border-radius: 35px 0 0 8px;
    z-index: 9999;
    box-shadow: -1px 0 10px 0 rgba(0, 0, 0, 0.3);
    cursor: pointer;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 14px;
    animation: slide-r 1s ease-in-out;

    @keyframes slide-r {
        0% {
            right: -50%;
        }

        100% {
            right: 0;
        }
    }

    i {
        font-size: 16px;
        margin-left: 1px;
    }

    .text-msg {
        color: $primary;
        margin-left: 8px;
        font-family: $font-book;
    }
}
