.leaderboard-wrapper {
    .refresh-list {
        position: absolute;
        bottom: 30px;
        right: 10px;
        width: 44px;
        height: 44px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $primary-dark;
        border-radius: 100%;
        border: 2px solid $white;
        box-shadow: 0 0 16px 4px rgba(0, 0, 0, 0.1);
        cursor: pointer;
        &:hover {
            @include background-opacity($primary-dark, 0.9);
            transform: scale(0.95);
        }
        &:focus {
            @include background-opacity($primary-dark, 0.9);
            transform: scale(0.95);
        }
        &.rotate {
            animation: rotating 1s linear infinite;
            pointer-events: none;
            @keyframes rotating {
                from {
                    transform: rotate(0deg);
                }
                to {
                    transform: rotate(360deg);
                }
            }
        }
        i {
            color: $white;
            font-weight: bold;
            font-size: 16px;
        }
    }
    .leaderboard-listing {
        padding-bottom: 0;
        background-color: $white;
    }
    .primary-select {
        width: 70%;
        display: inline-block;
        max-width: 120px;
        .select__dropdown-indicator {
            color: $primary;
        }
    }
    .score-card-header {
        overflow: auto;
        border-bottom: 1px solid $app_text_color;
        position: relative;
        background: $white;
        position: fixed;
        top: 56px;
        width: 100%;
        max-width: 550px;
        left: 0;
        .left-right-section {
            flex-direction: row;
            display: flex;
            align-items: center;
            justify-content: space-around;
            flex: 1;
            padding: 5px;
            margin-left: 20px;
            margin-right: 20px;
            .team-left {
                margin-top: 5px;
                text-align: left;
                .display-score-card {
                    flex-direction: row;
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    flex: 1;
                    .contest-details-first-div {
                        font-family: $font-bold;
                        font-size: $font-size-small;
                        line-height: 14px;
                        color: $primary_text_color;
                        @media (max-width: 320px) {
                            font-size: $font-size-xsmall;
                        }
                    }
                    .contest-details-sec-div {
                        margin-left: 10px;
                        font-family: $font-regular;
                        font-size: $font-size-small;
                        @media screen and(max-width:768px) {
                            font-size: $font-size-xsmall;
                        }
                        line-height: 14px;

                        .red-color-class {
                            color: $red;
                            margin-left: 3px;
                        }
                        .gray-color-class {
                            margin-left: 3px;
                            color: $help_text_color;
                        }
                        @media screen and(max-width: 380px) {
                            font-size: $font-size-xsmall;
                        }
                    }
                }
            }
            .team-right {
                margin-top: 5px;
                text-align: right;
                .display-score-card {
                    flex-direction: row;
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;
                    flex: 1;
                    .contest-details-first-div {
                        font-family: $font-bold;
                        font-size: $font-size-small;
                        line-height: 14px;
                        color: $primary_text_color;
                        @media (max-width: 320px) {
                            font-size: $font-size-xsmall;
                        }
                    }
                    .contest-details-sec-div {
                        margin-left: 10px;
                        font-family: $font-regular;
                        font-size: $font-size-small;
                        @media screen and(max-width:768px) {
                            font-size: $font-size-xsmall;
                        }
                        line-height: 14px;

                        .red-color-class {
                            color: $red;
                            margin-left: 3px;
                        }
                        .gray-color-class {
                            margin-left: 3px;
                            color: $help_text_color;
                        }
                        @media screen and(max-width: 380px) {
                            font-size: $font-size-xsmall;
                        }
                    }
                }
            }
        }
        .middle-header-content {
            text-align: center;
            font-family: $font-regular;
            font-size: 12px;
            line-height: 14px;
            padding-bottom: 5px;
            color: $primary_text_color;
            @media (max-width: 320px) {
                font-size: $font-size-xsmall;
            }
        }
    }
    .leaderboard-header {
        overflow: auto;
        position: fixed;
        top: 105px;
        width: 100%;
        max-width: 550px;
        z-index: 1005;
        &.top-completed {
            top: 54px;
        }

        @keyframes animate-down {
            from {
                position: absolute;
                left: 0;
                top: 10px;
                padding-left: 13px;
                padding-right: 13px;
            }
            to {
                position: absolute;
                left: 0;
                top: 30px;
                padding-left: 13px;
                padding-right: 13px;
            }
        }

        @keyframes animate-up {
            from {
                position: absolute;
                left: 0;
                top: 30px;
                padding-left: 13px;
                padding-right: 13px;
            }
            to {
                position: absolute;
                left: 0;
                top: 10px;
                padding-left: 13px;
                padding-right: 13px;
            }
        }

        /* Safari 4.0 - 8.0 */
        @-webkit-keyframes margin-top {
            from {
                position: absolute;
                left: 0;
                top: 10px;
                padding-left: 13px;
                padding-right: 13px;
            }
            to {
                position: absolute;
                left: 0;
                top: 25px;
                padding-left: 13px;
                padding-right: 13px;
            }
        }

        .ranking-list {
            border-radius: 0;
            padding: 0;
            background-color: $app_bg_color;

            &.my-ranking-list {
                border-bottom: 1px solid $white;
                border-radius: 6px;
                .space {
                    height: 2px;
                }
                .display-table-cell {
                    padding: 7px 0;
                    .rank {
                        padding-left: 5px;
                        margin-top: 4px;
                    }
                    .user-name-container {
                        display: block;
                    }
                }
                .leaderboard-grd {
                    position: relative;
                    width: 20%;
                    @media (max-width: 767px) {
                        width: 30%;
                    }
                    a {
                        position: absolute;
                        top: 45%;
                        transform: translateY(-45%);
                        left: 10px;
                        i.icon-ground {
                            color: $primary_text_color;
                            font-size: $font-size-h5;
                        }
                    }
                    @media (max-width: 360px) {
                        a {
                            i.icon-ground {
                                font-size: $font-size-medium;
                            }
                        }
                    }
                }
            }

            &:first-child {
                margin-top: 0;
            }

            .display-table-cell {
                &:first-child {
                    width: 8%;
                }
                &.center-table-cell {
                    padding-left: 50px;
                    position: relative;
                    .user-img {
                        position: absolute;
                        left: 5px;
                        top: 50%;
                        transform: translateY(-50%);
                        .profile-img {
                            width: 64px;
                            height: 64px;
                            border-radius: 32px;
                            object-fit: cover;
                        }
                    }
                }
                &:last-child {
                    padding: 10px 2px;
                }
            }
        }

        overflow: hidden;
        &.collpased-header-list-hide {
            transition: all 0.5s ease-in-out;
            height: 70px;
            padding-left: 13px;
            padding-right: 13px;
            padding-top: 12px;
            background-color: $white;
            position: relative;
            top: 0;
        }

        &.collpased-header-list-show {
            transition: all 0.5s ease-in-out;
            padding-left: 13px;
            padding-right: 13px;
            background-color: $white;
            padding-top: 12px;
            position: relative;
            top: 0;
        }

        .you {
            font-size: 14px;
            color: $primary_text_color;
            font-family: $font-bold;
            vertical-align: middle;
            padding-left: 8px;

            &.show-name {
                display: inherit;
            }

            &.hide-name {
                animation-direction: normal;
            }
        }
        /* Safari 4.0 - 8.0 */
        @-webkit-keyframes move-top {
            from {
                position: absolute;
                left: 15%;
                top: 10px;
                z-index: 1;
            }
            to {
                position: absolute;
                left: 13px;
                top: 9px;
                z-index: 1;
            }
        }
        @keyframes move-top {
            from {
                position: absolute;
                left: 15%;
                top: 10px;
                z-index: 1;
            }
            to {
                position: absolute;
                left: 13px;
                top: 9px;
                z-index: 1;
            }
        }

        /* Safari 4.0 - 8.0 */
        @-webkit-keyframes move-bottom {
            from {
                position: absolute;
                left: 13px;
                top: 9px;
                z-index: 1;
            }
            to {
                position: absolute;
                left: 15%;
                top: 10px;
                z-index: 1;
            }
        }
        @keyframes move-bottom {
            from {
                position: absolute;
                left: 13px;
                top: 9px;
                z-index: 1;
            }
            to {
                position: absolute;
                left: 15%;
                top: 10px;
                z-index: 1;
            }
        }
    }

    .header-arrow-container {
        width: 100%;
        height: 30px;
        background-color: $app_bg_color;

        .header-arrow-top {
            height: 13px;
            background-color: $white;
            border-bottom: 1px solid $border_color;
        }

        .header-arrow {
            background-color: $app_bg_color;
            float: right;
            margin-right: 15px;
            position: relative;
            &:after {
                content: "";
                background: $white;
                width: 77%;
                height: 1px;
                position: absolute;
                top: 0;
                left: 2px;
            }

            #pentagon {
                width: 54px;
                position: relative;
                border-width: 20px 18px 0;
                border-style: solid;
                border-color: $border_color transparent;
                margin-right: 10px;
                cursor: pointer;
                &:before {
                    content: "";
                    width: 54px;
                    position: absolute;
                    border-width: 20px 18px 0;
                    border-style: solid;
                    border-color: $white transparent;
                    margin-right: 10px;
                    cursor: pointer;
                    top: -21px;
                    left: -18px;
                }
            }
            div {
                position: relative;
                i {
                    position: absolute;
                    top: -17px;
                    left: 2px;
                    color: $primary_text_color;
                }
            }
        }
    }
    .ranking-list {
        background: $white;
        padding: 10px 15px 5px 12px;
        display: table;
        width: 100%;
        border-bottom: 1px solid $border_color;
        &.sel-active {
            @include background-opacity($primary, 0.15);
        }
        .display-table-cell {
            font-family: $font-regular;
            vertical-align: middle;
            .rank {
                color: $primary;
                font-size: 19px;
                font-family: $font-bold;
                line-height: 1.1;
                &.you-rank {
                    margin-left: 20px;
                }
            }
            .points {
                font-size: 12px;
                font-family: $font-bold;
                text-align: right;
                color: $primary_text_color;
                margin-right: 10px;
            }
            .rank-heading,
            .points-heading {
                font-size: 11px;
                color: $help_text_color;
                line-height: 1.1;
            }
            .points-heading {
                text-align: right;
            }
            .user-img {
                width: 40px;
                height: 40px;
                border-radius: 100%;
                display: inline-block;
                margin: 0;
                margin-right: 10px;
                overflow: hidden;
                @media (max-width: 320px) {
                    margin-right: 8px;
                }
                img {
                    width: 100%;
                    height: 100%;
                }
                &.shimmer {
                    vertical-align: middle;
                    margin: 0 0 1rem;
                }
            }
            .user-team-name {
                font-size: 12px;
                color: $help_text_color;
                font-family: $font-regular;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                vertical-align: middle;
                color: $help_text_color;
                line-height: 1.5;
                margin-top: 3px;
                font-weight: bold;
                .won-amount {
                    color: $primary;
                    line-height: 15px;
                    font-size: 12px;
                    font-weight: bold;
                    i.icon-bonus {
                        position: relative;
                        top: -1px;
                    }
                    .won {
                        color: $help_text_color;
                    }
                }
            }

            .user-name-container {
                display: inline-block;
                padding-left: 7px;

                &.shimmer {
                    width: 80%;
                    vertical-align: middle;

                    @media screen and(max-width: 430px) {
                        width: 72%;
                    }
                }

                @media screen and(max-width: 453px) {
                    width: 72%;
                }
            }
            .user-name {
                font-size: 14px;
                color: $primary_text_color;
                font-family: $font-bold;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                vertical-align: middle;
                &.user-name-mini-league-leaderboard {
                    font-size: 14px;
                    color: $primary_text_color;
                    font-family: $font-regular;
                }

                &.display-none {
                    display: none;
                }
            }
            &:first-child {
                width: 8%;
            }
            &:last-child {
                width: 20%;
            }

            .list-header-text {
                font-family: $font-regular;
                color: $primary_text_color;
                font-size: 12px;
                font-weight: bold;
                line-height: 15px;
                text-align: center;
                vertical-align: middle;
                text-transform: capitalize;
                &.list-heder-mini-league {
                    font-family: $font-book;
                    color: $help_text_color;
                    font-size: 12px;
                }

                &.left {
                    text-align: left;
                }
            }
        }
        &.user-list-header {
            height: 34px;
            background-color: #eaeaea;
            padding: 0 10px;
            &.mini-leage-leaderbord {
                background-color: #f2f2f2;
            }
            // margin-top: 15px;
        }
        &.shimmer {
            width: 100%;
        }
    }
}

.bottom-download-container {
    width: 100%;
    background-color: $primary;
    box-shadow: 0 -8px 16px 0 rgba(0, 0, 0, 0.1);
    text-align: center;
    position: fixed;
    bottom: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
    max-width: $window_width;

    span {
        color: $white;
        font-family: $font-bold;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        text-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.4);
    }
    i {
        color: $white;
        margin-right: 5px;
        font-weight: bold;
    }
}

.pointer-cursor {
    cursor: pointer;
}

.p-height {
    //height:calc(100vh - 220px);
    height: calc(100vh - 274px);
    overflow: hidden;
    overflow-y: auto;
    margin-top: 254px;
    transition: margin 600ms;
    padding-bottom: 30px;
    &.completed{
        margin-top: 203px;
  
    }
    &.user-rank-unavilable {
        margin-top: 145px;
    }
    &.user-rank-available {
        margin-top: 194px;
    }
    &.expand {
        margin-top: 280px;
        //height: calc(100vh - 320px);
        height: calc(100vh - 330px);
    }
    &.expand-completed{
        margin-top: 233px;
        //height: calc(100vh - 320px);
        height: calc(100vh - 300px);
    }
    &.no-arrow {
        margin-top: 224px;
    }
    &.no-arrow-completed {
        margin-top: 172px;
    }
}
.spacing-wrapper {
    height: 12px;
    width: 100%;
    background-color: $white;
    &.grey-clr {
        background-color: $app_bg_color;
    }
}
.leaderboard-rank {
    background-color: white;
    width: 100%;
    display: flex;
    padding: 1px 5px;
    align-items: center;
    &.margin-league-details {
        margin-top: 26px;
    }
    .no-margin {
        margin-top: 2px;
    }

    .hall-of-fame-img {
        width: 60px;
        height: 60px;
    }
    .text_hall_of_fame {
        text-align: right;
        align-self: center;
        margin-right: 3%;
        font-family: $font-regular;
        font-size: 12px;
        font-weight: 300;
        letter-spacing: 0;
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
    }
}
