.lineup-header-action {
    margin-top: 56px;
    &.with-modal {
        ul {
            li {
                &:last-child {
                    border-right: 1px solid $gray-500;
                }
            }
        }
    }
    box-shadow: 0 16px 16px 0 rgba(0, 0, 0, 0.05);
    background-color: $white;
    border-top: 1px solid $gray-500;
    position: relative;
    overflow: hidden;
    ul {
        float: left;
        margin: 0;
        padding: 0;
        li {
            display: inline-block;
            border-right: 1px solid $gray-500;
            padding: 10px 15px;
            a {
                display: block;
                color: $gray-450;
                &.active {
                    color: $primary;
                }
                i {
                    font-size: $font-size-h5;
                }
            }
            h4 {
                display: inline-block;
                margin: 0 5px 0 0;
                color: $primary_text_color;
                font-size: $font-base;
                font-family: $font-bold;
            }
            span {
                color: $help_text_color;
                font-size: $font-size-small;
                font-family: $font-bold;
            }
            &:last-child {
                border-right: 0;
            }
            &:nth-child(2) {
                border-right: 1px solid $gray-500;
            }
        }
    }
    .lineup-header-action-right {
        float: right;
        li {
            border: none;
            color: $gray-550;
            font-family: $font-italic;
            b {
                color: $body_text_color;
                font-size: $font-size-small;
            }
        }
    }
}
.field-view-cross-button-container {
    z-index: 1;
    position: relative;
    .brand-logo {
        position: absolute;
        top: 5px;
        max-height: 70px;
        left: 50%;
        opacity: 0.5;
        transform: translate(-50%);
        @media (max-width: 1366px) and (max-height: 600px) {
            top: 20px;
        }
    }
    .developed-by-container {
        position: absolute;
        left: 50%;
        -webkit-transform: translate(-50%);
        transform: translate(-50%);
        bottom: 20px;
    }
    span {
        position: absolute;
        z-index: 1;
        top: 20px;
        left: 15px;
        color: $white;
        font-family: $font-bold;
        font-size: $font-base;
        line-height: 18px;
        &.username-text {
            margin-left: 5px;
            position: relative;
            top: auto;
            left: auto;
        }
    }
    i {
        position: absolute;
        top: 20px;
        right: 20px;
        z-index: 1;
        font-size: $font-base;
        color: white;
        cursor: pointer;

        &.edit {
            right: 60px;
        }
    }
}
.field-view-container {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 0px);
    @media screen and(max-height:420px) {
        font-size: $font-size-xsmall;
    }
    &.cricket-ground-container {
        background-image: url("../img/ground-cricket.svg");
    }
    &.badminton-ground-container {
        background-image: url("../img/ground-badminton.png");
    }
    &.soccer-ground-container {
        background-image: url("../img/ground-soccer.svg");
    }
    &.baseball-ground-container {
        background-image: url("../img/ground-baseball.png");
    }
    &.kabaddi-ground-container {
        background-image: url("../img/ground-kabaddi.svg");
    }
    &.basketball-ground-container {
        background-image: url("../img/basketball-ground.png");
        background-size: 100% 100%;
    }
    &.football-ground-container {
        background-image: url("../img/football-ground.png");
        background-size: 100% 100%;
        @media (max-width: 1366px) and (max-height: 800px) {
            .player-area {
                .space-evenly-container {
                    .player-position-header {
                        padding: 12px;
                    }
                }
            }
        }
        @media (max-width: 1366px) and (max-height: 750px) {
            .player-area {
                .space-evenly-container {
                    .player-position-header {
                        padding: 10px;
                        font-size: 9px;
                        line-height: 1.1;
                    }
                    .player-position-row {
                        .player-row-container {
                            img {
                                width: 30px;
                                height: 30px;
                            }
                            .player-name {
                                font-size: 10px;
                                line-height: 12px;
                                padding: 2px 4px 0;
                            }
                            .player-postion {
                                font-size: 11px;
                            }
                        }
                    }
                }
            }
        }
        @media (max-width: 1366px) and (max-height: 600px) {
            .player-area {
                .space-evenly-container {
                    padding: 10px 0 0;
                }
            }
        }
        @media (max-width: 320px) {
            .player-area {
                .space-evenly-container {
                    .player-position-header {
                        padding: 7px 0 0;
                    }
                }
            }
        }
    }
    .player-area {
        position: relative;
        height: 100%;
        overflow-y: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        @media (max-height: 700px) {
            .space-evenly-container {
                min-height: 80% !important;
                justify-content: space-evenly !important;
            }
            .guru-container {
                min-height: 90% !important;
                justify-content: space-evenly !important;
            }
        }
        .space-evenly-container {
            min-height: 85%;
            width: 100%;
            display: -webkit-flex;
            display: flex;
            flex-direction: column;
            -webkit-box-pack: justify;
            -moz-box-pack: justify;
            -ms-flex-pack: justify;
            -webkit-justify-content: space-between;
            justify-content: space-between;
            text-align: center;
            &.guru-container {
                min-height: 90%;
                width: 95%;
            }
            .guru-asset-container {
                margin-top: -10px;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                .first-conatiner {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    .start-over-layout {
                        cursor: pointer;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        align-items: center;
                        max-width: 47px;
                        display: block;
                        margin-top: 5px;
                        .icon-image-back {
                            width: 20px;
                            height: 20px;
                            margin-top: -10px;
                        }
                        .start-over {
                            color: $white;
                            font-family: $font-regular;
                            font-size: 11px;
                            letter-spacing: 0;
                            line-height: 12px;
                            text-align: center;
                            max-width: 47px;
                            display: block;
                            margin-top: 5px;
                        }
                    }
                    .salary-remaing-conatiner {
                        margin-left: 20px;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        .salary-budget {
                            color: $white;
                            font-family: $font-bold;
                            font-size: 20px;
                            font-weight: bold;
                            letter-spacing: 0;
                            line-height: 24px;
                            text-align: center;
                        }
                        .salary-remaining {
                            color: $white;
                            font-family: $font-regular;
                            font-size: 11px;
                            letter-spacing: 0;
                            line-height: 12px;
                            text-align: center;
                            max-width: 47px;
                            display: block;
                            margin-top: 5px;
                        }
                    }
                }
                .second-conatiner {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    .start-over-layout {
                        cursor: pointer;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        .icon-image {
                            color: $white;
                            font-size: 22px;
                            margin-top: -5px;
                        }
                        .start-over {
                            color: $white;
                            font-family: $font-regular;
                            font-size: 11px;
                            letter-spacing: 0;
                            line-height: 12px;
                            text-align: center;
                            max-width: 47px;
                            display: block;
                            margin-top: 5px;
                        }
                    }
                    .exclude-lock-conatiner {
                        margin-left: 20px;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        .lock-exlude-layout {
                            padding: 5px;
                            margin-top: -10px;
                            display: flex;
                            flex-direction: row;
                            justify-content: center;
                            height: auto;
                            width: auto;
                            border-radius: 17px;
                            background-color: rgba(0, 0, 0, 0.1);
                            align-items: center;
                            .exclude-minus {
                                cursor: pointer;
                                height: 24px;
                                width: 24px;
                                border-radius: 13px;
                                align-items: center;
                                margin-right: 7px;
                                display: flex;
                                justify-content: center;
                                &.enable {
                                    background-color: $pl-primary;
                                }
                                .img {
                                    align-items: center;
                                    height: auto;
                                    width: auto;
                                    transform: rotate(90deg);
                                }
                            }
                            .lock {
                                cursor: pointer;
                                height: 24px;
                                width: 24px;
                                border-radius: 13px;
                                align-items: center;
                                margin-right: 7px;
                                display: flex;
                                justify-content: center;
                                &.enable {
                                    background-color: $pl-primary;
                                }
                                .ic-lock {
                                    color: $white;
                                    line-height: 0;
                                }
                            }
                        }
                        .salary-remaining {
                            margin-top: 5px;
                            color: $white;
                            font-family: $font-regular;
                            font-size: 11px;
                            letter-spacing: 0;
                            line-height: 12px;
                            text-align: center;
                            max-width: 47px;
                            margin-left: 10px;
                            display: block;
                            margin-top: 5px;
                        }
                    }
                }
            }
            .more-team-container {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                background-color: $white;
                margin-left: 45px;
                margin-right: 45px;
                height: 42px;
                .logo-conatiner {
                    margin-right: 10px;
                    height: 26px;
                    width: 26px;
                    border-radius: 8px;
                    background-color: #efefef;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    .icon-img {
                        height: 16px;
                        width: 19px;
                    }
                }
                .more-team {
                    display: flex;
                    flex-direction: row;
                    margin-top: 4px;
                    .want-more-than {
                        color: #212121;
                        font-family: $font-regular;
                        font-size: 12px;
                    }
                    .click-here {
                        cursor: pointer;
                        margin-left: 5px;
                        color: $pl-primary;
                        font-family: $font-bold;
                        font-size: 12px;
                        font-weight: bold;
                    }
                }
            }
            .player-position-header {
                opacity: 0.8;
                color: $white;
                line-height: 13px;
                padding: 16px;
                font-family: $font-bold;
                font-size: $font-size-xsmall;
                text-transform: uppercase;
                text-align: center;
                &.baseball-filedview {
                    padding: 2px;
                    line-height: 8px;
                }
                @media (max-width: 1366px) and (max-height: 650px) {
                    padding: 10px;
                }
                @media (max-width: 1366px) and (max-height: 600px) {
                    padding: 5px;
                }
                @media (max-width: 380px) and (max-height: 800px) {
                    padding: 12px;
                }
                @media (max-width: 320px) {
                    padding: 5px;
                }
            }
            .player-position-row {
                display: -webkit-flex;
                display: flex;
                flex-direction: row;
                -webkit-box-pack: justify;
                -moz-box-pack: justify;
                -ms-flex-pack: justify;
                -webkit-justify-content: space-around;
                justify-content: space-around;
                .player-row-container {
                    display: inline-block;
                    text-align: center;
                    position: relative;
                    .playing_indicator {
                        position: absolute;
                        right: 5%;
                        z-index: 2;
                    }
                    .player-name {
                        border-radius: 4px;
                        padding: 2px 4px;
                        @include background-opacity($black, 0.6);
                        color: $white;
                        font-family: $font-regular;
                        font-size: $font-size-small;
                        font-weight: 900;
                        letter-spacing: -0.08px;
                        line-height: 16px;
                        text-align: center;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        max-width: 85px;
                        margin: 0 auto;
                        @media (max-width: 420px) {
                            max-width: 70px;
                            font-size: $font-size-xsmall;
                        }
                        @media (max-width: 320px) {
                            max-width: 55px;
                            font-size: $font-size-xsmall;
                            line-height: 13px;
                        }
                        &.home-team {
                            background-color: $black;
                            color: $white;
                        }
                        &.away-team {
                            background-color: $white;
                            color: $black;
                        }
                    }
                    .player-postion {
                        color: $white;
                        padding: 2px;
                        text-align: center;
                        font-size: $font-base;
                        line-height: 1.1;
                        font-family: $font-bold;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        @media (max-width: 420px) {
                            font-size: $font-size-small;
                        }
                        @media (max-width: 320px) {
                            font-size: $font-size-xsmall;
                        }
                    }
                    img {
                        width: 38px;
                        height: 38px;
                    }
                    .remove-icon {
                        cursor: pointer;
                        width: 15px;
                        height: 15px;
                        display: inline-block;
                        position: absolute;
                        top: -8px;
                        left: -8px;
                        transform: rotate(90deg);
                    }
                    .lock {
                        cursor: pointer;
                        border-radius: 13px;
                        align-items: center;
                        justify-content: center;
                        width: 15px;
                        height: 15px;
                        display: flex;
                        position: absolute;
                        top: -8px;
                        left: -8px;
                        width: 24px;
                        height: 24px;
                        &.enable {
                            background-color: $pl-primary;
                        }
                        .ic-lock {
                            color: $white;
                            line-height: 0;
                        }
                    }
                    a.btn-remove-player {
                        img {
                            width: 14px;
                            height: 14px;
                            transform: rotate(90deg);
                        }
                    }
                }
            }
        }
        .space-evenly-container::before,
        .space-evenly-container::after {
            content: "";
        }
    }
}
.field-view {
    position: relative;
    &.soccer-ground {
        background-image: url("../img/ground-soccer.svg");
        background-repeat: no-repeat;
        background-size: cover;
        height: 100vh;
        padding-top: 60px;
        background-position: top center;
        @include media-breakpoint-down(sm) {
            background-position: center center;
        }
        .player-field-list {
            margin: 10% auto;
            @media screen and (max-width: 414px) {
                margin: 7% auto;
            }
            @media screen and (max-width: 360px) {
                margin: 2% auto;
            }
            @media screen and (max-width: 320px) {
                margin: -2% auto;
            }
        }
        .player-position-header {
            opacity: 0.7;
            color: $white;
            font-family: $font-bold;
            font-size: $font-size-h6;
            line-height: 13px;
            text-align: center;
            text-transform: uppercase;
            padding: 16px;
        }
    }
    &.kabaddi-ground {
        background-image: url("../img/ground-kabaddi.svg");
        background-repeat: no-repeat;
        background-size: cover;
        height: 780px;
        margin-top: 60px;
        padding-top: 80px;
        background-position: top center;
        @include media-breakpoint-down(sm) {
            height: 530px;
            padding-top: 30px;
            margin-top: 30px;
            background-position: center center;
        }
    }
    &.badminton-ground {
        background-image: url("../img/ground-badminton.png");
        background-repeat: no-repeat;
        background-size: cover;
        height: 100vh;
        padding-top: 60px;
        background-position: top center;
        @include media-breakpoint-down(sm) {
            background-position: center center;
        }
        .player-field-list {
            &.list-hide {
                visibility: hidden;
                opacity: 0;
                display: none !important;
            }
            .player-name {
                max-width: 100%;
            }
            .player-postion {
                max-width: 100%;
            }
            @include media-breakpoint-down(sm) {
                .player-name {
                    max-width: 100px;
                }
                .player-postion {
                    max-width: 100px;
                }
            }
        }
    }
    &.cricket-ground {
        background-image: url("../img/ground-cricket.svg");
        background-repeat: no-repeat;
        background-size: cover;
        height: 100vh;
        padding-top: 60px;
        background-position: center;
        @include media-breakpoint-down(sm) {
            background-position: center center;
        }
        .player-field-list {
            margin: 5% auto;

            @media screen and (max-width: 414px) {
                margin: 7% auto;
            }
            @media screen and (max-width: 360px) {
                margin: 2% auto;
            }
            @media screen and (max-width: 320px) {
                margin: -2% auto;
            }
        }
        .player-position-header {
            opacity: 0.7;
            color: $white;
            line-height: 13px;
            padding: 16px;
            font-family: $font-bold;
            font-size: $font-size-h6;
            text-transform: uppercase;
            font-family: $font-regular;
            &.guru-line-height {
                line-height: 5px;
            }
        }
    }
    .player-field-list {
        display: table;
        margin: 5px auto;
        padding: 0;
        width: 100%;
        text-align: center;
        min-height: 84px;
        @include media-breakpoint-up(sm) {
            margin: 30px auto;
        }
        li {
            &.remove {
                div {
                    min-height: 78px;
                    display: table-cell;
                    width: 100%;
                    padding: 0 25px;
                }
            }
            display: inline-block;
            text-align: center;
            position: relative;
            margin: 0 15px;
            img {
                width: 42px;
                height: 42px;
            }
            @include media-breakpoint-down(sm) {
                margin: 0 4px;
                img {
                    width: 40px;
                    height: 40px;
                }
            }
            a.btn-remove-player {
                img {
                    width: 14px;
                    height: 14px;
                    transform: rotate(90deg);
                }
            }
            @media (max-width: 320px) {
                margin: 0 4px;
            }
        }
    }
    .player-name {
        @include background-opacity($black, 0.6);
        color: $white;
        padding: 2px 4px;
        border-radius: 4px;
        font-size: $font-size-small;
        font-family: $font-bold;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        max-width: 72px;
        @media (max-width: 420px) {
            max-width: 60px;
        }
        @media (max-width: 320px) {
            max-width: 54px;
        }
    }
    .player-postion {
        color: $white;
        padding: 2px;
        border-radius: 0 0 4px 4px;
        font-size: 9px;
        line-height: 1.1;
        font-weight: 800;
        font-family: $font-bold;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        @media (max-width: 320px) {
            max-width: 56px;
        }
        @media (max-width: 767px) {
            max-width: 72px;
        }
    }
}
.btn-remove-player {
    height: 12px;
    width: 12px;
    border-radius: 3px;
    display: block;
    position: absolute;
    top: -5px;
    right: -3px;
    i {
        color: $red;
        font-size: 8px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-weight: bold;
    }
}
@include media-breakpoint-down(sm) {
    .field-view {
        .player-postion {
            font-size: 8px;
        }
    }
}
.captain-player {
    background-color: $primary;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    color: $white !important;
    font-family: $font-regular;
    font-weight: bold;
    font-size: $font-size-h6;
    display: inline-block;
    text-align: center;
    position: absolute;
    top: -18px;
    right: 0px;
    line-height: 20px;
}
.vcaptain-player {
    background-color: $primary;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    color: $white !important;
    font-family: $font-regular;
    font-weight: bold;
    font-size: $font-size-h6;
    display: inline-block;
    text-align: center;
    padding: 2px 0;
    position: absolute;
    top: -18px;
    right: -5px;
}

html[dir="rtl"] {
    .lineup-header-action {
        ul {
            float: right;
        }
    }
}
