.contest-entry{
    float: right;
    font-family: $font-bold-italic;
    font-size: $font-size-small;        
    span{
        color: $gray-550;
    }
    p{
        margin: 0;
        color:$link;
    }
}
.contest-list-wrapper{
    padding-left: 15px;
    padding-right: 15px;
    .contest-list{
        &.pinned{
            margin-bottom: 20px;
        }
    }
}
.contest-list{
    border-radius: 14px;
    background: $white;
    margin-bottom: 20px;
    border: 1px solid $white;
    &.pinned{
        border: 1px solid $primary;
        position: relative;
        .contest-heading{
            padding-left: 0;
            .prize-pool-text{
                font-size: $font-size-medium;
                color: $help_text_color;
                font-family: $font-bold;
            }
            .prize-pool-value{
                color: $primary;
                font-family: $font-bold;
            }
        }
    }
     .shimmer-container{
        padding: 17px 15px;
        position: relative;

        .shimmer-top-view{
            width: 100%;

            .shimmer-image{
                width: 30px;
                height: 30px;
                float: right;
                margin: -5px 0;
                &.predict{
                    position: absolute;
                    right: 10px;
                    top: 10px;
                    width: 24px;
                    height: 24px;
                }
            }

            .shimmer-line{
                width: 85%;
                margin-top: 4px;
                float: left;
                line-height: 17px;

                @media screen and(max-width: 620px){
                    width: 83%;                    
                }
                @media screen and(max-width: 411px){
                    width: 77%;                    
                }
                @media screen and(max-width: 320px){
                    width: 68%;                    
                }
                &.predict{
                    width: 100%;
                    span
                    span{
                        border-radius: 8px;
                        margin-bottom: 6px;
                    }
                }
            }
        }
        .shimmer-bottom-view{
            width: 100%;
            margin-top: 70px;
            line-height: 15px;
            display: flex;
            justify-content: space-between;
            .shimmer-buttin{
                width: 20%;
                height: 30px;
                border-radius: 15px;
                overflow: hidden;
                margin-top: -2px;
                &.predict{
                    width: 30%;
                    margin-top: -4px;
                }
            }
        }
    }

    .contest-list-header{
        padding: 10px 15px;
        padding-top: 24px;
        cursor: pointer;
        &.disabled-contest-card{
            pointer-events: none;
            opacity: 0.5;
            cursor: not-allowed;
        }
    }
    .contest-heading{
        position: relative;
        margin-bottom: 8px;
        .img-coin-contest{
            margin-top: 2px;
            margin-left: 35px;                 
            width: 14px;
            height: 14px;
           
        }
        
        img{
            width: auto;
            height: auto;
            max-height: 35px;
            max-width: 35px;
            position: absolute;
            left: 0;
        }
        h3{
            font-size: $font-size-medium;
            font-family: $font-bold-italic;
            color: $gray-200;
            margin: 0;
            span{ 
                .prize-pool-text{
                    font-size: $font-size-medium;
                    color: $help_text_color;
                    font-family: $font-bold;
                }
                .prize-pool-value{
                    color: $primary;
                    font-family: $font-bold;
                }
                &.currency-span{
                    margin-left: 0;
                    margin-right: 4px;
                }
            }
            i{
                margin-left: 10px;
                display: inline-block;
                position: absolute;
                right: 20px;
                top: -16px;
                &.icon-share{
                    right: -4px;
                }
                &.icon-bonus{
                    position: relative;
                    top: -2px;
                    right: 0;
                    margin-left: 0px;
                }
            }
            &.win-type{
                i{
                    &:not(.icon-share){
                        position: static;
                    }
                }

                .prize-pool-text{
                    font-size: $font-size-medium;
                    color: $primary;
                    font-family: $font-bold;
                }
                .img-coin{
                    margin-top: -1px;
                    margin-right: 3px;                 
                    width: 14px;
                    height: 14px;
                    position: relative;
                   
                }
                .img-bonus{
                    margin-left: 32px;
                    width: 14px;
                    height: 14px;
                   
                }
                span{
                    color: $primary;
                    font-family: $font-bold;

                    i.icon-bonus{
                        position: relative;
                        top: -2px;
                        right: 0;
                        margin-left: 4px;
                    }
                    &.currency-span{
                        margin-left: 4px;
                        margin-right: 4px;
                    }
                    &.contest-prizes{
                        padding: 0;
                    }
                }
            }
        }
        .contest-pin{
            position: absolute;
            top: -25px;
            left: -16px;

            i{
                border-radius: 90px;
                background: $primary;
                color: $white;
                font-size: 22px;
            }
        }
        
    }
    .contest-name{
        font-size: $font-size-small;
        color: $primary_text_color;
        font-family: $font-bold-italic;
        margin-bottom: 5px;
    }
   
    .progress-bar-default {
        width: 75%;
    }
    .entry-criteria{
        text-align: right;
        .btn{
            font-family: $font-bold;
            width: 90px;
            padding: 7px;
            border-radius: 16px;
            font-size: $font-size-small;
            span{
                font-size: $font-size-medium;
                position: relative;
                top: 2px;
                left: -4px;
            }
            img.img-coin{
                max-width: 13px;
                margin-right: 2px;
            }
        }
    }
    .contest-list-footer{
        border-top: 1px solid $border-light;
        text-align: center;
        font-size: $font-size-small;
        padding: 5px;
        font-family: $font-bold-italic;
        color: $gray-550;
    }
    &.alreay-joined{
        background: $gray-75;
        .contest-list-footer{
            margin-bottom: 0;
        }
    }
}
.help-section{
    padding: 0 15px;
    text-align: center;
    a{
        display: inline-block;
        text-decoration: underline;
        background: transparent;
        padding: 6px 5px;
        font-family: $font-bold;
        font-size: $font-size-small;
        color: $primary_text_color;
        text-transform: uppercase ;
        text-align: center;
    }
}
.league-list-all{
    border-bottom: 1px solid #EAEAEA;
    margin-bottom: 10px;
    &.no-margin{
        margin-bottom: none;

    }
 .sort-contest-wrapper{
        margin-left: 15px;
        margin-right: 15px;
        &.less-margin{
            margin-left: 5px;
            margin-right: 5px;
        }
    }
}

.sort-contest-section{
    font-size: $font-size-small;
    font-family: $font-bold;
    color: $primary_text_color;
    padding: 6px 16px;
    background: $white;
    border-radius: 7px;
    position: relative;
    cursor: pointer;
    p{
        color: $help_text_color;
        text-transform: none;
        font-size: $font-size-h6;
        font-family: $font-regular;
        margin-bottom: 0;
    }
    .verity-count {
        background: $primary;
        color: $white;
        font-size: $font-size-small;
        font-family: $font-bold;
        font-weight: bold;
        text-align: center;
        padding: 4px 13px;
        border-radius: 20px;
        position: absolute;
        top: 50%;
        right: 36px;
        transform: translateY(-50%);
        line-height: 18px;
    }
    i{
        position: absolute;
        top: 50%;
        right: 16px;
        transform: translateY(-50%);
        color: $gray-550;
        font-size: $font-size-small;
        cursor: pointer;
        &.icon-next-arrow{
            transform: translateY(-50%) rotate(90deg);
        }
    }
}

.contest-list-wrapper{
    padding-left: 15px;
    padding-right: 15px;
    .contest-listing-card{
        padding: 0;
        .contest-listing-card-header {
            position: relative;
            text-align: left;
            color: $help_text_color;
            padding: 10px 0 10px 62px;
            border-radius: 14px 14px 0 0;
            font-size: $font-size-xsmall;
            font-family: $font-regular;
            margin-left: 10px;
            margin-right: 10px;
            background: $disabled_text_color;
            img.contest-img {
                position: absolute;
                left: 15px;
                width: 32px;
                height: 32px;
                top: 50%;
                transform: translateY(-50%);
            }
            .contest-name-heading {
                color: $primary_text_color;
                font-size: $font-size-small;
                margin: 0;
                font-family: $font-bold;
            }
            .contest-name-heading-description{
                font-family:  $font-regular;
                color: $help_text_color;
                font-size: $font-size-h6;
            }
        }
        .contest-list {
            position: relative;
            z-index: 0;
            .contest-heading{
                padding-left: 0;
            }
        }
        .show-more-contest{
            cursor: pointer;
            border-radius: 0 0 14px 14px;
            background: $white;
            background: $disabled_text_color;
            margin-top: -30px;
            padding: 20px 0 11px;
            font-size: $font-size-small;
            font-family: $font-regular;
            font-weight: bold;
            color: $primary;
            i{
                color: $help_text_color;
                display: inline-block;
                margin-left: 5px;
            }
        }        
    }
    &.sorted-contest-listing{
        .contest-list .contest-heading{
            padding-left: 0;
        }
    }
    &:last-child{
        margin-bottom: 30px !important;
    }
}
.contest-heading{
    i.contest-type{
        margin-left: 10px;
        display: inline-block;
        position: absolute;
        right: 20px;
        top: -16px;
        font-size: $font-size-medium;
        ~ i.contest-type{
           right: 40px;
           ~ i.contest-type{
            right: 60px;
         }
        }
    }
}
.contest-list{
    .contest-heading{
        .contest-type-img{
            max-width: 16px;
            max-height: 16px;
            position: absolute;
            right: 20px;
            top: -16px;
            left: auto;
            ~ .contest-type-img{
                right: 40px;
                ~ .contest-type-img{
                right: 60px;
            }
            }
        }
    }
}
.contest-card-body-header{
    .share-contest{
        ~ i.contest-type,~ .featured-image{
            margin-left: 10px;
            display: inline-block;
            position: absolute;
            font-size: $font-size-medium;
            top: 13px;
            right: 35px;
            ~ i.contest-type,~ .featured-image{
                right: 55px;
                ~ i.contest-type,~ .featured-image{
                 right: 75px;
              }
            }
        }
    }
    i.contest-type{
        margin-left: 10px;
        display: inline-block;
        position: absolute;
        font-size: $font-size-medium;
        top: 13px;
        right: 12px;
        ~ i.contest-type{ 
            right: 32px;
            ~ i.contest-type{
             right: 52px;
          }
        }
    }
    i.icon-share{
        top: 10px;
        right: -4px;
    }
}
.contest-collection-slider{
    position: relative;
    background: $white;
    border-radius: 0;
    .collection-list{
        .display-table-cell{
            @media (max-width: 767px){
                .team-img{
                    max-width: 25px;
                    max-height: 25px;
                }                
            }
        }
    }
    position: relative;
    .slick-arrow.slick-next, .slick-arrow.slick-prev{
        display: none !important;
    }

}
.contest-collection-slider-roster{
    position: relative;
    background: transparent;
    border-radius: 0;
    .collection-list{
        width: 70%;
        .display-table-cell{
            @media (max-width: 767px){
                .team-img{
                    max-width: 25px;
                    max-height: 25px;
                }                
            }
        }
    }
    position: relative;
    .slick-arrow.slick-next, .slick-arrow.slick-prev{
        display: none !important;
    }
    
}
.collection-info-section{
    cursor: pointer;
    background: $primary;
    height: 25px;
    text-align: center;
    color: $white;
    font-size: $font-size-small;
    text-transform: uppercase;
    padding: 2px;
    font-family: $font-bold;
    letter-spacing: 1px;
    span{
        position: relative;
        top: 3px;
    }
    i{
        position: relative;
        top: 4px;
        display: inline-block;
        margin-left: 5px;
        vertical-align: top;
        font-size: 13px;
    }
    img{
        transform: rotate(90deg);
        display: inline-block;
        transform-origin: 20px 23px;
    }
}
.contest-listing-web-conatiner{
    .fixture-list-content{
        margin-bottom: 0;
        @media (max-width: 767px){
            .collection-list-slider .collection-list{
                padding: 0 5px 5px;
            }
        }
    }
}
.contest-listing-web-collection{
    .app-header-style .header-content .team-header-content{
        padding-left: 0;
        padding-right: 0;
    }
}
.contest-list-wrapper{
    .contest-listing-card{
        .contest-heading{
            .text-small-italic{
                font-size: $font-size-small;
                font-family: $font-regular;
            }
        }        
    }
}

.contest-card-footer{
    padding: 0px 0px;
    margin-top: 5px;
    color: $help_text_color;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &.height-sponsor-strip{
        height: 31px;
    }
    .sponser-by-text{
        font-size: $font-size-small;
        font-family: $font-regular;
        width: 50%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        max-width: 200px;
        @media (max-width: 767px){
            max-width: 140px;
        }
    }
    .sponsor-logo-section{
        font-family: $font-bold;
        font-size: $font-base;
        span{
            color: $primary;
        }
        display: block;
        width: 100%;
        position: absolute;
        left: 0;
        bottom: 0;
        border-radius: 0 0 14px 14px;
        overflow: hidden;
        a{
            display: block;
            width: 100%;
            .lobby_sponser-image{
                width: 100%;
            }
        }
    }
    &.justify-center{
        justify-content: center;
    }
    
   
}

// reverse fantasy related styling
.reverse-fantasy-sort-wrap{
    padding: 15px 25px 0;
    .rev-fant-sort-inner-wrap{
        position: relative;
        background: $disabled_text_color;
        padding: 10px 10px 10px 49px;
        border-radius: 14px;
    }
    img{
        position: absolute;
        left: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 30px;
    }
    .label{
        font-size: $font-size-small;
        font-family: $font-bold;
        color: $primary_text_color;
    }
    .desc{
        font-size: $font-size-h6;
        font-family: $font-regular;
        color: $help_text_color;
    }
    .view-rev-fan{
        color: $primary;
        background: $white;
        border-radius: 22px;
        text-transform: uppercase;
        font-size: $font-size-small;
        padding: 7px 20px 5px;
        font-family: $font-bold;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
    }
    &.dfs-card-wrap{
        .dfs-card {
            margin-bottom: 0;
            height: 56px;
            .img-dfs-shape{
                width: 30px;
                position: absolute;
                right: 0;
                top: 0;
                left: auto;
                transform: translate(0);
            }
            .dfs-c{
                width: 97%;    
                display: block;
                position: relative;
                padding-left: 110px;   
                padding-right: 15px;
                @media (max-width: 767px){
                    padding-left: 90px;
                }
                .img-dfs{
                    width: 30%;
                    max-width: 90px;
                    @media (max-width: 767px){
                        max-width: 60px;
                    }
                }
                p{
                    font-size: $font-base;
                    line-height: 16px;
                    max-width: 100%;
                }
            }
        }
    }
}