.static-page{
    background: $app_bg_color;
    font-size: 13px;
    .page-body{
        padding: 25px;
    }
    .page-header{
        padding: 60px 10px;
        text-align: center;
        box-shadow: 0 16px 16px 0 rgba(0, 0, 0, 0.05);
        p{
            margin: 5px 0;
            font-size: 12px;
            font-family: $font-regular;
            color: $primary_text_color;
            text-transform: uppercase;
        }
    }
    .page-subheading{
        font-size: 11px;
        color: $primary_text_color;
        font-family: $font-black;
        text-transform: uppercase;
        background: $light-gray;
        padding: 5px 15px;
    }
    .static-page-title-header-container{
        background-color: $white;
        box-shadow: 0 16px 16px 0 rgba(0,0,0,0.05);
        padding: 30px 40px;
        text-align: center;
        position: fixed;
        width: 100%;
        max-width: $window-width;

        .title {
            color: $primary_text_color;
            font-family: $font-black;
            font-weight: 900;
            font-size: 14px;
            line-height: 20px;
            text-align: center;
            text-transform: uppercase;
        }
    }
    .heading,p{
        font-size: 12px;
        color: $primary_text_color;
        margin-top: 15px;
        margin-bottom: 15px;        
    }
    .heading{
        font-family: $font-bold;
        text-transform: uppercase;
    }
    p{
        font-family: $font-regular;
        margin-bottom: 15px;
    }
    .panel.panel-default{
        border-bottom: 1px solid $border-light;
    }
    .panel-title{
        padding: 15px 25px 15px 15px;
        font-size: 14px;
        font-family: $font-regular;
        font-weight: bold;
        a{
            color: $primary_text_color;
            position: relative;
            span{
                display: inline-block;
                padding-left: 30px;
            }
            .panel-label{
                position: absolute;
                left: 0;
                padding: 0;
                color: $primary;
                text-transform: uppercase;
            }
        }
    }
    .panel-body{
        padding: 0px 25px 15px 46px;
    }
    .page-body{
        ul{
            padding: 0;
            margin: 0;
            list-style-type: none;
            li{
                font-size: 12px;
                font-family: $font-regular;
                margin-bottom: 15px;
            }
            &.list-inline{
                display: inline-block;
                width: 50%;
                li{
                    font-family: $font-black;
                }
            }
        }
        &.header-top-margin{
            margin-top: 81px;
            @media screen and(max-width:320px){
                margin-top: 108px;
            }
        }
    }
}
.page-heading{
    font-size: 20px;
    font-family: $font-black;
    color: $primary_text_color;
    max-width: 240px;
    text-transform: uppercase;
    margin: 0 auto;
    &.about-heading{
        max-width: 340px;
    }
}

.static-page {
    .page-body {
        .rules-scoring-table {
            margin: 20px auto;
            width: 80%;
            td {
                display: block;
                float: left;
                width: 50%;
                border: 1px solid #ddd;
            }
        }

        .custom-nav-tabs {
            .nav.nav-tabs {
                margin-bottom: 0;
                li {
                    width: 33.33%;
                    margin-bottom: 0;
                }
            }
        }

        &.rules-scoring-body {
               
            .custom-nav-tabs .nav-tabs{
                border: 0;
                box-shadow: 0 16px 16px 0 rgba(0, 0, 0, 0.05);
                background: $white;
                margin-bottom: 10px;

                li{
                    a{
                        color: $help_text_color;
                        font-family: $font-regular;
                        font-weight: 800;
                        text-transform: uppercase;
                        display: inline-block;
                        width: 100%;
                        text-align: center;
                        padding: 10px;
                        font-size: 12px;
                        letter-spacing: 1px;
                    }
                    &.active{
                        a{
                            color: $primary;
                            position: relative;
                            &:before{
                                content: '';
                                position: absolute;
                                height: 2px;
                                width: 100%;
                                background: $primary;
                                bottom: 0px;
                                left: 0;
                            }
                        }
                    }
                }
            }

            .type-heading {
                text-align: center;
                padding: 6px;
                background: $primary;
                border-radius: 5px;
                font-family: $font-regular;
                font-weight: bold;
                color: $white;
            }

            ul.scoring-chart {
                margin: 20px 0;

                .text-block,
                .value-block {
                    display: table-cell;
                    background-color: $white;
                    box-shadow: 0 0 4px 0 rgba(0, 0, 0, .1);
                    padding: 10px;
                    vertical-align: middle;
                    color: $primary_text_color;
                }

                .text-block {
                    border-radius: 3px 0 0 3px;
                }

                .value-block {
                    text-align: center;
                    width: 20%;
                    @media (max-width: 767px){
                        width: 25%;
                    }
                    border-radius: 0 3px 3px 0;
                    color: $primary_text_color;
                    &.new-value-block{
                      color: $alert_success_color;
                    }
                }
                li{
                    &.header-block{
                        .text-block,
                        .value-block {
                            background: transparent;
                            box-shadow: none;
                        }
                    }
                }
            }
        }
    }
}