.my-team-root{
    &.ios-header-fix{
        @media (max-width: 500px){
            max-height: 100vh !important;
            overflow: auto !important;
        }
    }
    .create-team-button{

        margin: 0 auto;
        margin-top: 10px;
        // margin-bottom: 10px;
        text-align: center;
        border-radius: 15px;
        background-color: $primary;
        padding: 5px;
        width: 50%;
        cursor: pointer;
        background-position: center;
        transition: 0.8s;

        &:active {
            // background-color: rgba(12, 191, 235, 0.8);
            @include background-opacity($primary,0.8);
            background-size: 100%;
            transition: 0s;
        }


        @media screen and (max-width: 420px){
            width: 90%;
        }

        span {
            color: $white;
            font-family: $font-bold;        
            font-size: $font-size-small;
            line-height: 16px;
            text-align: center;
            text-transform: uppercase;
            text-shadow: 0 0 4px 0 rgba(0,0,0,0.4);
        }
    }

    .my-team-list-item{
        list-style: none;
        padding: 0;
        border-bottom: 0px;
        border-radius: 14px;
        margin: 15px;

        .drawer-menu-item{
            &.collpased-list-hide {
                transition: all 5s ease-in-out;
                overflow: hidden;
                height: 0px;
            }
            &.collpased-list-show{
                transition: all 5s ease-in-out;
                height: auto;
            }
        }
    }
    .my-teams-item {
        width: 100%;
        border-radius: 14px;
        background-color: $white;
        border: 1px solid $white;
        position: relative;

        &.selected{
            opacity: 0.4px;
            border: 1px solid $primary;
        }
        
        table{
            border: 0px;
            padding: 0px;
            margin: 0px;
            .captain-vice-captain{
                background-color: $app_bg_color;
            }
            tr{
                background-color: $white;
                &.captain-vice-captain{
                    &.full-width-content{
                        td{
                            width: 100%;
                        }
                    }
                    td{
                        vertical-align: middle;
                        width: 50%;
                        position: relative;
                        padding-left: 44px;
                        .image-container{
                            display: inline-block;
                            width: 58px;
                            position: absolute;
                            left: 10px;
                            top: 50%;
                            transform: translateY(-50%);
                        }
                        .player-name-container{
                            padding-left: 12px;  
                        }
                    }
                }
                td{
                    border-top: 0px;
                    .image-container{
                        display: inline-block;
                        width: 58px;
                        position: relative;

                        .player-image{
                            height: 40px;
                            width: 40px;
                            border-radius: 50%;
                        }

                        .player-post{
                            height: 16px;
                            width: 16px;
                            border-radius: 50%;
                            position: absolute;
                            bottom: 0;
                            right: 15px;
                            text-align: center;
                            color: $white;
                            background-color: $primary;
                            font-weight: bold;
                            display: inline-block;
                            vertical-align: middle;
                            line-height: 15px;
                        }
                    }
                    .player-name-container{
                        display: inline-block;
                        vertical-align: middle;
                        margin-left: 7px;
                        
                        .player-name{
                            color: $primary_text_color;
                            font-family: $font-book;
                            font-size: $font-size-small;
                            line-height: 1;
                            margin-bottom: 4px;
                        }
                        .team-vs-team{
                            color: $help_text_color;
                            font-family: $font-regular;
                            font-size: $font-size-h6;
                            line-height: 13px;
                            margin-top: 0px;
                        }

                       
                        @media screen and(max-width: 420px) {
                            .player-name{
                                font-size: $font-size-small;
                            }
                            .team-vs-team{
                                font-size: $font-size-h6;                            
                            }
                        }
                        @media screen and(max-width: 320px) {
                            .player-name{
                                font-size: $font-size-small;
                            }
                            .team-vs-team{
                                font-size: 9px;                            
                            }
                        }
                    }

                }
            }

        }

        .bottom-row{
            width: 100%;
            background-color: $white;
            text-align: center;
            padding: 10px 8px;
            vertical-align: middle;
            cursor: pointer;
            border-radius: 0 0 14px 14px;

            span {
                color: $primary;
                font-family: $font-bold;
                font-size: $font-size-small;
                line-height: 15px;
                text-align: center;
            }

            i{
                color: $primary;
                font-size: $font-size-h6;
                margin: -1px 0 0 5px;
                transform: rotate(90deg);
                display: inline-block;
            }
        }

        .player-list-container{
            .player-list-item{
                .item-header{
                    padding: 5px;
                    text-align: center;
                    background-color: $app_bg_color;
                    box-shadow: 8px 8px 16px 0 rgba(0,0,0,0.1);

                    color: $help_text_color;
                    font-family: $font-bold;
                    font-size: $font-size-small;
                    letter-spacing: -0.1px;
                    line-height: 15px;
                    text-align: center;
                    & ~ .item table tr td{
                        border-top: 0;
                    }
                }
                .item{

                    table{
                        border: 0px;
                        padding: 0px;
                        margin: 0px;
                        .captain-vice-captain{
                            background-color: $app_bg_color;
                            box-shadow: 0 16px 16px 0 rgba(0,0,0,0.05);
                        }
                        tr{
                            background-color: $white;
                            
                            td{
                                border-top: 1px solid $border-light;
                                padding: 4px 15px;
                                &.left{
                                    text-align: left;
                                    vertical-align: middle;
                                }

                                &.right{
                                    text-align: right;
                                    vertical-align: middle;
                                }

                                .image-container{
                                    display: inline-block;
                                    width: 58px;
                                    position: relative;
                
                                    .player-image{
                                        height: 40px;
                                        width: 40px;
                                        border-radius: 50%;
                                    }
                
                                    .player-post{
                                        height: 16px;
                                        width: 16px;
                                        border-radius: 50%;
                                        position: absolute;
                                        bottom: 0;
                                        right: 15px;
                                        color:$white;
                                        text-align: center;
                                        background-color: $primary;
                
                                        &.captain{
                                            background-color: $primary;
                                        }
                
                                        &.vice-captain{
                                            background-color: $primary;
                                        }
                                        
                                    }
                                }
                                
                                .credit-container{
                                    display: inline-block;
                                    vertical-align: middle;
                                    margin-left: 7px;
                                    
                                    .credit-amount{
                                        font-family: $font-regular;                        
                                        color: $help_text_color;
                                        font-size: $font-base;
                                        letter-spacing: -0.12px;
                                        line-height: 18px;
                                        text-align: right;
                                    }
                                    .credit-text{
                                        font-family: $font-bold;
                                        color: $help_text_color;
                                        font-size: $font-size-xsmall;
                                        letter-spacing: -0.07px;
                                        line-height: 13px;
                                        text-align: right;
                                    }

                                    @media screen and(max-width: 420px) {
                                        .player-name{
                                            font-size: $font-size-small;
                                        }
                                        .team-vs-team{
                                            font-size: $font-size-h6;                            
                                        }
                                    }
                                    @media screen and(max-width: 320px) {
                                        .player-name{
                                            font-size: $font-size-xsmall;
                                        }
                                        .team-vs-team{
                                            font-size: 9px;                            
                                        }
                                    }
                                }
                
                            }
                        }
                
                    }
                }
                &:last-child{
                    border-bottom: 1px solid $border-light;
                }
            }
        }
        a.edit-team{
            position: absolute;
            top: 12px;
            right: 45px;
            font-size: $font-size-h5;
            color: $body_text_color;
        }
        a.clone-team{
            position: absolute;
            top: 12px;
            right: 75px;
            font-size: $font-size-h5;
            color: $body_text_color;
        }
    }

    .row-header {
        background-color: $white;
        justify-content: space-between;
        padding: 10px;
        margin: 5px;
        border-radius: 14px 14px 0 0;

        .name-container{
            float: left;

            .team-name {
                height: 18px;
                color: $primary_text_color;
                font-family: $font-book;
                font-size: $font-size-medium;
                line-height: 18px;
                margin-bottom: 4px;
            }

            .contests-joined {
                color: $help_text_color;
                font-family: $font-regular;
                font-size: $font-size-h6;
                line-height: 10px;
                padding-bottom: 5px;
            }
        }

        .select-team-checkbox {
            float: right;
            box-sizing: border-box;
            height: 20px;
            width: 20px;
            border-radius: 10px;
            border: 1px solid $border_color;
            cursor: pointer;

            &.selected{
                border: 5px solid $primary;
            }
        }
    }

    .rectangle-11-copy-21 {
        height: 60px;
        width: 100%;
        background-color: rgb(160, 31, 31);
        box-shadow: 0 16px 16px 0 rgba(0, 0, 0, 0.05);
    }

    .bottom{
        width: 100%;
        background-color: $primary;
        color: $white;
        font-family: $font-bold;
        font-size: $font-base;
        line-height: 16px;
        text-align: center;
        text-shadow: 0 0 4px 0 rgba(0,0,0,0.4);
    }


    .header-content{
        text-align: center;
        height: 60px;
        padding-top: 10px;
        margin-top: 10px;
        justify-content: center;
        align-items: center;
        display: flex;
        div{
            display: inline-block;
            vertical-align: top;
        }
        .team-img-block{
            width: 34px;
            height: 34px;
            border-radius: 50%;
            overflow: hidden;
            img{
                max-width: 100%;
                max-height: 100%;
            }
        }
         .team-header-content{
            color:$primary_text_color;
            font-family: $font-bold;
            font-size: $font-base;
            padding-left: 10px;
            padding-right: 10px;
            small{
                font-size: $font-size-xsmall;
            }
            p{
                margin: 0;
                font-size: 8px;
                font-family: $font-bold-italic;
                color:$body_text_color
                
            }
        }
        .team-header-detail{
            margin:  0 10px;
            padding-left: 5px;
            padding-right: 5px;
            .team-header-content{
                color:$primary_text_color;
                font-family: $font-bold;
                font-size: $font-base;
                small{
                    font-size: $font-base;
                }
            }
            p{
                margin: 0;
                font-size: 8px;
                font-family: $font-bold-italic;
                color:$body_text_color;
                
                .date{
                    color: $help_text_color;
                    font-family: $font-bold;
                    font-size: $font-size-xsmall;
                    line-height: 13px;
                    text-align: center;
                }
            }
        }
    }
}
.reverse-heading{
    font-size: $font-base;
    color: $primary;
    font-family: $font-bold;
    text-align: left;
    padding: 20px 16px 0;
    text-transform: capitalize;
    display: flex;
    justify-content: space-between;

    .select-team-checkbox {
        float: right;
        box-sizing: border-box;
        height: 20px;
        width: 20px;
        min-width: 20px;
        border-radius: 10px;
        border: 1px solid $primary;
        cursor: pointer;

        &.selected {
            border: 5px solid $primary;
        }
    }
}
.my-t-j-width {
    display: block;
    font-size: $font-size-h6;
    font-family: $font-book;
    line-height: 6px;
    margin-top: 2px;
}
