.web-container.web-container-fixed{
    &.add-funds-wrapper{
        padding-top: 56px;
    }
    &.withdraw-page-wapper{
        padding-top: 56px;
        .app-header-style{
            background: $primary;
            .row-container .section-left .header-action,
            .app-header-text{
                color: $secondary_text_color;
            }
        }
    }
}
.add-funds-wrapper{
    .app-header-style{
        background: $primary;
        .header-action,.app-header-text{
            color: $secondary_text_color;
        }
    }
}
.add-funds-header{
    padding: 0 30px;
    background: $primary;
    label{
        color: $secondary_text_color;
        font-size: $font-size-small;
        font-family: $font-bold;
    }
    .form-control{
        box-shadow: 0 2px 30px 0 rgba(0,0,0,0.1);
        border-radius: 10px;
        font-size: $font-size-h4;
        color: $primary_text_color;
        font-family: $font-bold;
        padding: 15px 20px;
        height: 54px;
        border: none;
    }
    .form-group{
        margin-bottom: 0;
        position: relative;
        top: 24px;
        ::-webkit-input-placeholder { /* Edge */
            font-size: $font-size-h4 !important;
        }
          
        :-ms-input-placeholder { /* Internet Explorer 10-11 */
            font-size: $font-size-h4 !important;
        }
          
        ::placeholder {
            font-size: $font-size-h4 !important;
        }
    }
}
.add-funds-body{
    background: $view_bg_color;
    padding: 50px 30px 30px;
    .promo-code-section{
        font-size: $font-size-small;
        margin-top: 50px;
        margin-bottom: 50px;
        span{
            color:$help_text_color;
        }
    }
    .button-block{
        max-width: 315px;
        margin: 0 auto;
    }
}
.amount-section{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    a{
        display: inline-block;
        width: 150px;
        @media (max-width: 767px){
            width: 108px;
        }
        height: 54px;
        vertical-align: middle;
        border: 1px solid $border_color;
        border-radius: 10px;
        background: $view_bg_color;
        padding: 18px;
        text-align: center;
        font-size: $font-base;
        color: $body_text_color;
        font-family: $font-bold;
        i{
            display: none;
        }
        &:nth-child(2){
            margin: 0 15px;
        }
        &.selected{
            position: relative;
            border-color: $primary;
            i{
                display: block;
                position: absolute;
                right: 10px;
                top: 8px;
                font-size: 9px;
                color: $primary;
            }
        }
        @media (max-width: 360px){
            margin: 0 6px 0 0;
            padding: 18px 10px;
            &:nth-child(2){
                margin: 0 10px;
            }
            &:nth-child(3){
                margin: 0 0 0 6px;
            }
        }
    }
}
.best-deal{
    @include background-opacity($ternary,0.48);
    border-radius: 10px;
    padding: 13px 15px 12px;
    text-align: center;
    position: relative;
    font-size: $font-size-h5;
    color: $body_text_color;
    text-transform: uppercase;
    margin: 15px auto 30px;
    max-width: 100%;
    cursor: pointer;
    @media (max-width: 767px){
        max-width: 100%;
    }
    border: 1px solid transparent;
    @media (max-width: 360px){
        font-size: $font-size-medium;
    }
    img{
        position: absolute;
        left: 15px;
        top: 0;
    }
    span{
        color: $primary;
        font-family: $font-black;
        display: inline-block;
        margin-left: 10px;
    }
    .icon-tick-ic{
        display: none;
    }
    &.selected{
        border: 1px solid rgba(194, 227, 238, 0.48);
        border-color: $primary;
        .icon-tick-ic{
            position: absolute;
            display: block;
            right: 10px;
            top: 8px;
            color: $primary;
            font-size: 9px;
        }
    }
}
.selected-deal-offer{
    margin: 15px 0;
    border-radius: 10px;
    background: url('../img/selected-deal-bg.png') no-repeat;
    background-size: cover;
    @media (max-width: 767px){
        background: url('../img/selected-deal-bg-sm.png') no-repeat;
        background-size: cover;
        background-position: center 10px;
    }
    padding: 20px;
    border: 1px solid $border_color;
    .deposite-heading{
        color: $body_text_color;
        font-size: $font-base;
        font-family: $font-regular;
        span{
            font-family: $font-bold;
        }
    }
}
.additional-benifit-section{
    padding-top: 28px;
    .heading{
        font-size: $font-size-small;
        color: $body_text_color;
        font-family: $font-book;
        text-align: center;
        margin-bottom: 10px;
    }
    .bonus-offer{
        border: 1px solid $border_color;
        border-radius: 6px;
        background: $view_bg_color;
        padding: 15px 10px;
        text-align: center;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        .icon-add{
            font-size: $font-size-h5;
            color: $help_text_color;
        }
        .bonus-amt{
            color: $primary;
            font-size: $font-size-h5;
            font-family: $font-bold;
            i{
                position: relative;
                top: -1px;
                display: inline-block;
                margin-right: 2px;
                font-size: $font-size-medium;
            }
            .coin-img {
                width: 15px;
                position: relative;
                top: -2px;
                left: -2px;
            }
        }
        .bonus-label{
            font-size: $font-size-xsmall;
            font-family: $font-regular;
            text-align: center;
            color: $help_text_color;
        }
    }
    .icon-plus-ic{
        font-size: $font-base;
        @include color-opacity($help_text_color,0.5);
    }
}

.withdraw-page-wapper{
    .total-winning-section{
        @include background-opacity(#212121,0.1);
        border-radius: 10px;
        padding: 10px 15px;
        position: relative;
        display: table;
        margin: 0 auto;
        width: 100%;
        .winning-text-section{
            color: $secondary_text_color;
            padding-left: 40px;
            i{
                position: absolute;
                left: 15px;
                top: 50%;
                transform: translateY(-50%);
                font-size: 38px;
            }
            h2{
                font-size: $font-size-medium;
            }
            p{
                margin: 0;
                font-size: $font-size-h6;
            }
        }
        .winning-amount{
            width: 30%;
            vertical-align: middle;
            font-size: $font-size-medium;
            color: $secondary_text_color;
            font-family: $font-bold;
        }
    }
    &.pending-section{
        .total-winning-section{
            padding: 20px; 
            .winning-text-section{
                h2{
                    line-height: 1;
                    margin-bottom: 7px;
                }
                p{
                    line-height: 1;
                }
            }
        }
    }
    .min-withdraw-text{
        margin: 15px 0 20px;
        color: $help_text_color;
        font-size: $font-size-xsmall;
        font-family: $font-regular;
    }
}
.withdraw-header{
    padding: 20px 30px 0;
    background: $primary;
    label{
        color: $secondary_text_color;
        font-size: $font-size-small;
        font-family: $font-bold;
    }
    .form-control{
        box-shadow: 0 2px 30px 0 rgba(0,0,0,0.1);
        border-radius: 10px !important;
        background: $view_bg_color !important;
        font-size: $font-size-h4;
        color: $primary_text_color;
        font-family: $font-bold;
        padding: 15px 20px;
        height: 54px;
        border-color: $view_bg_color !important;
    }
    .form-group{
        margin-bottom: 0;
        position: relative;
        top: 24px;
        ::-webkit-input-placeholder { /* Edge */
            font-size: $font-size-h4 !important;
        }
          
        :-ms-input-placeholder { /* Internet Explorer 10-11 */
            font-size: $font-size-h4 !important;
        }
          
        ::placeholder {
            font-size: $font-size-h4 !important;
        }
        &.has-error{
            label{
                color: $secondary_text_color;
            }
            .form-control{
                border-color: $red;
            }
        }
    }
    &.header-with-circle{
        .overlay-white-circle{
            width: 150%;
            height: 190%;
            top: -60%;
            left: -62%;
        }
    }
}
.withdraw-body{
    background: $view_bg_color;
    padding: 20px 30px 30px;
    .promo-code-section{
        font-size: $font-size-small;
        margin-top: 50px;
        margin-bottom: 50px;
    }
    .button-block{
        max-width: 315px;
        margin: 0 auto;
    }
}
.withdraw-acc-detail{
    background: $app_bg_color;
    padding: 20px;
    border-radius: 10px;
    margin-bottom: 25px;
    .heading{
        font-size: $font-size-small;
        color: $primary_text_color;
        font-family: $font-regular;
    }
    .detail-for{
        padding-top: 25px;
        font-family: $font-regular;
        font-size: $font-size-small;
        color: $help_text_color;
    }
    .detail-value{
        font-family: $font-book;
        font-size: $font-base;
        color: $primary_text_color;
        margin-top: 5px;
    }
}
.withdrawal-status{
    padding: 20px 0;
    font-size: $font-size-small;
    color: $secondary_text_color;
    font-family: $font-bold;
}
.withdraw-help-text{
    margin: 0 auto;
    max-width: 250px;
    font-size: $font-size-xsmall;
    font-family: $font-regular;
    color: $help_text_color;
}
.header-with-circle{
    position: relative;
    .overlay-white-circle{
        position: absolute;
        @include background-opacity($view_bg_color,0.08);
        width: 100%;
        height: 100%;
        top: -25%;
        left: -15%;
        z-index: 2;
        display: block;
        border-radius: 0 0 130% 0;
    }
}
.cash-action{
    .data-block-wrap:first-child {
        .data-count-block{
            background: $primary;
            border-color: $primary;
            .count,.count-for,i.icon-positioned-left{
                color: $secondary_text_color;
            }
        }
    }
}
.card-img-section{
    padding-top: 20px;
    width: 100%;
    max-width: 550px;
    text-align: center;
    img{
        max-width: 280px;
    }  
}