.sports-hub-c {
    padding-bottom: 65px !important;
    background: $white !important;
    .app-header-style{
        .app-header-text{
            margin-top: 2px;
        }
    }

    .dashboard-container {
        margin-top: 85px !important;

        .sports-tab-nav {
            background: $primary;
            padding: 0 30px;
            height: 30px;

            @media (max-width: 414px) {
                padding: 0 16px;
            }
        }

        .nav {
            padding: 0 5%;
            background-color: $white;
            height: 44px !important;
            border-radius: 10px;
            box-shadow: 0 6px 24px 0 rgba(0, 0, 0, 0.05);
            margin-top: 7px;

            li {
                min-width: auto !important;
                margin: 0 8% 0 0;

                a {
                    padding: 10px 6px 8px !important;

                    .animated-tab-div {
                        position: relative;
                        top: 0;
                        animation: fadein-slide 1s;
                        opacity: 1;

                        @keyframes fadein-slide {
                            0% {
                                opacity: 0;
                                top: -18px;
                            }
                            20% {
                                opacity: 0;
                            }

                            100% {
                                opacity: 1;
                                top: 0px;
                            }
                        }
                    }

                    i {
                        font-size: 26px;
                    }

                    &::after {
                        height: 3px !important;
                        border-radius: 1.5px;
                        min-width: 38px;
                    }
                }

                &.active {
                    a {
                        .animated-tab-div {
                            animation: fadein-slide1 1s;
                            opacity: 1;

                            @keyframes fadein-slide1 {
                                0% {
                                    opacity: 0;
                                    top: 18px;
                                }
                                20% {
                                    opacity: 0;
                                }
    
                                100% {
                                    opacity: 1;
                                    top: 0px;
                                }
                            }
                        }

                        i {
                            color: $primary-text-color;
                        }
                    }
                }
            }
        }

        .pick-type {
            background-color: $primary;
            padding: 25px 8% 45px;

            .pick-type-title {
                font-size: $font-size-h4;
                color: $white;
                font-family: $font-bold;
                margin-bottom: 8px;
                position: relative;
                left: 0;
                animation: slide-left 0.9s ease-in-out;

                @keyframes slide-left {
                    0% {
                        left: -100%;
                        opacity: 0;
                    }

                    40% {
                        left: -60%;
                    }

                    100% {
                        left: 0;
                        opacity: 1;
                    }
                }
            }

            .pick-type-detail {
                font-size: $font-size-small;
                color: $white;
                max-width: 295px;
                position: relative;
                right: 0;
                animation: slide-right 0.9s ease-in-out;

                @keyframes slide-right {
                    0% {
                        right: -150%;
                        opacity: 0;
                    }

                    40% {
                        right: -120%;
                    }

                    100% {
                        right: 0;
                        opacity: 1;
                    }
                }
            }
        }

        .card-container {
            padding: 40px 0 0;
            margin: 0 auto;
            max-width: 510px;

            .card-v {
                margin: 0 8% 20px;

                @media (min-width: 450px) {
                    margin: 0 9% 20px;
                }

                background-color: $white;
                border-radius: $border-radius-lg;
                overflow: hidden;
                list-style: none;
                cursor: pointer;
                perspective: 100px;
                box-shadow: 0 2px 30px 0 rgba(0, 0, 0, 0.1);

                &.new-op {
                    margin: 0 0 20px;
                    padding: 20px 8% 12px;

                    @media (min-width: 450px) {
                        padding: 20px 9% 12px;
                    }

                    border-radius: 0;
                    box-shadow: none;
                    overflow: unset;

                    img {
                        background-color: transparent;
                        max-height: unset;
                        object-fit: fill;
                    }

                    .game-v {
                        position: absolute;
                        top: 25%;
                        left: 38%;

                        @media (min-width: 414px) {
                            top: 27%;
                            left: 40%;
                        }

                        .game-v-title {
                            color: $primary;
                            max-width: 130px;

                            @media (min-width: 414px) {
                                margin-bottom: 10px;
                            }
                        }

                        .game-v-detail {
                            color: $primary-text-color;
                            font-size: $font-size-small;
                            font-family: $font-book;
                            max-width: 132px;
                        }
                    }

                    .p-now {
                        display: block;
                        position: absolute;
                        left: 50%;
                        bottom: 10px;
                        padding: 7px 20px 4px;
                        background: $primary;
                        border-radius: 15px;
                        color: $white;
                        font-family: $font-bold;
                        font-size: $font-size-small;
                        text-transform: uppercase;
                    }


                    .bg-abs {
                        position: absolute;
                        top: 0;
                        left: -20px;
                        width: calc(100% + 40px);
                        height: 100%;
                        @include background-opacity($primary, 0.22);
                        z-index: -1;
                        pointer-events: none;
                    }

                    // .non-sports {
                    //     mask: url('../img/non-Sports-label.svg') no-repeat;
                    //     mask-size: cover;
                    //     background: $primary;
                    //     width: 20px;
                    //     height: 87px;
                    //     position: relative;
                    //     left: 20px;

                    //     @media (min-width: 525px) {
                    //         left: 0px;
                    //     }

                    //     span {
                    //         font-family: $font-bold;
                    //         font-size: $font-size-xsmall;
                    //         color: $white;
                    //         line-height: 13px;
                    //         transform: rotate(-90deg);
                    //         white-space: nowrap;
                    //         display: block;
                    //         position: relative;
                    //         top: 54px;
                    //         left: 1px;
                    //     }
                    // }
                }

                &.big-card {
                    background-color: transparent;
                    box-shadow: none;
                    border-radius: 35px;

                    img {
                        max-height: 555px;
                        height: 100%;
                        object-position: top;
                        background-color: transparent;
                        min-height: 410px;
                        background-color: $primary_dark;
                    }

                    .game-v {
                        position: absolute;
                        top: 110px;
                        height: calc(100% - 90px);
                        width: 100%;
                        padding: 17px 26px 14px;
                        @media (max-width: 320px) {
                            padding: 8px 24px 14px;
                        }
                        &.pickem-type{
                            top: 100px;
                            height: calc(100% - 100px);
                            @media (max-width: 450px) {
                                top: 70px;
                                height: calc(100% - 70px);
                            }
                            .game-v-title {
                                max-width: 200px;
                                margin: 0 0 20px 0; 
                            }
                        }

                        .game-v-title {
                            color: $white;
                            font-size: 34px;
                            line-height: 42px;
                            max-width: 230px;
                            margin: 0 0 35px 0;
                            text-transform: capitalize;
                            @media (max-width: 320px) {
                                margin-bottom: 15px;
                            }
                        }

                        .game-v-detail {
                            color: $white;
                            margin: 0;
                            line-height: 16px;
                            font-size: $font-size-small;
                            font-family: $font-bold;
                            max-width: 300px;
                        }

                        .p-now {
                            top: unset;
                            right: unset;
                            bottom: 30px;
                            left: 50%;
                            transform: translateX(-50%);
                            font-size: $font-base;
                            padding: 11px 0 8px;
                            width: calc(100% - 50px);
                            text-align: center;
                            border-radius: 20px;
                            margin-top: 30px;
                            border: none;
                        }
                        .ava-sport-sec{
                            margin-top: 10px;
                            max-width: 280px;
                            height: 54px;
                            @media (max-height:320px){
                                height: 74px;
                            }
                            span{
                                margin-top: 5px;
                                font-family: $font-book;
                                font-size: $font-base;
                                color: $white;
                                border: 1px solid $white;
                                padding: 2px 5px;
                                border-radius: 4px;
                                line-height: 1;
                                margin-right: 5px;
                                text-transform: capitalize;
                                display: inline-block;
                            }
                        }
                    }
                }

                img {
                    min-width: 100%;
                    width: 100%;
                    max-height: 180px;
                    min-height: 135px;
                    object-fit: cover;
                    object-position: bottom;
                    background-color: $border_color;
                }

                .game-v {
                    padding: 17px 20px 14px;
                    position: relative;

                    .p-now {
                        display: block;
                        position: absolute;
                        right: 15px;
                        top: -15px;
                        padding: 7px 15px 4px;
                        background: $primary;
                        border-radius: 15px;
                        color: $white;
                        font-family: $font-bold;
                        font-size: $font-size-small;
                        text-transform: uppercase;
                        border: 1px solid $white;
                    }

                    .game-v-title {
                        font-size: $font-size-medium;
                        color: $primary-text-color;
                        font-family: $font-bold;
                        text-transform: uppercase;
                        margin: 0 0 5px;
                        line-height: 19px;
                    }

                    .game-v-detail {
                        color: $help_text_color;
                        margin: 0;
                        line-height: 14px;
                        font-size: $font-size-small;
                    }
                }

            }

            // @for $i from 1 through 10 {
            //     li:nth-child(#{$i}) {
            //         animation-delay: .10s * $i;
            //     }
            // }
        }
    }
}

.sports-hub-footer-tabs {
    background-color: $white;
    box-shadow: 0 -4px 20px 0 rgba(0, 0, 0, 0.1);
    bottom: 0;
    position: fixed;
    z-index: 999;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    max-height: 30px;
    max-width: $window_width;

    .coins-tab-label {
        position: absolute;
        bottom: 9px;
        font-size: 8px;
        font-family: $font-bold;
        color: $white;
        transform: rotate(-3deg);
        letter-spacing: -0.4px;
        z-index: 2;
        &.marque{
            -webkit-animation:left-marquee linear infinite 3s normal;
            -moz-animation:left-marquee linear infinite 3s normal;
            animation:left-marquee linear infinite 3s normal;
            width: 140%;
        }
      }

    @-webkit-keyframes left-marquee{     
        0% { left:120%; } 100% { left:-50px; }  
    }

    @-moz-keyframes left-marquee{
        0% { left:120%; } 100% { left:-50px; }      
    }
  
    @keyframes left-marquee{
        0% { left:120%; } 100% { left:-50px; }      
    }

    .isCoin {
        height: 65px;
        width: 64px;
        background: $white;
        border-radius: 100%;
        box-shadow: 0 -8px 20px 0 rgba(0, 0, 0, 0.1);
        top: -34px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 100%;
        }
    }

    .shadow-v {
        position: absolute;
        bottom: 0px;
        height: 30px;
        width: 75px;
        background: $white;
    }

    .dot-list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 35%;

        span {
            width: 6px;
            height: 6px;
            background: $disabled_text_color;
            border-radius: 100%;
            animation: dots 2s infinite;
            opacity: 1;

            @keyframes dots {
                0% {
                    opacity: 0.1;
                }

                100% {
                    opacity: 1;
                }
            }
        }

        &.left {

            span:nth-child(6) {
                animation-delay: 0;
            }

            span:nth-child(5) {
                animation-delay: 0.2s;
                animation-duration: 1.8;
            }

            span:nth-child(4) {
                animation-delay: 0.3s;
                animation-duration: 1.7;
            }

            span:nth-child(3) {
                animation-delay: 0.4s;
                animation-duration: 1.6;
            }

            span:nth-child(2) {
                animation-delay: .5s;
                animation-duration: 1.5;
            }

            span:nth-child(1) {
                animation-delay: .6s;
                animation-duration: 1.4;
            }
        }

        &.right {

            span:nth-child(1) {
                animation-delay: 0;
            }

            span:nth-child(2) {
                animation-delay: 0.2s;
                animation-duration: 1.8;
            }

            span:nth-child(3) {
                animation-delay: 0.3s;
                animation-duration: 1.7;
            }

            span:nth-child(4) {
                animation-delay: 0.4s;
                animation-duration: 1.6;
            }

            span:nth-child(5) {
                animation-delay: .5s;
                animation-duration: 1.5;
            }

            span:nth-child(6) {
                animation-delay: .6s;
                animation-duration: 1.4;
            }
        }
    }
}
.sports-hub-c{
    &.new-sport-hub{
        background: $app_bg_color !important;
        .slick-initialized .slick-slide{
            &:first-child{
                padding-left: 0;
            }
            padding-left: 10px;
        }
        .dashboard-container {
            margin-top: 56px !important;
            .sports-tab-nav{
                background: transparent;
            }
            .card-container{
                max-width: 100%;
                padding: 0;
            }
            .card-v {
                margin: 0 20px 20px;
                box-shadow: none;
                @media (min-width: 450px){
                    margin: 0 20px 20px;
                }
                border-radius: 10px;
