                        }
                    }
                    padding: 17px 20px 21px;
                    .game-v-title{
                        max-width: 220px;
                        color: $primary;
                        text-transform: capitalize;
                        position: relative;
                        z-index: 2;
                        @media (max-width: 320px){
                            max-width: 190px;
                        }
                    }
                    .game-v-detail{
                        color: $body_text_color;
                        font-family: $font-semibold;
                        max-width: 360px;
