                        width: 80px;
                        font-size: $font-base;
                        font-family: $font-bold;
                        border: 1.5px solid $white;
                        @media (max-width: 550px){
                            border: 1px solid $white;
                        }
                    }
                }
                .ava-sport{
                    border-top: 1px solid $border-color;
                    padding: 7px 10px;
                    .sport-text{
                        border-radius: 4px;
                        background: $app_bg_color;
                        padding: 7px 6px 6px;
                        text-transform: capitalize;
                        color: $primary_text_color;
