                    }
                }
                &.big-card{
                    margin: 0;
                    border-radius: 30px;
                    margin-bottom: 20px;
                    &.big-card-single{
                        margin:  0 20px 20px;
                    }
                    img{
                       max-height: 420px;
                        @media (max-width: 550px){
                            max-width: 100%;
