                        }
                    }
                    .game-v {
                        // top: 45px;
                        // height: calc(100% - 45px);
                        // padding: 0 42px 14px;
                        .game-v-title{
                            margin: 0;
                            color: $white;
                            height: 126px;
                            max-width: 170px;
                            @media (max-width: 500px){
                                max-width: 140px;
                            }
                        }
                        .game-v-detail{
                            font-size: $font-size-medium;
                            line-height: 1.3;
                            margin-top: 15px;
                            font-family: $font-book;
                            max-width: 250px;
                            color: $white;
                            @media (max-width: 550px){
                                max-width: 210px;
                            }
                        }
                        @media (max-width: 360px){
                            top: 65px;
                            height: calc(100% - 65px);
                            padding-top: 5px;
