                                line-height: 37px;
                                max-width: 130px;
                                height: 106px;
                            }
                        }
                        @media (max-width: 320px){

                            .game-v-title{
                                font-size: 28px;
                                line-height: 34px;
                                max-width: 130px;
                            }
                            .game-v-detail{
                                font-size: $font-base;
                                margin-top: 12px;
                                max-width: 180px;
                            }
                        }
                        .p-now{
                            background: $black;
                            margin-top: 36px;
                            bottom: unset;
                            top: unset;
                            // text-transform: uppercase;
                            padding: 13px 0 10px;
                            position: relative;
                            width: 80%;
                            margin-top: 56px;
                            @media (max-width: 500px){
                                width: 252px;
                            }
                        }
                        .ava-sport-sec{
                            margin-top: 20px;
                            text-align: center;
                            height: auto;
