        .app-header-style{
            .section-middle{
                .center-container{
                    width: 142px;
                }
            }
            .header-brand-logo{
                width: 100%;
                max-width: 142px;
                height: auto;
            }
            .row-container{
                padding: 0 20px;
            }
        }
    }
}
