.registration-wrap{
    ~ .web-container-right{
        display: none;
    }
}
.web-container-right{
    position: fixed;
    right: 20px;
    bottom: 30px;
    display: flex;
    @media (max-width: 1024px){
        display: none;
    }
    img{
        background: black;
        height: 55px;
        border: 1px solid #fff;
        border-radius: 6px;
        margin-right: 12px;
    }
}