.share-contest-wrapper{
    &.share-collection-wrapper{
        .contest-card-wrapper.contest-card{
            padding: 0;
            background: $white;
            .contest-card-header{
                padding-bottom: 0;
                box-shadow: none;
                border-radius: 0;
                ul{
                    border-radius: 14px 14px 0 0;
                    box-shadow: none;
                    padding: 15px 0 0;
                    li.progress-middle{
                        &.progress-middle-fullwidth{
                            width: 100%;
                        }
                    }
                    li{
                        @media (max-width: 414px){
                            p{
                                max-width: 100%;
                            }
                        }
                    }
                }
            }
            .contest-card-body{
                border-radius: 0 0 14px 14px;
                border: 0;
                box-shadow: none;
                padding: 5px 15px 15px;
                .contest-list-header{
                    border: 1px solid $border_color;
                    border-radius: 10px;
                }
                 
            }
        }
    }
}
.contest-list .contest-heading img.rev-fan-img{
    position: static;
    width: 15px;
    margin-left: 5px;
}