html,body{
    height: 100%;
}
body{
    background: $app_bg_color;
    * { -webkit-overflow-scrolling: touch !important; }
    &.ReactModal__Body--open{
        * { -webkit-overflow-scrolling: auto !important; }
        overflow: hidden !important;
    }
    height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;
    @media (max-width: $window_width){
        background: $app_bg_color;
    }
}
.grecaptcha-badge { visibility: hidden !important; }
.Site-content{
    background-attachment: fixed;
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100vh;
    z-index: 0;
    @media (max-width: $window_width){
        background: $app_bg_color !important;
    }
}
.position-unset{
    position: initial;
}
.web-container{
    width: 100%;
    max-width: $window_width;
    min-height: 100vh;
    padding-bottom: 60px;
    width: 100%;
    background: $app_bg_color;
    overflow-y: auto;
    &.onboarding-style{
        min-height: calc(100vh - 56px);
    }

    &.pb0{
        padding-bottom: 0px;
    }
    .no-tab-two-height{
        min-height: calc(100vh - 110px);
    }
    .tab-two-height{
        min-height: calc(100vh - 164px);
    }

    .my-contest-style{
        margin-top: 0px;
    }
    &.header-margin{
        margin-top: 56px;
    }
    &.header-margin-55{
        margin-top: 55px;
    }
    &.minus-header-height{
        min-height: calc(100vh - 56px);
    }

    &.position-unset{
        position: initial;
    }
    &.transparent-header{
        .header-wrap{
            background-color: transparent;
        }
    }
    &.transaction-web-container{
        padding-bottom: 0;
    }
    .bottom{
        position: fixed;
        bottom: 0;
        width: 100%;
        max-width: $window_width;
        z-index: 9;
        border-radius: 0px;
        box-shadow: 0 -4px 20px 0 rgba(0,0,0,0.1);

    }
    &.web-container-fixed{
        overflow: hidden;
        padding-top: 0;
        padding-bottom: 50px;
        margin-top: 0;
        min-height: 100vh;
        &.refer-friend{
            padding-bottom: 0;
        }
    }
    &.profile-web-container{
        .dashboard-container{ margin-top: -56px;}
    }
}

.white-bg{
    background: $white;
}
.display-table{
    display: table;
    width: 100%;
}
.display-table-cell{
    display: table-cell;
    &.v-mid{
        vertical-align: middle;   
    }
}
.registration-header.header-wrap{
    & ~ .mt-10per{
        .verification-block{
            height: calc(100vh - 120px);
           
        }
    }
    width: 100%;
    max-width: $window_width;
}
.view-center-align{
    width: 100%;
    margin-top: 15%;
    background-color: $white;
}
.verses{
    color: $primary_text_color;
    font-size: 14px;
    font-family: $font-bold;
    display: inline-block;
    width: 25px;
}
.text-small-italic{
    font-size: 10px;
    font-family: $font-bold-italic;
    color: $help_text_color;
}

.slick-arrow.slick-next,
.slick-arrow.slick-prev{
    z-index: 2;
    display: none !important;
    &::before{
        color: $primary;
    }
}
.match-heading{
    .display-table-cell{
        &:first-child,&:last-child{
            width: 25%;
        }
        img{
            max-width: 35px;
            max-height: 35px;
        }
        .team-name{
            font-size: 12px;
            color: $gray-900;
        }
    }
}
.winner-count{
    font-size: 10px;
    font-family: $font-bold-italic;
    color: $gray-550;
}
.modal-close {
    position: absolute;
    font-size: 12px;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    text-align: center;
}
.icon-g{
    color: $primary;
}
.icon-m{
    color: $primary;
}
.input-transparent{
    input{
        background: transparent !important;
    }
}
.page-footer{
    position: fixed;
    bottom: 0;
    width: 100%;
    left: 0;
    z-index: 2;
    max-width: $window_width;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
    .btn{
        border-radius: 0;
        &.btn-default{
            background-color: $gray-450;
            color: $primary;
            font-family: $font-black;
        }
    }
    &.zIndex9999{
        z-index: 9999;        
    }
}
.form-group{
    margin-bottom: 16px;
}
.list-card{
    background: $white;
    border-radius: 14px;
    padding: 12px 15px;
    margin-bottom: 15px;
    color: $primary_text_color;
    font-size: 14px;
    p{
        margin-bottom: 0;
    }
}
.font-12{
    font-size: 12px !important;
}
.dot-class{
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background: #999999;
    display: inline-block;
    position: relative;
    top: -1px;
    margin: 0 10px;
    @media (max-width: 767px){
        margin: 0 6px;
    }
}
.font-16{
    font-size: 16px !important;
}
.font-22{
    font-size: 22px !important;
}
.custom-radio-group{
    input[type="radio"]{
        position: absolute;
        opacity: 0;
    }
    .btn{
        border-radius: 50px !important;
        background: $border-light;
        color: $gray-600;
        font-size: 14px;
        padding: 12px 10;
        text-align: center;
        width: 105px;
        margin-left: 10px !important;
        border: 1px solid transparent;
        height: 44px;
        line-height: 1.3;
        &:first-child{
            margin-left: 0 !important;
        }
        @media (max-width: 767px) and (min-width: 375px){
            &:nth-child(3n+1) {
                margin-left: 0 !important;
            }            
        }
        @media (max-width: 374px) {
            margin-left: 10px !important;
            &:nth-child(2n+1) {
                margin-left: 0 !important;
            }
        }
        &.active{
            border-color: $primary;
            background: $white;
            color: $primary;
        }
    }
    [type="button"]{
        -webkit-appearance: none;
    }
    .btn-group {
        flex-wrap: wrap !important;
        margin: 10px auto;
        justify-content: center;
    }
}
.slide-pane__overlay.ReactModal__Overlay--after-open{
    max-width: $window_width;
    margin: 0 auto;
}
.react-date-picker__calendar-button {
    svg{
        display: none;
    }
    &:before{
        content: '\e935';
        font-family: 'icomoon' !important;
        font-size: 16px;
        color:#999999;

    }
}
.modal-footer{
    z-index: 9;
}
.text-small{
    font-size: 10px;
    color: $gray-550;
}
.select__value-container{
    font-size: 12px;
    text-align: center;
    .select__placeholder{
        width: 100%;
