}
.mt-20per{
    margin-top: 20% !important;
}
.icon-next-btn{
    font-size: 60px;
}
.dual-btn-footer{
    padding: 5px 0;
    overflow: hidden;
    a,.btn,.my-alert-button-text{
        font-size: 11px;
        border-radius: 0;
        box-shadow: none;
        background: $primary;
        margin: 0;
        box-shadow: none;
        width: 50%;
        color: $white;
        text-transform: uppercase;
        text-align: center;
        padding: 10px;
        font-family: $font-bold;
        &:nth-child(2){
            border-left: 1px solid $secondary;
            border-radius: 0;
        }
        &.single-text{
            width: 100%;
            background-position: center;
            transition: 0.8s;
    
            border-bottom-left-radius: 14px;
            border-bottom-right-radius: 14px;
            &:active,&.active {
                @include background-opacity($primary,0.8);
                background-size: 100%;
                transition: 0s;
            }
        }
        background-position: center;
        transition: 0.8s;

        &:active,&.active {
            @include background-opacity($primary,0.8);
            background-size: 100%;
            transition: 0s;
        }
    }
}
.select__indicators{
    position: absolute;
    right: 0;
}
.select__menu {
    z-index: 999 !important;position: relative;
}
