.featured-icon{
    display: inline-block;
    height: 16px;
    width: 16px;
    background: $white;
    color: $primary_text_color;
    font-family: $font-bold;
    text-transform: capitalize;
    font-size: $font-size-xsmall - 1px;
    text-align: center;
    border: 1px solid #BDBDBD;
    border-radius: 50%;
    line-height: 1.9;
    position: absolute;
    right: 10px;
    top: -16px;
    left: auto;
    & ~ .featured-icon{
        margin-right: 20px;
        & ~ .featured-icon{
            margin-right: 40px;
        }
    }
    &:hover,&:active{
        background: $primary;
        border-color: $primary;
        color: $white;
    }
}
.share-contest{
