            text-transform: capitalize;
            font-size: 12px;
            font-family: "Geominist-Bold";
            display: inline-block;
            padding: 13px;
            line-height: 16px;
            height: 42px;
            &.active{
                color: $primary;
                position: relative;
                &:after{
                    content: '';
                    position: absolute;
                    bottom: 1px;
                    height: 2px;
                    width: 100%;
                    background: $primary;
                    left: 0;
                }
            }
        }
    }
