.web-container.web-container-fixed {
    &.verify-account {
        padding-top: 57px;
        background: $view_bg_color;
    }
    &.wallet-wrapper {
        padding-top: 57px;
        padding-bottom: 0;
    }
    &.trans-web-container {
        padding-top: 57px;
        background: $view_bg_color;
    }
}
.font-style-normal {
    font-style: normal !important;
}

.m-state-tagging {
    .edit-Mobile-form {
        position: relative;
        padding-top: 72px;

        .state-b {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
        }
    }

    .select__menu-list {
        max-height: 200px !important;

        @media (max-height: 650px) {
            max-height: 150px !important;
        }
    }

    .modal-body {
        @media (max-height: 650px) {
            min-height: 280px !important;
        }
    }
}

.wallet-header {
    background: $primary;
    padding: 26px 30px 75px;
    color: $secondary_text_color;
    position: relative;
    &.wallet-header-with-verify {
        padding-bottom: 100px;
    }
    .acc-bal {
        font-size: $font-size-h2;
        line-height: 1.5;
        font-family: $font-bold;
        padding-top: 10px;
        .coin-img {
            width: 23px;
            position: relative;
            top: -2px;
        }
    }
    .total-bal-text {
        font-size: $font-base;
        font-family: $font-regular;
        line-height: 1.5;
    }
    .bal-summary {
        font-size: $font-size-xsmall;
        line-height: 1.5;
        font-family: $font-regular;
    }
    .currency-circle {
        position: absolute;
        top: 22px;
        right: 20px;
        border-radius: 100%;
        width: 108px;
        height: 108px;
        text-align: center;
        font-size: 55px;
        line-height: 110px;
        padding-top: 5px;
        @include background-opacity($black, 0.1);
        font-family: $font-bold;
        @include color-opacity($black, 0.1);
        .icon-no-currency {
            font-size: 44px;
            position: relative;
            top: -7px;
            left: 1px;
        }
    }
    .wallet-cont{
        position: relative;
        z-index: 3;
    }
    &.header-with-circle .overlay-white-circle {
        position: absolute;
        @include background-opacity($view_bg_color,1);
        opacity: 0.08;
        width: 130%;
        height: 210%;
        bottom: 0%;
        right: 14%;
        @media (max-width: 767px) {
            width: 210%;
            left: -132%;
            top: -110%;
            bottom: auto;
            right: auto;
        }
        z-index: 2;
        display: block;
        border-radius: 90%;
        top: auto;
        left: auto;
    }
    .add-cash-btn{
        position: absolute;
        bottom: 15px;
        right: 0px;
        a{
            border-radius: 22px;
            background: $white;
            display: inline-block;
            color: $primary;
            padding: 15px 22px 13px;
            &:not(:disabled):not(.disabled):active{
                background: $white;
                color: $primary;
            }
        }
    }
}
.user-winning-section {
    background: $view_bg_color;
    border-radius: 15px;
    .user-winning-body {
        display: table;
        width: 100%;
        border-bottom: 1px solid $border_color;
        .display-table-cell {
            vertical-align: middle;
            &.winning-amt {
                color: $primary;
                font-size: $font-size-h4;
                width: 40%;
                font-family: $font-bold;
                text-align: right;
                padding-right: 20px;
                &.winning-amt-sm {
                    font-size: 15px;
                    padding-right: 8px;
                }
            }
            &.data-count-block {
                padding-top: 13px;
                padding-bottom: 16px;
            }
        }
        .data-count-block {
            padding-top: 12px;
            padding-bottom: 12px;
            .count {
                font-size: $font-base;
            }
        }
    }
}
.wallet-body {
    padding: 15px;
    position: relative;
    top: -50px;
    z-index: 3;
    &.wallet-body-with-verify {
        top: -81px;
    }
    &.coin-only-head {
        top: -61px !important;
    }
}
.cash-action {
    .data-block-wrap {
        cursor: pointer;
    }
    .data-count-block {
        padding: 11px 10px 10px;
        margin-top: 15px;
        padding-left: 68px;
    }
    .count {
        font-size: $font-base;
    }
}
.bal-summary-wrap {
    margin-top: 15px;
    display: table;
    height: 100%;
    width: 100%;
    .cash-summary-with-amt {
        display: table-cell;
        width: 50%;
        height: 100%;
        vertical-align: middle;
        &:first-child {
            padding-right: 7px;
        }
        &:last-child {
            padding-left: 7px;
        }
    }
}

.custom-page-header {
    padding: 20px 48px;
    @media (max-width: 320px) {
        padding: 20px;
    }
    h1 {
        font-size: $font-size-h2;
        font-family: $font-bold;
        color: $primary_text_color;
        max-width: 200px;
        line-height: 1.2;
        text-transform: capitalize;
    }
    p {
        font-size: $font-size-small;
        font-family: $font-regular;
        color: $help_text_color;
        max-width: 100%;
        @media (max-width: 767px) {
            max-width: 230px;
        }
    }
}
.verify-account-body {
    padding: 48px;
    padding-bottom: 30px;
    @media (max-width: 320px) {
        padding: 20px;
        padding-bottom: 30px;
    }
}
.verify-block {
    border-radius: 10px;
    background: $view_bg_color;
    border: 1px solid $border_color;
    padding: 20px;
    position: relative;
    margin-top: 15px;
    display: table;
    width: 100%;
    min-height: 80px;
    &.email-block {
        padding-bottom: 13px;
        .bonus-banner {
            margin-top: 10px;
        }
    }
    &:first-child {
        margin-top: 0;
    }
    &.display-block {
        display: block;
    }
    &.verify-block-success {
        border-color: $alert_success_color;
        i {
            font-size: $font-size-h5;
            color: $alert_success_color;
        }
    }
    .verify-block-status {
        padding: 10px 0 10px 10px;
        @include background-opacity($view_bg_color, 0.8);
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 18px;
        img {
            max-width: 22px;
        }
        .button {
            padding: 2px 10px;
            font-size: $font-size-h6;
        }
        span {
            color: $help_text_color;
            display: inline-block;
            margin-right: 10px;
            text-align: right;
            font-size: $font-size-h6;
            max-width: 50px;
            vertical-align: middle;
        }
    }
    .verify-block-content {
        display: table-cell;
        vertical-align: middle;
    }
    .bonus-banner {
        margin-top: 20px;
        cursor: pointer;
    }
}

.bonus-banner {
    background: $primary;
    position: relative;
    .overlay-banner {
        background-image: url("../img/offer-stp-overlay@2x.png");
        background-size: 100%;
        width: 100%;
        position: absolute;
        right: -10%;
        top: 0;
        height: 100%;
    }
    &:after {
        content: "";
        height: 100%;
        width: 100%;
        position: absolute;
        background: url("../img/banner-circle-shape.svg");
        background-repeat: no-repeat;
        z-index: 2;
        top: 6px;
        left: 0;
    }
    &:before {
        content: "";
        height: 100%;
        width: 100%;
        position: absolute;
        background: url("../img/banner-circle-shape.svg");
        background-repeat: no-repeat;
        z-index: 2;
        top: -5px;
        right: 0;
        transform: rotate(180deg);
    }
    @media (max-width: 400px) {
        background-size: contain;
    }
    padding: 5px;
    text-align: center;
    font-size: $font-size-h5;
    font-family: $font-regular;
    color: $secondary_text_color;
    text-transform: uppercase;
    background-repeat: no-repeat;
    background-size: cover;
    span {
        font-family: $font-black;
    }
    i {
        display: inline-block;
        margin-left: 4px;
        position: relative;
        top: -1px;
    }
}

.payable-amt {
    padding: 15px;
    color: $secondary_text_color;
    background: $primary;
    font-size: 20px;
    font-family: $font-regular;
    text-align: center;
    text-transform: uppercase;
    flex: 1;
    display: flex;
    justify-content: space-between;
    span {
        font-family: $font-bold;
    }
    i {
        font-size: $font-size-h4;
        position: relative;
        font-family: $font-bold;
    }
}
.payment-section-wrap {
    padding: 30px;
}
.payment-selection {
    background: $white;
    margin-bottom: 15px;
    border-radius: 10px;
    padding: 15px;
    color: $primary_text_color;
    font-size: $font-size-small;
    font-family: $font-bold;
    border: 1px solid $disabled_text_color;
    cursor: pointer;
    text-transform: uppercase;
    img {
        float: right;
    }
    &.payment-selection-border {
        border: 1px solid $primary;

    }
    &.cashfree-view-wallet {
        .item-view {
            justify-items: center;
            flex-direction: row;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .btn-expand-action {
                flex-direction: row;
                display: flex;
                justify-content: start;
                align-items: center;
                .view-items {
                    background-color: $white;
                    border: 1px solid #eaeaea;
                    border-radius: 5px;
                    font-size: 12px;
                    padding: 3px 6px;
                    &.active-border {
                        border: 1px solid $primary;
                    }
                    .active {
                        color: $primary;
                    }
                    .not-active {
                        color: $help_text_color;
                    }
                }
                .title-payment {
                    font-family: $font-bold;
                    font-size: 12px;
                    font-weight: 400;
                    line-height: 1.5;
                    color: $gray-900;
                    margin-top: 2px;
                    &.selected{
                        color: $primary;
                    }
                }
            }
        }
        .label-expiry-date {
            color: #555555;
            font-family: $font-regular;
            font-size: 14px;
            letter-spacing: 0;
            line-height: 18px;
            text-transform: capitalize;
        }
    }
    &.height-expand {
        height: auto;
        .wallet-box {
            height: 44px;
            width: 30%;
            border-radius: 10px;
            background: $white;
            border: 1px solid $disabled_text_color;
            justify-items: center;
            flex-direction: row;
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            &.selected {
                border: 1px solid $primary;
                background: $white;
            }

            .title-wallet {
                font-family: $font-regular;
                font-size: 12px;
                font-weight: bold;
                line-height: 1.5;
                color: $gray-900;
                margin-left: 10px;
            }
        }
        .nb-view {
            flex-direction: column;
            display: flex;
            justify-content: center;
            align-items: center;
            .net-banking {
                margin-top: 8px;
                box-sizing: border-box;
                height: 49px;
                width: 48.83px;
                border: 1px solid $disabled_text_color;
                border-radius: 10px;
                background-color: $white;
                flex-direction: row;
                display: flex;
                justify-content: center;
                align-items: center;
                &.selected {
                    border: 1px solid $primary;
                }
                .bank-logo {
                    align-items: center;
                    height: 21px;
                    width: 24.91px;
                }
            }
            .title-nb {
                font-family: $font-regular;
                font-size: 12px;
                font-weight: bold;
                line-height: 1.5;
                color: $gray-900;
            }
        }

        .paynow-btn {
            margin-top: 15px;
            margin-bottom: 15px;
            height: 44px;
            border-radius: 25px;
            background-color: $primary;
            margin-left: 50px;
            margin-right: 50px;
            &.active-pay-now{
                cursor: pointer;
                background-color: $primary;

            }
            &.disable-pay-now{
                pointer-events: none;
                background-color: $disabled_text_color;

            }
            .title-pay-now {
                color: $white;
                font-family: $font-semibold;
                font-size: 14px;
                font-weight: bold;
                letter-spacing: 0;
                line-height: 43px;
                text-align: center;
            }
        }
    }
}
.affiliate-wrap {
    background: $white;
    .summary-cont {
        padding: 16px;
        background: $app_bg_color;
        .bal-summary-wrap {
            margin: 0;
        }
    }
    .wallet-header {
        padding-bottom: 20px;
    }
    .currency-circle {
        font-family: $font-semibold;
        top: unset;
        bottom: 22px;
        font-size: 55px;
        padding-top: 5px;
    }
    .acc-bal {
        line-height: 1.1;
    }
    .profile-view {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        margin-left: -1px;
        img {
            width: 46px;
            height: 46px;
            object-fit: contain;
            border-radius: 100%;
            border: 1px solid $white;
            overflow: hidden;
            background: $white;
        }
        .name-view {
            margin: 2px 0 0 15px;
            .fl-name {
                color: $white;
                font-size: $font-size-medium;
                font-family: "Geominist-Bold";
                line-height: 18px;
                display: block;
            }
            .u-name {
                color: $white;
                font-size: $font-base;
                font-family: "Geominist-Regular";
                display: block;
            }
        }
    }
    .data-count-block {
        padding: 9px;
    }
    .user-list {
        .detail-header {
            background-color: $disabled_text_color;
            padding: 7px 15px 4px;
            span {
                color: $btn-disable-color;
                font-size: $font-size-small;
                font-family: $font-semibold;
            }
        }
        .header-v {
            border-bottom: 1px solid $app_bg_color;
            padding: 8px 15px 5px;
            display: flex;
            align-items: center;
            span {
                color: $primary_text_color;
                font-size: $font-size-small;
                font-family: $font-regular;
                width: 20%;
                &.user {
                    width: 60%;
                    .timing {
                        font-size: $font-size-xsmall;
                        color: $help_text_color;
                    }
                }
                .curr {
                    color: $help_text_color;
                }
            }
            &.list-item {
                span {
                    &.user {
                        color: $body_text_color;
                    }
                }
            }
        }
        .list-view {
            list-style: none;
            margin: 0;
            padding: 0;
            li:nth-child(even) {
                background: $gray-100;
            }
            li:nth-child(odd) {
                background: $white;
            }
        }
    }
}
.font-style-normal {
    font-style: normal;
}
